import {styled} from "styletron-react";
import {getBasicCSS, IBasicProps} from "./GameDiv";

interface IProps extends IBasicProps {
    size?: number;
    $fWeight?: "bolder" | "lighter";

    $noWrap?: boolean;
}

export const GameText = styled<any, IProps>("div", (props) => {
    let {size} = props;
    let customProps = {...props};
    delete customProps["size"];
    delete customProps["$fWeight"];
    delete customProps["$textAlign"];

    customProps.$fSize = size || 14;

    return {
        ...getBasicCSS(customProps),
        userSelect: "none",
        textAlign: props.$textAlign || "center",
        verticalAlign: "center",
        boxSizing: "border-box",
        whiteSpace: props.$noWrap ? "nowrap" : "pre-wrap",
        fontWeight: props.$fWeight
    };
});
