import {styled} from "styletron-react";
import React from "react";
import {getBorder, IBorderProps} from "./BasicProps";

interface RoleCardImageProps extends IBorderProps {
    size?: number;
}

export const RoleCardImage = styled<any, RoleCardImageProps>("img", (props) => {
    let size = props.size || window.innerHeight / 2.5;
    return {
        width: `${size * (3 / 3.5)}px`,
        height: `${size}px`,
        objectFit: "fill",
        userSelect: "none",
        ...getBorder(props),
        boxSizing: `border-box`
    };
});

export const RoleCardText = styled<any, RoleCardImageProps>("div", (props) => {
    let size = props.size || window.innerHeight / 2.5;
    return {
        width: `${size * (3 / 3.5)}px`,
        height: `${size}px`,
        color: "white",
        position: "absolute",
        top: "10px",
        left: "50%",
        marginLeft: `-${(size * (3 / 3.5)) / 2}px`,
        boxSizing: "border-box",
        display: "flex",
        flexDirection: "column",
        border: "solid 2px",
        borderRadius: "1rem",
        padding: `.2rem`,
        pointerEvents: "none"
    };
});
export const RoleCardTextHeader = styled<any, RoleCardImageProps>("p", (props) => {
    let size = props.size || window.innerHeight / 2.5;
    return {
        color: "white",
        textAlign: "center",
        fontSize: `${size / 15}px`,
        margin: "2%"
    };
});
export const RoleCardTextBody = styled<any, RoleCardImageProps>("p", (props) => {
    let size = props.size || window.innerHeight / 2.5;
    return {
        color: "white",
        textAlign: "center",
        fontSize: `${size / 25}px`,
        margin: "2%"
    };
});
