import React, {useContext} from "react";
import {COLOR} from "../../../shared/styled/ColorsPalette";
import {PARTY, Roles} from "../../../../werewolf-consts/enums";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";
import {GameText} from "../../../shared/game-basic-components/GameText";
import {I18Namspace} from "../../../../language/i18n";
import {GameSettingEnum, getBalanceEnum} from "../../../../language/Vietnamese/GameSetting_vi";
import {useTranslation} from "react-i18next";
import {RoomContext, SizeContext, StateContext} from "../../../../context";
import {PartyColor} from "./RoleItem";
import {ROLE_AVATAR} from "../../startgame-screen/GameCardShower";
import {RoleGuideEnum} from "../../../../language/Vietnamese/RoleGuide_vi";
import {MapSchema} from "@colyseus/schema";
import {GameIcon} from "../../../shared/game-basic-components/GameIcon";
import saveIcon from "../../../../assets/game-btn/save.svg";
import editIcon from "../../../../assets/game-btn/edit.svg";
import {calculateBalancePoint} from "../../../../werewolf-consts/utils/create_setup_tool/AutoGenerateSetupV2";
import GameCheckBox from "../../../shared/game-basic-components/GameCheckBox";
import {isSameSetup} from "../../../../werewolf-consts/utils/create_setup_tool/SetupCheck";
import GameLayout from "../../../shared/game-advance-components/GameLayout";

/**
 * activated: mảng đang được dùng ở phòng chơi hay không ?
 */
interface IProps {
    setup: MapSchema<number>;
    balancePoint?: number;
    onRoleClick?: (role: Roles) => void;
    activated: boolean;
    onCheckActive?: (setting: MapSchema<number>) => void;
    onEdit?: (setup: MapSchema<number>) => void;
    onSave?: (setup: MapSchema<number>) => void;
}

function GameSettingView(props: IProps) {
    const {t} = useTranslation([I18Namspace.GameSetting, I18Namspace.RoleGuide]);
    let currentSetup = useContext(StateContext).setup;
    const isActive = isSameSetup(props.setup, currentSetup);
    const {basic: baseSize} = useContext(SizeContext);
    let state = useContext(StateContext);
    let players = useContext(RoomContext).room.state.players;
    let showSetup = Object.keys(props.setup).length > 0;
    const balance = props.balancePoint || calculateBalancePoint(props.setup);

    function roleClick(role: Roles) {
        if (props.onRoleClick) props.onRoleClick(role);
    }

    const roleItems = () => {
        if (showSetup)
            return [...Object.keys(props.setup)]
                .map((r) => parseInt(r) as Roles)
                .map((role) => {
                    return (
                        <GameDiv
                            onClick={() => roleClick(role)}
                            key={"role" + role}
                            boder={"solid white 2px"}
                            $backgroundColor={PartyColor(role)}
                            $W={baseSize.size10 * 2}
                            round={".4rem"}
                            m={".4rem"}
                        >
                            <GameLayout.ColExpanded $alignContent={"center"} $alignItems={"center"}>
                                <GameLayout.Row $fullW $justifyContent={"space-between"} $alignItems={"center"}>
                                    <GameLayout.ColExpanded $justifyContent={"center"}>
                                        <GameText size={baseSize.size8}>{props.setup[role]}</GameText>
                                    </GameLayout.ColExpanded>
                                    <GameIcon
                                        src={ROLE_AVATAR[role]}
                                        $W={baseSize.size10}
                                        ratio={3 / 3.5}
                                        $roundTopRight={".4rem"}
                                    />
                                </GameLayout.Row>
                                <GameLayout.RowExpanded
                                    $fullW
                                    p={".2rem"}
                                    round_bottom={".4rem"}
                                    $justifyContent={"center"}
                                    $alignItems={"center"}
                                    $backgroundColor={COLOR.blurDark50}
                                >
                                    <GameText size={baseSize.size3}>
                                        {t(`${I18Namspace.RoleGuide}:${RoleGuideEnum.name}.${role}`)}
                                    </GameText>
                                </GameLayout.RowExpanded>
                            </GameLayout.ColExpanded>
                        </GameDiv>
                    );
                });
        return Array(players).map((p, i) => {
            return (
                <GameDiv
                    key={"?role" + i}
                    $backgroundColor={COLOR[PARTY.BETRAYER]}
                    $W={baseSize.size10 * 2}
                    round={".4rem"}
                    m={".4rem"}
                >
                    <GameText size={baseSize.size10}>?</GameText>
                </GameDiv>
            );
        });
    };
    let color = balance > 1 ? COLOR[PARTY.VILLAGER] : balance < -1 ? COLOR[PARTY.WEREWOLF] : COLOR[PARTY.BETRAYER];

    function updateActive(value: boolean) {
        if (props.onCheckActive) {
            if (value) props.onCheckActive(props.setup);
            else props.onCheckActive(new MapSchema<number>());
        }
    }

    return (
        <GameDiv
            $alignSelf={"center"}
            round={"1rem"}
            border={"solid white 1px"}
            m={".5rem"}
            $backgroundColor={color}
            $maxW={baseSize.gameW * 0.9}
        >
            <GameLayout.Col $alignItems={"center"}>
                <GameDiv $fullW $backgroundColor={COLOR.background} round_top={"1rem"}>
                    <GameLayout.RowExpanded
                        $flexWrap={"wrap"}
                        $justifyContent={"center"}
                        $alignContent={"space-around"}
                    >
                        {roleItems()}
                    </GameLayout.RowExpanded>
                </GameDiv>
                <GameLayout.Row $fullW $justifyContent={"space-between"} p={".4rem"} $alignItems="center">
                    <>
                        {props.onCheckActive && (
                            <GameCheckBox
                                $alignSelf={"center"}
                                m={".4rem"}
                                size={baseSize.size5}
                                value={isActive}
                                onChange={(value) => updateActive(value)}
                            >
                                {t(`${I18Namspace.GameSetting}:${GameSettingEnum.active}`)}
                            </GameCheckBox>
                        )}
                    </>
                    <>
                        {showSetup && (
                            <GameLayout.ColExpanded $justifyContent={"center"} $alignItems={"center"}>
                                <GameText size={baseSize.size3}>
                                    {t(`${I18Namspace.GameSetting}:${GameSettingEnum.numOfPlayer}`, {
                                        playerCount: state.players.size,
                                        roleCount: Object.keys(props.setup).length
                                    })}
                                </GameText>
                                <GameText size={baseSize.size3}>
                                    {t(`${I18Namspace.GameSetting}:${getBalanceEnum(balance)}`)}
                                </GameText>
                            </GameLayout.ColExpanded>
                        )}
                    </>
                    <>
                        {!showSetup && (
                            <GameLayout.ColExpanded $justifyContent={"center"} $alignItems={"center"}>
                                <GameText size={baseSize.size3}>
                                    {t(`${I18Namspace.GameSetting}:${GameSettingEnum.setupNotBeConfigured}`)}
                                </GameText>
                            </GameLayout.ColExpanded>
                        )}
                    </>
                    <>
                        {props.onSave && (
                            <GameIcon
                                $alignSelf={"center"}
                                onClick={() => {
                                    if (props.onSave) props.onSave(props.setup);
                                }}
                                src={saveIcon}
                                size={baseSize.size5}
                                $backgroundColor={COLOR.blurDark80}
                                m={".4rem"}
                                round={".4rem"}
                            />
                        )}
                    </>
                    <>
                        {props.onEdit && (
                            <GameIcon
                                $alignSelf={"center"}
                                onClick={() => {
                                    if (props.onEdit) props.onEdit(props.setup);
                                }}
                                src={editIcon}
                                size={baseSize.size5}
                                $backgroundColor={COLOR.blurDark80}
                                p={".4rem"}
                                round={".4rem"}
                            />
                        )}
                    </>
                </GameLayout.Row>
            </GameLayout.Col>
        </GameDiv>
    );
}

export default GameSettingView;
