import React, {useContext, useEffect, useState} from "react";
import {RoomContext, SizeContext, UserContext} from "../../../context";
import {CardBack, CardFront, FlipCard} from "../../shared/styled/Card";
import RoleCardBack from "../../../assets/images/card_back.jpg";
import Grid from "../../shared/styled/game-layout/Grid";
import {GameDiv} from "../../shared/game-basic-components/GameDiv";
import RoleCard from "./RoleCard";
import {GameImage} from "../../shared/game-basic-components/GameImage";

function StartGameScreen() {
    let roomContext = useContext(RoomContext);
    const userContext = useContext(UserContext);
    const [openCard, setOpenCard] = useState<boolean>(false);
    const {basic: S} = useContext(SizeContext);
    const myRole = roomContext.state.players[userContext.playerId]?.role.roleID;
    useEffect(() => {
        let timeout;
        timeout = setInterval(() => {
            setOpenCard(true);
        }, 1000);

        return function clean() {
            clearInterval(timeout);
        };
    }, []);

    return (
        <Grid.Row>
            <Grid.FullWidthCol />
            <Grid.Col style={{justifyContent: "center"}} $alignItems={"center"}>
                <Grid.FullHeightRow />
                <GameDiv position={"relative"} w={S.gameH / 3} ratio={3 / 4} $alignSelf={"center"}>
                    <FlipCard style={openCard ? {transform: "rotateY(180deg)"} : {}}>
                        <CardFront>
                            <GameImage round={".8rem"} src={RoleCardBack} w={S.gameH / 3} ratio={3 / 4} />
                        </CardFront>
                        <CardBack>
                            <RoleCard role={myRole} size={S.gameH / 3} />
                        </CardBack>
                    </FlipCard>
                </GameDiv>
                <Grid.FullHeightRow />
            </Grid.Col>
            <Grid.FullWidthCol />
        </Grid.Row>
    );
}

export default StartGameScreen;
