import React, {useContext, useEffect, useState} from "react";
import {StageNames} from "../../../../../werewolf-consts/enums/StageNames";
import {StateContext} from "../../../../../context/StateContext";
import {useTranslation} from "react-i18next";
import {RoomContext} from "../../../../../context/RoomContext";
import {StageGuideEnum} from "../../../../../language/Vietnamese/StageGuide_vi";
import {countRoleEvent, getPlayersName} from "../../../../../werewolf-consts/colyseus/stateUtils";
import {EventNames} from "../../../../../werewolf-consts/enums";
import {UserContext} from "../../../../../context/UserContext";

interface IStageGuide {
    header: string;
    targets: string[];
    body: string;
}

function useStageGuide() {
    const [guide, setGuide] = useState<IStageGuide>();
    const {t} = useTranslation("StageGuide");
    let state = useContext(StateContext);
    const userContext = useContext(UserContext);
    let roomContext = useContext(RoomContext);
    let roomState = roomContext.state;
    useEffect(() => {
        let stageGuide: IStageGuide = {
            body: "",
            header: "",
            targets: []
        };
        const stageName = state.currentStage.stageName;
        switch (stageName) {
            case StageNames.WOLF_PLUS:
                stageGuide.targets = state.currentStage.deadMans;
                if (countRoleEvent(roomState, userContext.playerId, EventNames.CURSE) > 0) {
                    stageGuide.body = t(`${StageNames.WOLF_PLUS}.${StageGuideEnum.cannotCurse}`);
                } else {
                    if (stageGuide.targets.length === 1)
                        stageGuide.body = t(`${StageNames.WOLF_PLUS}.${StageGuideEnum.canCurse}`, {
                            name: getPlayersName(roomState, stageGuide.targets[0])[0]
                        });
                    else {
                        stageGuide.body = t(`${StageNames.WOLF_PLUS}.${StageGuideEnum.noDie}`);
                    }
                }
                break;
            case StageNames.LAST_WORD:
                stageGuide.targets = state.currentStage.deadMans;
                stageGuide.body = t(StageNames.LAST_WORD, {name: getPlayersName(roomState, stageGuide.targets[0])[0]});
                break;
            case StageNames.VOTE_YES_NO:
                stageGuide.targets = state.currentStage.deadMans;
                stageGuide.body = t(StageNames.VOTE_YES_NO, {
                    name: getPlayersName(roomState, stageGuide.targets[0])[0]
                });
                break;
            case StageNames.WITCH_SAVE:
                stageGuide.targets = state.currentStage.deadMans;
                if (countRoleEvent(roomState, userContext.playerId, EventNames.SAVE) > 0) {
                    stageGuide.body = t(`${StageNames.WITCH_SAVE}.${StageGuideEnum.cannotSave}`);
                } else {
                    if (stageGuide.targets.length === 1)
                        stageGuide.body = t(`${StageNames.WITCH_SAVE}.${StageGuideEnum.canSave}`, {
                            name: getPlayersName(roomState, stageGuide.targets[0])[0]
                        });
                    else {
                        stageGuide.body = t(`${StageNames.WITCH_SAVE}.${StageGuideEnum.noDie}`);
                    }
                }
                break;
            case StageNames.WITCH_KILL:
                stageGuide.targets = state.currentStage.deadMans;
                if (countRoleEvent(roomState, userContext.playerId, EventNames.KILL) > 0) {
                    stageGuide.body = t(`${StageNames.WITCH_KILL}.${StageGuideEnum.cannotKill}`);
                } else {
                    stageGuide.body = t(`${StageNames.WITCH_KILL}.${StageGuideEnum.canKill}`);
                }
                break;
            default:
                stageGuide.body = t(`${stageName}`);
        }
        setGuide(stageGuide);
    }, [roomState, state.currentStage.deadMans, state.currentStage.stageName, t, userContext.playerId]);

    return guide;
}

export default useStageGuide;
