import React, {useContext} from "react";
import PropTypes from "prop-types";
import Grid from "../../../../shared/styled/game-layout/Grid";
import {GameDiv} from "../../../../shared/game-basic-components/GameDiv";
import XButton from "../../../../shared/game-basic-components/XButton";
import GameIconButton from "../../../../shared/game-basic-components/GameIconButton";
import logout from "../../../../../assets/game-btn/logout.svg";
import {SizeContext} from "../../../../../context/SizeContext";
import {POSITION} from "../../../../shared/styled/game-layout/StackLayout";
import {COLOR} from "../../../../shared/styled/ColorsPalette";
import {GameRouterContext} from "../../../../shared/game-advance-components/GameRouter";
import {useTranslation} from "react-i18next";
import {I18Namspace} from "../../../../../language/i18n";
import {GameInfoEnum} from "../../../../../language/Vietnamese/GameInfo_vi";
import {RoomContext} from "../../../../../context/RoomContext";

ChatHeader.propTypes = {
    //
    onClose: PropTypes.func
};

function ChatHeader(props) {
    const {basic} = useContext(SizeContext);
    const room = useContext(RoomContext).room;
    const router = useContext(GameRouterContext);
    const {t} = useTranslation(I18Namspace.GameInfo);
    return (
        <GameDiv m={".5rem"}>
            <Grid.Row $alignItems="center">
                <GameIconButton
                    onClick={() => {
                        if (router?.routeTo) router.routeTo("home", []);
                        if (room?.leave) room.leave();
                    }}
                    $textPosition={POSITION.RIGHT}
                    src={logout}
                    size={basic.size5}
                    color={COLOR.bloodColor}
                >
                    {t(`${GameInfoEnum.exit}`)}
                </GameIconButton>
                <Grid.FullWidthCol />
                <XButton
                    size={basic.size3}
                    onClick={() => {
                        if (props.onClose) props.onClose();
                    }}
                />
            </Grid.Row>
        </GameDiv>
    );
}

export default ChatHeader;
