import React from "react";

export enum NoticeType {
    error,
    warning,
    success,
    info
}

export interface INotice {
    content: string;
    time: number;
    uuid?: string;
    type?: NoticeType;
}

export interface IGameAlert {
    showNotice(alert: INotice): void;
}

export const RoomNotifierContext = React.createContext<IGameAlert>({
    showNotice: function (alert: INotice) {
        console.log(alert);
    }
});
