import React, {useContext} from "react";
import Grid from "../../../shared/styled/game-layout/Grid";
import {AVAILABLE_ROLES, ROLE_PARTY, Roles} from "../../../../werewolf-consts/enums";
import RoleItem from "./RoleItem";
import {MapSchema} from "@colyseus/schema";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";
import {ROLE_MAX} from "../../../../werewolf-consts/definitions/RoleConsts";
import GameLayout from "../../../shared/game-advance-components/GameLayout";
import {SizeContext} from "../../../../context/SizeContext";

interface IProps {
    setup: MapSchema<number>;
    onRoleClick: (role: Roles) => void;
}

function RolePickerTable(props: IProps) {
    const {window: S, basic: basicSize} = useContext(SizeContext);
    return (
        <GameDiv $alignSelf={"center"} mb={".5rem"}>
            {!S.isVertical && (
                <GameLayout.RowExpanded pr={"1rem"}>
                    {Object.keys(ROLE_PARTY).map((party, index) => {
                        return (
                            <GameDiv key={"rirpktb" + index} $scrollY $H={basicSize.gameH * 0.4}>
                                <GameLayout.RowExpanded
                                    $justifyContent="center"
                                    $alignItems="center"
                                    $alignContent="flex-start"
                                    $flexWrap="wrap"
                                >
                                    {ROLE_PARTY[party].map((role, index) => (
                                        <GameDiv $alignItems="center" key={"roleItem" + index}>
                                            <RoleItem
                                                role={role}
                                                key={"roleItem" + index}
                                                onClick={() => props.onRoleClick(role)}
                                            />
                                        </GameDiv>
                                    ))}
                                </GameLayout.RowExpanded>
                            </GameDiv>
                        );
                    })}
                </GameLayout.RowExpanded>
            )}
            {S.isVertical && (
                <GameDiv $scrollY>
                    <Grid.Row
                        $H={basicSize.gameH * 0.3}
                        $justifyContent={"space-around"}
                        $alignItems="flex-start"
                        $alignContent="flex-start"
                        $flexWrap="wrap"
                    >
                        {AVAILABLE_ROLES.filter((r) => ROLE_MAX[r] > 0).map((role, index) => (
                            <RoleItem role={role} key={"roleItem" + index} onClick={() => props.onRoleClick(role)} />
                        ))}
                    </Grid.Row>
                </GameDiv>
            )}
        </GameDiv>
    );
}

export default RolePickerTable;
