import React from "react";
import {POSITION} from "../../shared/styled/game-layout/StackLayout";
import {SkillImage} from "../game-body/action-form/SkillButton";
import dialogBackground from "../../../assets/game-btn/dialogBackground.png";
import useDialogElementSize from "./hooks/useDialogElementSize";
import {GameIcon} from "../../shared/game-basic-components/GameIcon";
import GameEventDialogTargets from "./GameEventDialogTargets";
import {IGameDialogData} from "./GameDialogManager";
import {GameImage} from "../../shared/game-basic-components/GameImage";
import GameLayout from "../../shared/game-advance-components/GameLayout";
import {GameDiv} from "../../shared/game-basic-components/GameDiv";
import {GameText} from "../../shared/game-basic-components/GameText";
import {COLOR} from "../../shared/styled/ColorsPalette";

interface GameEventDialogProps {
    dialogData: IGameDialogData;
    index?: number;
    onClose: () => void;
}

function GameEventDialog(props: GameEventDialogProps) {
    let gameEvent = props.dialogData.event;
    let Size = useDialogElementSize({targetCount: gameEvent.targets.length | 0});
    return (
        <GameDiv
            w={Size.dialogW}
            ratio={389 / 499}
            pt={`${(props.index || 0) * 1.2}em`}
            pl={`${(props.index || 0) * 1.2}em`}
        >
            <GameLayout.Stack w={Size.dialogW} ratio={389 / 499}>
                <GameImage $alignSelf={"flex-start"} w={Size.dialogW} ratio={389 / 499} src={dialogBackground} />
                <GameLayout.Positioned w={Size.dialogW} ratio={389 / 499} pos={POSITION.TOP_LEFT}>
                    <GameIcon m={"5.3%"} src={SkillImage[gameEvent.eventName]} size={Size.skillSize} />
                </GameLayout.Positioned>
                <GameLayout.Col
                    $alignSelf={"center"}
                    $justifyContent={"center"}
                    $W={Size.dialogBodyW}
                    $H={Size.dialogBodyH}
                >
                    <GameEventDialogTargets targets={gameEvent.targets} targetSize={Size.targetSize} />
                    <GameLayout.ColExpanded>
                        <GameText size={Size.fontSize} color={COLOR.dialogTextColor}>
                            {gameEvent.content}
                        </GameText>
                    </GameLayout.ColExpanded>
                    <GameDiv
                        w={Size.dialogW / 2}
                        cursor={"pointer"}
                        onClick={() => props.onClose()}
                        $alignSelf={"center"}
                        border={`solid ${COLOR.dialogBorder} .2rem`}
                    >
                        <GameText
                            $size={Size.fontSize}
                            color={COLOR.dialogTextColor}
                        >{`Okay (${props.dialogData.time})!`}</GameText>
                    </GameDiv>
                </GameLayout.Col>
            </GameLayout.Stack>
        </GameDiv>
    );
}

export default GameEventDialog;
