import React, {Reducer, useContext, useEffect, useMemo, useReducer} from "react";
import PropTypes from "prop-types";
import {COLOR, ColorEnum} from "../../shared/styled/ColorsPalette";
import {v4 as uuidv4} from "uuid";
import XButton from "../../shared/game-basic-components/XButton";
import {GameText} from "../../shared/game-basic-components/GameText";
import GameLayout from "../../shared/game-advance-components/GameLayout";
import {SizeContext} from "../../../context";
import {INotice, NoticeType, RoomNotifierContext} from "./context/RoomNotifierContext";

GameNotifier.propTypes = {
    //
    children: PropTypes.element
};

export type RoomNotifierAction =
    | {
          type: "showNotice";
          alert: INotice;
      }
    | {
          type: "close";
          alertID?: string;
      }
    | {
          type: "secondEscaped";
      };

function GameNotifier(props) {
    const [notices, dispatchNotices] = useReducer<Reducer<INotice[], RoomNotifierAction>>((prevState, action) => {
        switch (action.type) {
            case "showNotice":
                if (action.alert.content == null) return prevState;
                let newList = [...prevState].slice(0, 4);
                return [{...action.alert, uuid: uuidv4(), type: action.alert.type || NoticeType.error}, ...newList];
            case "close":
                return prevState.filter((a) => {
                    return a.uuid !== action.alertID;
                });
            case "secondEscaped":
                const notices = [...prevState];
                for (const notice of notices) {
                    notice.time -= 1;
                }
                return notices.filter((a) => {
                    return a.time > 0;
                });
            default:
                return prevState;
        }
    }, []);
    const {window, basic} = useContext(SizeContext);

    const roomNotifierContext = useMemo(
        () => ({
            showNotice: (alert: INotice) => {
                dispatchNotices({type: "showNotice", alert});
            }
        }),
        []
    );

    useEffect(() => {
        if (notices.length > 0) {
            let i = setTimeout(() => {
                dispatchNotices({type: "secondEscaped"});
            }, 1000);
            return function clean() {
                clearTimeout(i);
            };
        }
    }, [notices]);

    return (
        <RoomNotifierContext.Provider value={roomNotifierContext}>
            <GameLayout.Col
                $zIndex={11}
                position={"fixed"}
                ml={basic.mobileView ? (window.width - basic.gameW * 0.8) / 2 : 0}
                $alignSelf={"flex-start"}
                width={basic.mobileView ? basic.gameW * 0.8 : basic.chatW}
                $alignItems={basic.mobileView ? "center" : "flex-start"}
                $justifyContent="flex-start"
            >
                {notices.map((a, i) => {
                    return (
                        <GameLayout.RowExpanded
                            key={`alert_${i}`}
                            $justifyContent="center"
                            $backgroundColor={COLOR[`${ColorEnum.noticeBackground}${a.type}`]}
                            round={".5rem"}
                            mt={".3rem"}
                            p={".5rem"}
                        >
                            <GameLayout.ColExpanded $justifyContent="center" $alignContent="flex-start">
                                <GameText
                                    color={COLOR[`${ColorEnum.noticeText}${a.type}`]}
                                    p={".1rem"}
                                    $fWeight={"bolder"}
                                >
                                    {a.content}
                                </GameText>
                            </GameLayout.ColExpanded>
                            <XButton
                                color={COLOR[`${ColorEnum.noticeText}${a.type}`]}
                                onClick={() => {
                                    dispatchNotices({type: "close", alertID: a.uuid});
                                }}
                            />
                        </GameLayout.RowExpanded>
                    );
                })}
            </GameLayout.Col>

            {props.children}
        </RoomNotifierContext.Provider>
    );
}

export default GameNotifier;
