import React, {Fragment} from "react";
import PropTypes from "prop-types";
import Grid from "../../shared/styled/game-layout/Grid";
import TargetItem from "../game-body/target-picker/TargetItem";
import {GameDiv} from "../../shared/game-basic-components/GameDiv";

GameEventDialogTargets.propTypes = {
    targets: PropTypes.array,
    targetSize: PropTypes.number
};

function GameEventDialogTargets(props) {
    return (
        <Fragment>
            {props.targets && (
                <Grid.Row
                    $style={{
                        justifyContent: "center",
                        alignSelf: "center",
                        alignContent: "flex-start",
                        flexWrap: "wrap",
                        width: `${props.targetSize * 4}px`
                    }}
                >
                    {props.targets.map((targetID) => (
                        <GameDiv
                            key={`DialogTarget_${targetID}`}
                            style={{
                                borderRadius: "50%",
                                width: `${props.targetSize}px`,
                                height: `${props.targetSize}px`,
                                margin: "2% 2% 2% 2%"
                            }}
                        >
                            <TargetItem targetId={targetID} size={props.targetSize} />
                        </GameDiv>
                    ))}
                </Grid.Row>
            )}
        </Fragment>
    );
}

export default GameEventDialogTargets;
