import React, {useContext, useEffect, useState} from "react";
import PropTypes from "prop-types";
import "../app/DefaultCSS.css";
import {GameDiv} from "../shared/game-basic-components/GameDiv";
import {GameRouterContext} from "../shared/game-advance-components/GameRouter";
import {SizeContext} from "../../context/SizeContext";
import {COLOR} from "../shared/styled/ColorsPalette";
import {PARTY} from "../../werewolf-consts/enums";
import {GameText} from "../shared/game-basic-components/GameText";
import GameLayout from "../shared/game-advance-components/GameLayout";
import {useTranslation} from "react-i18next";
import {I18Namspace} from "../../language/i18n";
import {GameInfoEnum} from "../../language/Vietnamese/GameInfo_vi";
import {db} from "../../context/FirestoreContext";
import {User} from "../../database/firestore/User";
import AvatarView from "../shared/game-basic-components/AvatarView";
import {Metadata} from "../../werewolf-consts/colyseus/GameRoomTypes";
import {DEFAULT_AVT} from "../../context/UserContext";

export interface RoomData {
    roomId: string;
    metadata?: Metadata;
}

function RoomItem(props: RoomData) {
    const router = useContext(GameRouterContext);
    const {basic, window} = useContext(SizeContext);
    const W = basic.mobileView ? window.width * 0.9 : window.width / 5;
    const {t} = useTranslation(I18Namspace.GameInfo);
    const [mayor, setMayor] = useState<User>();
    useEffect(() => {
        if (props.metadata?.villageId) {
            db.users
                .document(props.metadata?.villageId)
                .get()
                .then((user) => {
                    if (user != null) setMayor(user);
                });
        }
    }, [props.metadata]);

    function gotoRoom(id: any) {
        if (router?.routeTo) router.routeTo("gameRoom", [id, undefined, mayor?.villageName]);
    }

    return (
        <GameDiv
            className="GameBtn"
            $backgroundColor={COLOR[PARTY.BETRAYER]}
            m={"1rem"}
            p={"1rem"}
            round={"1rem"}
            onClick={() => gotoRoom(props.metadata?.villageId)}
            w={basic.mobileView ? basic.gameW * 0.8 : basic.gameW / 2.3}
        >
            <GameLayout.ColExpanded>
                <GameText size={W / 10} $fontStyle={"Altus"}>
                    {mayor?.villageName?.slice(0, 20)}
                </GameText>
                <GameLayout.RowExpanded $alignItems={"center"} $justifyContent={"space-around"}>
                    <GameLayout.Col $alignItems={"center"} w={W / 3}>
                        <AvatarView src={mayor?.avatarUrl || DEFAULT_AVT} size={W / 3} />
                        <GameText $fontStyle={"Altus"} size={W / 20}>
                            {mayor?.name || ""}
                        </GameText>
                    </GameLayout.Col>
                    <GameLayout.Col $alignItems={"flex-start"} m={"1rem"}>
                        <GameText $fontStyle={"Altus"} size={W / 18}>
                            {t(`${GameInfoEnum.watchers}`, {count: props.metadata?.watchers || 0})}
                        </GameText>
                        <GameText $fontStyle={"Altus"} size={W / 18}>
                            {t(`${GameInfoEnum.playersCount}`, {count: props.metadata?.playersCount || 0})}
                        </GameText>
                        <GameText $fontStyle={"Altus"} size={W / 18}>
                            {props.metadata?.isPlaying == null
                                ? t(`${GameInfoEnum.offline}`)
                                : props.metadata.isPlaying
                                ? t(`${GameInfoEnum.playing}`)
                                : t(`${GameInfoEnum.waiting}`)}
                        </GameText>
                    </GameLayout.Col>
                </GameLayout.RowExpanded>
                <GameLayout.Row $alignItems={"center"} $justifyContent={"space-between"}>
                    <GameText $alignSelf={"flex-end"} $fontStyle={"Altus"} size={W / 18}>
                        {props.metadata?.lock
                            ? t(`${GameInfoEnum.passwordRequired}`)
                            : t(`${GameInfoEnum.noPasswordRequired}`)}
                    </GameText>
                    <GameText $alignSelf={"flex-end"} $fontStyle={"Altus"} size={W / 18}>
                        ID: {props.roomId}
                    </GameText>
                </GameLayout.Row>
            </GameLayout.ColExpanded>
        </GameDiv>
    );
}

RoomItem.propTypes = {
    id: PropTypes.string
};

export default RoomItem;
