import React, {useContext} from "react";
import PropTypes from "prop-types";
import GameLayout from "../../../shared/game-advance-components/GameLayout";
import TargetItem from "./TargetItem";
import {GameText} from "../../../shared/game-basic-components/GameText";
import {getPlayersName} from "../../../../werewolf-consts/colyseus/stateUtils";
import {RoomContext} from "../../../../context/RoomContext";
import {COLOR} from "../../../shared/styled/ColorsPalette";
import {SizeContext} from "../../../../context/SizeContext";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";

function AttackersView(props) {
    const state = useContext(RoomContext).room.state;
    const {basic} = useContext(SizeContext);
    return (
        <GameDiv
            $scrollY
            $maxH={basic.playerAvatarSize * 3}
            $backgroundColor={COLOR.chatBackground}
            p={".5rem"}
            round={".5rem"}
        >
            <GameLayout.Col>
                {props.attackers.map((id, index) => {
                    return (
                        <GameLayout.Row $alignItems={"center"} key={"atk" + index}>
                            <GameText m={".5rem"} size={basic.playerAvatarSize / 3.5} $noWrap>
                                {index + 1}:
                            </GameText>
                            <TargetItem targetId={id} size={Math.max(basic.playerAvatarSize / 2, 20)} />
                            <GameText m={".5rem"} size={basic.playerAvatarSize / 3.5} $noWrap>
                                {getPlayersName(state, id)}
                            </GameText>
                        </GameLayout.Row>
                    );
                })}
            </GameLayout.Col>
        </GameDiv>
    );
}

AttackersView.propTypes = {
    attackers: PropTypes.array.isRequired
};

export default AttackersView;
