import React, {useContext, useReducer, useState} from "react";
import {RoomContext} from "../../../../context/RoomContext";
import Grid from "../../../shared/styled/game-layout/Grid";
import {GameText} from "../../../shared/game-basic-components/GameText";
import {useTranslation} from "react-i18next";
import {I18Namspace} from "../../../../language/i18n";
import {GameSettingEnum} from "../../../../language/Vietnamese/GameSetting_vi";
import SettingPicker from "./SettingPicker";
import GameIconButton from "../../../shared/game-basic-components/GameIconButton";
import createIcon from "../../../../assets/game-btn/create.svg";
import {GameSetupEditor} from "./GameSetupEditor";
import {MapSchema} from "@colyseus/schema";
import {gameSettingReducer} from "./functions/gameSettingReducer";
import {AdminActionTypes, IAdminAction, MessageTypes} from "../../../../werewolf-consts/colyseus/MessageTypes";
import {mapSchemaNumber2Array} from "../../../../werewolf-consts/colyseus/Utils";
import {StateContext} from "../../../../context/StateContext";
import GameLayout from "../../../shared/game-advance-components/GameLayout";
import {SizeContext} from "../../../../context/SizeContext";

function GameSettingTab() {
    let currentSetup = useContext(StateContext).setup;
    const [showSettingPicker, setShowSettingPicker] = useState<boolean>(false);
    const {t} = useTranslation(I18Namspace.GameSetting);
    const {basic} = useContext(SizeContext);
    const [tempSetup, setTempSetup] = useReducer(gameSettingReducer, new MapSchema());
    const room = useContext(RoomContext).room;

    /**
     * Tạo ra setup mới
     */
    function createNewSetup() {
        setTempSetup({type: "clear"});
        setShowSettingPicker(true);
    }

    /**
     * Lưu setup tuỳ chính của người dùng vào database
     * @param setup
     */
    function saveNewSetup(setup: MapSchema<number>) {}

    /**
     * Thay đổi setup hiện tại của phòng
     * @param setup
     */
    function setActiveSetup(setup: MapSchema<number>) {
        room.send(MessageTypes.ADMIN_ACTION, {
            type: AdminActionTypes.UPDATE_GAME_SETUP,
            data: mapSchemaNumber2Array(setup)
        } as IAdminAction);
    }

    /**
     * Tuỳ chỉnh setup có sẵn
     * @param setup
     */
    function editExistSetup(setup: MapSchema<number>) {
        setTempSetup({type: "clone", setup: setup});
        setShowSettingPicker(true);
    }

    return (
        <GameLayout.ColExpanded $alignItems={"center"}>
            <>
                {!showSettingPicker && (
                    <>
                        <GameText size={basic.size5} $fontStyle={"Altus"}>
                            {t(`${GameSettingEnum.availableSetup}`)}
                        </GameText>
                        <SettingPicker
                            activeSetup={currentSetup}
                            onChangeSetup={(setup) => setActiveSetup(setup)}
                            onEditSetup={(setup) => editExistSetup(setup)}
                        />
                        <Grid.FullHeightRow />
                        <GameIconButton src={createIcon} size={basic.size8} onClick={createNewSetup}>
                            {t(`${I18Namspace.GameSetting}:${GameSettingEnum.createNew}`)}
                        </GameIconButton>
                    </>
                )}
            </>
            <>
                {showSettingPicker && (
                    <GameSetupEditor
                        setup={tempSetup}
                        updateSetup={setTempSetup}
                        onSave={(setup) => saveNewSetup(setup)}
                        onActive={(setup) => setActiveSetup(setup)}
                        onClose={() => setShowSettingPicker(false)}
                    />
                )}
            </>
        </GameLayout.ColExpanded>
    );
}

export default GameSettingTab;
