import {styled} from "styletron-react";
import "./keyframes.css";
import {POSITION} from "./game-layout/StackLayout";

export const ZoomInZoomOut = styled("div", {
    animationDuration: "5s",
    animationName: "animatetop"
});

interface TimeProps {
    time: number;
}

export const Zoom = styled<any, TimeProps>("div", (props) => ({
    transition: `width ${props.time}s, height ${props.time}s`
}));

export const Rotate = styled("div", {
    animationDuration: "160s",
    animationName: "rolling",
    animationIterationCount: "infinite"
});

export const BlurUnBlur = styled("div", {
    animationDuration: "60s",
    animationName: "blur",
    animationIterationCount: "infinite"
});

export const UpToDown = styled("div", {
    animation: "upDown 1s infinite alternate"
});
export const LeftToRight = styled("div", {
    animation: "leftRight 1s infinite alternate"
});
export const TopLeftToBottomRight = styled("div", {
    animation: "TopLeftToBottomRight 1s infinite alternate"
});
export const TopRightToBottomLeft = styled("div", {
    animation: "TopRightToBottomLeft 1s infinite alternate"
});

interface BounceEffectProps {
    position: POSITION;
}

export const BounceEffect = styled<any, BounceEffectProps>("div", (props) => {
    let {position} = props;
    let animation: string = "";
    if ([POSITION.LEFT, POSITION.RIGHT].includes(position)) animation = "leftRight 1s infinite alternate";
    if ([POSITION.TOP, POSITION.BOTTOM].includes(position)) animation = "upDown 1s infinite alternate";
    if ([POSITION.BOTTOM_RIGHT, POSITION.TOP_LEFT].includes(position))
        animation = "TopLeftToBottomRight 1s infinite alternate";
    if ([POSITION.BOTTOM_LEFT, POSITION.TOP_RIGHT].includes(position))
        animation = "TopRightToBottomLeft 1s infinite alternate";
    return {
        animation: animation
    };
});

export const GoFromBottom = styled<any, TimeProps>("div", (props) => {
    return {
        $zIndex: 10,
        animationDuration: `${props.time}s`
    };
});
