import React from "react";
import {Room} from "colyseus.js";
import {State} from "../werewolf-consts/colyseus/State";

export interface IRoomContext {
    room: Room<State>;
    state: State;
}

export const RoomContext = React.createContext<IRoomContext>({
    room: {} as Room<State>,
    state: new State()
});
