import React, {useContext} from "react";
import GameHistory from "./gameHistory";
import {COLOR} from "../../shared/styled/ColorsPalette";
import GameLayout from "../../shared/game-advance-components/GameLayout";
import {ISizeProps} from "../../shared/styled/BasicProps";
import {PICKER_VIEW, TargetContext} from "../../../context/TargetContext";
import {SizeContext} from "../../../context/SizeContext";

function EndGame(props: ISizeProps) {
    const {basic: baseSize} = useContext(SizeContext);

    const currentView = useContext(TargetContext).currentView;
    const showHistory = currentView === PICKER_VIEW.HISTORY || !baseSize.mobileView;

    return (
        <GameLayout.ColExpanded
            show={showHistory}
            $justifyContent={"center"}
            $backgroundColor={COLOR.backgroundHistory}
            round={"1rem"}
        >
            <GameHistory {...props} />
        </GameLayout.ColExpanded>
    );
}

export default EndGame;
