import React from "react";
import {GameDiv, IBasicProps} from "../game-basic-components/GameDiv";
import "../styled/keyframes.css";

interface IProps extends IBasicProps {
    show: boolean;
}

function GameLoadingSpinner(props: IProps) {
    let customProps = {...props};
    customProps.round = "50%";
    customProps.border = `${(props.size || 16) / 6}px solid #f3f3f3`;
    customProps.$alignSelf = "center";
    customProps.display = props.show ? "inline-block" : "none";
    return (
        <GameDiv
            {...customProps}
            $style={{
                borderTop: `${(props.size || 16) / 4}px solid ${props.color || "#3498db"}`,
                "-webkit-animation": "spin 2s linear infinite" /* Safari */,
                animation: "spin 2s linear infinite"
            }}
        />
    );
}

export default GameLoadingSpinner;
