import {useCallback, useEffect, useState} from "react";
import PropTypes from "prop-types";
import "../../app/DefaultCSS.css";

useClock.propTypes = {
    closeTime: PropTypes.number
};

function useClock(closeTime: number) {
    const [time, setTime] = useState("");
    const setCloseTime = useCallback(
        function () {
            let currentTime = Math.floor(Date.now() / 1000);
            let leftTime = closeTime - currentTime - 1;
            if (leftTime >= 0) {
                let minute = ("0" + Math.floor(leftTime / 60)).slice(-2);
                let second = ("0" + (leftTime % 60)).slice(-2);
                setTime(`${minute}:${second}`);
            } else {
                setTime("");
            }
        },
        [closeTime]
    );

    useEffect(() => {
        setCloseTime();
        let interval = setInterval(() => {
            setCloseTime();
        }, 1000);
        return function cleanup() {
            clearInterval(interval);
        };
    }, [closeTime, setCloseTime]);
    return time.length > 0 ? `(${time})` : "";
}

export default useClock;
