import React, {useContext, useEffect, useRef, useState} from "react";
import PropTypes, {InferProps} from "prop-types";
import {RoomContext} from "../../../../context/RoomContext";
import TargetObject from "./TargetObject";
import {POSITION} from "../../../shared/styled/game-layout/StackLayout";
import {IAction, MessageTypes} from "../../../../werewolf-consts/colyseus/MessageTypes";
import {SkillNames, StageNames} from "../../../../werewolf-consts/enums";
import useTargetLayout from "./customHooks/useTargetLayout";
import useAttackerMap from "./customHooks/useAttackerMap";
import useTargetPickerOptions from "./customHooks/useTargetPickerOptions";
import {PICKER_VIEW, SizeContext, StateContext, TargetContext, UserContext} from "../../../../context";
import ChangeViewBtn from "./ChangeViewBtn";
import {AbsolutePosition} from "../../../shared/styled/game-layout/AbsolutePosition";
import GameLayout from "../../../shared/game-advance-components/GameLayout";

export enum SELECT_TYPE {
    SINGLE,
    GROUP,
    NONE
}

TargetPicker.propTypes = {
    children: PropTypes.element,
    onTargetsCommit: PropTypes.func
};

function TargetPicker(props: InferProps<typeof TargetPicker.propTypes>) {
    const userContext = useContext(UserContext);
    let roomContext = useContext(RoomContext);
    let stateContext = useContext(StateContext);
    const {basic: baseSize} = useContext(SizeContext);
    const [selected, setSelected] = useState<string[]>([]);
    const [targets, setTargets] = useState<string[]>([]);
    const tempSelected = useRef<string[]>([]);
    const [view, setView] = useState<PICKER_VIEW>(PICKER_VIEW.CIRCLE);
    let options = useTargetPickerOptions();
    let layout = useTargetLayout();
    let attackerMap = useAttackerMap();
    useEffect(() => {
        setSelected([]);
        setTargets([]);
        if (stateContext.currentStage.stageName === StageNames.END_GAME && baseSize.mobileView)
            setView(PICKER_VIEW.HISTORY);
        else if (stateContext.currentStage.stageName === StageNames.END_GAME && !baseSize.mobileView)
            setView(PICKER_VIEW.GRID);
        else setView(PICKER_VIEW.CIRCLE);
    }, [baseSize.mobileView, stateContext.currentStage.stageName]);

    function genTargetObjects(targets: string[], position: POSITION) {
        return targets.map((id, index) => {
            let pos: POSITION = position;
            if (position === POSITION.TOP) {
                pos = POSITION.TOP;
                if (index === 0 && view === PICKER_VIEW.CIRCLE) pos = POSITION.TOP_LEFT;
                if (index === layout.topTargets.length - 1 && view === PICKER_VIEW.CIRCLE) pos = POSITION.TOP_RIGHT;
            }
            // if (position === POSITION.LEFT)
            //     pos = index === layout.leftTargets.length - 1 ? POSITION.BOTTOM_LEFT : POSITION.LEFT;
            // if (position === POSITION.RIGHT)
            //     pos = index === layout.rightTargets.length - 1 ? POSITION.BOTTOM_RIGHT : POSITION.RIGHT;
            return (
                <TargetObject
                    key={"tg_Object" + index}
                    showAttacker={options.targetOptions.showAttacker}
                    attackers={attackerMap.get(id)}
                    position={pos}
                    showPointer={selected.includes(id)}
                    onChoose={chose}
                    targetId={id}
                />
            );
        });
    }

    function chose(targetID: string) {
        if (options.selectType === SELECT_TYPE.NONE) return;
        if (!stateContext.currentStage.activePlayers.includes(userContext.playerId)) return;
        if (tempSelected.current.length < options.maxChoices) {
            //chọn độc lập
            if (options.selectType === SELECT_TYPE.SINGLE) {
                let index = tempSelected.current.indexOf(targetID);
                if (index >= 0) tempSelected.current.splice(index, 1);
                else tempSelected.current.push(targetID);
                setSelected([...tempSelected.current]);
            }
            //Chọn nhóm cạnh nhau
            if (options.selectType === SELECT_TYPE.GROUP) {
            }
        } else if (tempSelected.current.length === options.maxChoices) {
            let index = tempSelected.current.indexOf(targetID);
            if (index >= 0) tempSelected.current.splice(index, 1);
            else tempSelected.current[tempSelected.current.length - 1] = targetID;
            setSelected([...tempSelected.current]);
        } else {
            tempSelected.current.splice(0, tempSelected.current.length);
            setSelected([...tempSelected.current]);
        }

        if (tempSelected.current.length === 0 || tempSelected.current.length === options.maxChoices) {
            roomContext.room.send(MessageTypes.ACTION, {
                from: userContext.playerId,
                skill: SkillNames.POINT,
                targets: tempSelected.current
            } as IAction);
            // props.onTargetsCommit(tempSelected.current);
            setTargets(tempSelected.current);
        }
    }

    let circleView = view === PICKER_VIEW.CIRCLE;
    let gridView = view === PICKER_VIEW.GRID;
    return (
        <TargetContext.Provider value={{targets: targets, currentView: view}}>
            <GameLayout.ColExpanded>
                <GameLayout.ColExpanded>
                    <GameLayout.Row show={circleView} $justifyContent={"space-around"}>
                        {genTargetObjects(layout.topTargets, POSITION.TOP)}
                    </GameLayout.Row>
                    <GameLayout.Row show={gridView} $justifyContent={"space-around"} $flexWrap={"wrap"}>
                        {genTargetObjects([...stateContext.players.keys()], POSITION.TOP)}
                    </GameLayout.Row>
                    <GameLayout.RowExpanded>
                        <GameLayout.Col show={circleView} ml={".5rem"} $justifyContent={"space-around"}>
                            {genTargetObjects(layout.leftTargets, POSITION.LEFT)}
                        </GameLayout.Col>
                        <GameLayout.ColExpanded $justifyContent={"center"} $alignItems={"center"}>
                            {props.children}
                        </GameLayout.ColExpanded>
                        <GameLayout.Col show={circleView} mr={".5rem"} $justifyContent={"space-around"}>
                            {genTargetObjects(layout.rightTargets, POSITION.RIGHT)}
                        </GameLayout.Col>
                    </GameLayout.RowExpanded>
                </GameLayout.ColExpanded>
                <GameLayout.Row show={circleView} $justifyContent={"center"} mb={"1rem"}>
                    {genTargetObjects(layout.bottomTargets, POSITION.BOTTOM)}
                </GameLayout.Row>
                <AbsolutePosition pos={POSITION.BOTTOM_RIGHT}>
                    <ChangeViewBtn onChangeView={(view) => setView(view)} view={view} />
                </AbsolutePosition>
            </GameLayout.ColExpanded>
        </TargetContext.Provider>
    );
}

export default TargetPicker;
