import {useContext, useEffect, useState} from "react";
import {useTranslation} from "react-i18next";
import {EventNames, Roles} from "../../../../werewolf-consts/enums";
import {RoomContext} from "../../../../context";
import {WereWolfEvent} from "../../../../werewolf-consts/colyseus/state/WereWolfLog";
import {getPlayersName} from "../../../../werewolf-consts/colyseus/stateUtils";
import {mapSchema2Object} from "../../../../werewolf-consts/colyseus/Utils";
import {EventData} from "../../../../werewolf-consts/definitions/EventData";
import {I18Namspace} from "../../../../language/i18n";
import {GameHistoryEnum} from "../../../../language/Vietnamese/GameHistory_vi";

interface IEventData {
    description: string;
}

function useEventDescription(event: WereWolfEvent, withTime = false) {
    const [data, setData] = useState<IEventData>({description: ""});
    let roomContext = useContext(RoomContext);
    const {t} = useTranslation(I18Namspace.GameHistory);
    useEffect(() => {
        let des = "";
        const eventData = mapSchema2Object<EventData>(event.data);
        const player = roomContext.state.players[event.from || ""];
        const actorName = player ? `${player?.name}(${player?.seat})` : "";
        let targetNames = getPlayersName(roomContext.state, eventData.targets, true);
        switch (event.eventName) {
            case EventNames.VOTE:
                des = t(EventNames.VOTE, {name: targetNames[0]});
                break;
            case EventNames.VOTE_YES:
                des = t(EventNames.VOTE_YES, {name: targetNames[0]});
                break;
            case EventNames.VOTE_NO:
                des = t(EventNames.VOTE_NO, {name: targetNames[0]});
                break;
            case EventNames.BITE:
                des = t(EventNames.BITE, {target: targetNames[0]});
                break;
            case EventNames.SEE:
                let eventName = `${EventNames.SEE}.${event.data.success ? Roles.WEREWOLF : Roles.VILLAGER}`;
                des = t(eventName, {seer: actorName, target: targetNames[0]});
                break;
            case EventNames.SAVE:
                des = t(EventNames.SAVE, {witch: actorName, target: targetNames[0]});
                break;
            case EventNames.FIRE:
                des = t(EventNames.FIRE, {hunter: actorName, target: targetNames[0]});
                break;
            case EventNames.FIRE_DIRECTLY:
                des = t(EventNames.FIRE_DIRECTLY, {hunter: actorName, target: targetNames[0]});
                break;
            case EventNames.KILL:
                des = t(EventNames.KILL, {witch: actorName, target: targetNames[0]});
                break;
            case EventNames.CURSE:
                des = t(EventNames.CURSE, {wolfplus: actorName, target: targetNames[0]});
                break;
            case EventNames.PAIRING:
                des = t(EventNames.PAIRING, {cupid: actorName, target1: targetNames[0], target2: targetNames[1]});
                break;
            case EventNames.CHOOSE_MOTHER:
                des = t(EventNames.CHOOSE_MOTHER, {wildchild: actorName, target: targetNames[0]});
                break;
            case EventNames.SEE_MOTHER_DIE:
                des = t(EventNames.SEE_MOTHER_DIE, {wildchild: actorName, target: targetNames[0]});
                break;
            case EventNames.PROTECT:
                des = t(EventNames.PROTECT, {saver: actorName, target: targetNames[0]});
                break;
            case EventNames.BE_BITE:
                des = t(EventNames.SAVE, {oldman: actorName, lifeCount: event.data.targets[0]});
                break;
            case EventNames.BECOME_WOLF:
                des = t(EventNames.BECOME_WOLF, {cursed: actorName});
                break;
            case EventNames.DIE_OF_LOVE:
                des = t(EventNames.DIE_OF_LOVE, {name1: actorName, name2: targetNames[0]});
                break;
            default:
                break;
        }
        if (withTime && eventData.dayNo != null && eventData.session != null) {
            des = `${t(`${GameHistoryEnum.gameHistoryHeader}.${eventData.session}`, {dayNo: eventData.dayNo})}: ${des}`;
        }
        setData({description: des});
    }, [event, roomContext.state, t, withTime]);
    return data;
}

export default useEventDescription;
