import React, {useContext, useState} from "react";
import PropTypes, {InferProps} from "prop-types";
import GameLayout from "../shared/game-advance-components/GameLayout";
import GameIconButton from "../shared/game-basic-components/GameIconButton";
import {POSITION} from "../shared/styled/game-layout/StackLayout";
import {COLOR} from "../shared/styled/ColorsPalette";
import backBtn from "../../assets/game-btn/back.svg";
import {GameInfoEnum} from "../../language/Vietnamese/GameInfo_vi";
import {GameText} from "../shared/game-basic-components/GameText";
import GameInput from "../shared/game-basic-components/GameInput";
import unlockIc from "../../assets/game-btn/unlocked.svg";
import {GameRouterContext} from "../shared/game-advance-components/GameRouter";
import {useTranslation} from "react-i18next";
import {I18Namspace} from "../../language/i18n";
import {SizeContext} from "../../context/SizeContext";

GameRoomAuthScreen.propTypes = {
    onSubmitPass: PropTypes.func,
    roomName: PropTypes.string
};

function GameRoomAuthScreen(props: InferProps<typeof GameRoomAuthScreen.propTypes>) {
    let router = useContext(GameRouterContext);
    let [showAuth, setShowAuth] = useState<boolean>(false);
    const {basic} = useContext(SizeContext);
    const [roomPass, setRoomPass] = useState<string>();
    const {t} = useTranslation(I18Namspace.GameInfo);
    const handleKeyDown = (event) => {
        if (event.key === "Enter") {
            if (props.onSubmitPass) props.onSubmitPass(roomPass);
            setRoomPass("");
        }
    };
    return (
        <GameLayout.ColExpanded $justifyContent={"flex-start"} $backgroundColor={COLOR.blurDark80}>
            <GameLayout.Row>
                <GameIconButton
                    $textPosition={POSITION.RIGHT}
                    color={COLOR.bloodColor}
                    onClick={() => {
                        if (router?.routeTo) router.routeTo("lobby", []);
                    }}
                    src={backBtn}
                    size={basic.size5}
                    mx={basic.iconButtonSize * 0.1}
                >
                    {t(`${GameInfoEnum.back}`)}
                </GameIconButton>
                <GameLayout.ColExpanded />
            </GameLayout.Row>
            <GameLayout.ColExpanded $justifyContent={"center"} $alignItems={"center"}>
                <GameText mb={"1rem"} $fontStyle={"Altus"} size={basic.size10}>
                    {props.roomName}
                </GameText>
                <GameLayout.Row $backgroundColor={"white"} round={"5rem"} px={"1rem"} py={".5rem"}>
                    <GameInput
                        $backgroundColor={"white"}
                        color={"black"}
                        $fSize={basic.size5}
                        placeholder={t(`${GameInfoEnum.enterPassword}`)}
                        h={basic.size10}
                        value={roomPass}
                        onChange={(event) => setRoomPass(event.target.value)}
                        onKeyDown={handleKeyDown}
                    />
                    <GameIconButton
                        src={unlockIc}
                        size={basic.size9}
                        onClick={() => {
                            if (props.onSubmitPass) props.onSubmitPass(roomPass);
                            setRoomPass("");
                        }}
                    />
                </GameLayout.Row>
            </GameLayout.ColExpanded>
        </GameLayout.ColExpanded>
    );
}

export default GameRoomAuthScreen;
