import React, {useContext} from "react";
import {WereWolfEvent} from "../../../../werewolf-consts/colyseus/state/WereWolfLog";
import useEventDescription from "../../game-history/hooks/useEventDescription";
import {SizeContext} from "../../../../context/SizeContext";
import {GameIcon} from "../../../shared/game-basic-components/GameIcon";
import {SkillImage} from "../../game-body/action-form/SkillButton";
import {GameText} from "../../../shared/game-basic-components/GameText";
import GameLayout from "../../../shared/game-advance-components/GameLayout";

interface IRoleEvent {
    event: WereWolfEvent;
}

function RoleEvent(props: IRoleEvent) {
    let des = useEventDescription(props.event, true);
    const {basic} = useContext(SizeContext);
    return (
        <GameLayout.Row $alignItems="center" my={".3rem"}>
            <GameIcon src={SkillImage[props.event.eventName]} size={basic.size8} />
            <GameText ml="3%" size={basic.size4} $fontStyle={"Altus"}>
                {des.description}
            </GameText>
        </GameLayout.Row>
    );
}

export default RoleEvent;
