GameCardShower.propTypes = {
    onClose: PropTypes.func,
    btnString: PropTypes.string,
    size: PropTypes.number
};
GameCardShower.defaultProps = {
    onClose: null,
    btnString: "Sẵn sàng",
    size: 300
};

function GameCardShower(props) {
    let roomContext = useContext(RoomContext);
    const userContext = useContext(UserContext);
    const [role, setRole] = useState(Roles.WEREWOLF);
    const [flip, setFlip] = useState<boolean>(false);
    const {t} = useTranslation("RoleGuide");
    useEffect(() => {
        let role = Roles.VILLAGER;
        try {
            role = roomContext.state.players[userContext.playerId].role.roleID;
        } catch (e) {}
        if (role) {
            setRole(role);
        }
    }, [roomContext.state.players[userContext.playerId].role.roleID, userContext.playerId]);

    return (
        <Fragment>
            <Card
                onClick={() => setFlip(!flip)}
                style={{height: `${props.size}px`, width: `${props.size * (3 / 3.5)}px`, alignSelf: "center"}}
            >
                <FlipCard style={{transform: flip ? "rotateY(180deg)" : ""}}>
                    <CardFront>
                        <RoleCardImage
                            size={props.size}
                            src={ROLE_AVATAR[role]}
                            round_right={"1rem"}
                            round_left={"1rem"}
                        />
                    </CardFront>
                    <CardBack>
                        <RoleCardText size={props.size}>
                            <RoleCardTextHeader size={props.size}>
                                {t(`${RoleGuideEnum.name}.${role}`)}
                            </RoleCardTextHeader>
                            <RoleCardTextBody size={props.size}>
                                {t(`${RoleGuideEnum.description}.${role}`)}
                            </RoleCardTextBody>
                        </RoleCardText>
                    </CardBack>
                </FlipCard>
            </Card>
        </Fragment>
    );
}

export default GameCardShower;
