import {styled} from "styletron-react";
import {getBasicCSS, IBasicProps} from "./GameDiv";
import {COLOR} from "../styled/ColorsPalette";

const GameInput = styled<any, IBasicProps>("input", (props) => {
    let customProps = {...props};
    customProps.round = "5rem";
    delete customProps["$textAlign"];
    customProps.$backgroundColor = props.$backgroundColor || COLOR.inputBackground;
    customProps.px = props.px || ".3rem";
    return {
        ...getBasicCSS(customProps),
        textAlign: props.$textAlign || "left",
        wordBreak: "break-all",
        border: "none",
        flex: 1,
        ":focus": {
            outline: "none"
        }
    };
});

export default GameInput;
