import React, {useCallback, useContext, useEffect, useState} from "react";
import {DEFAULT_AVT, FacebookPlayer} from "../../context/UserContext";
import GameLayout from "../shared/game-advance-components/GameLayout";
import {GameText} from "../shared/game-basic-components/GameText";
import {SizeContext} from "../../context/SizeContext";
import GoogleIcon from "../../assets/game-btn/google.svg";
import GameIconButton from "../shared/game-basic-components/GameIconButton";
import {firebase} from "@phamngocduy98/flashstore-react";
import {googleAuthProvider} from "../../context/FirestoreContext";
import {POSITION} from "../shared/styled/game-layout/StackLayout";
import {COLOR} from "../shared/styled/ColorsPalette";
import {GameDiv} from "../shared/game-basic-components/GameDiv";
import {importScript} from "../../utils/react_utils";
import {DEV, FBINSTANT_CLIENT_SIGNATURE} from "../../env.const";

interface LoginFormProps {
    onLogin: ({playerId, playerName, playerPic, signature}: Partial<FacebookPlayer>) => any;
}

export function LoginForm(props: LoginFormProps) {
    const {basic, window: windowSize} = useContext(SizeContext);
    const [playerId, setPlayerId] = useState("1");

    const {onLogin} = props;

    useEffect(() => {
        // load fbInstant script
        const script = importScript("https://connect.facebook.net/en_US/fbinstant.6.2.js");
        script.onload = async () => {
            try {
                await window.FBInstant.initializeAsync();
                await window.FBInstant.startGameAsync();
                let result = await window.FBInstant.player.getSignedPlayerInfoAsync(FBINSTANT_CLIENT_SIGNATURE);
                const user = {
                    playerId: "fbg-" + window.FBInstant.player.getID() || undefined,
                    playerName: window.FBInstant.player.getName() || "FB-user",
                    playerPic: window.FBInstant.player.getPhoto() || DEFAULT_AVT,
                    signature: "FB-" + result.getSignature()
                };
                onLogin(user);
            } catch (e) {}
        };
        document.body.appendChild(script);
        return () => {
            document.body.removeChild(script);
        };
    }, [onLogin]);

    function quickLogin(key) {
        if (key.keyCode >= 49 && key.keyCode <= 57) props.onLogin({playerId: key.keyCode + ""});
    }

    const googleLogin = useCallback(
        async function () {
            try {
                const {user, credential} = await firebase.auth().signInWithPopup(googleAuthProvider);
                if (user) {
                    const token = await user.getIdToken();
                    const accessToken = (credential as firebase.auth.OAuthCredential)?.accessToken;
                    const player = {
                        playerId: "gg-" + user.uid,
                        playerName: user.displayName || undefined,
                        playerPic: user.photoURL || undefined,
                        signature: "GG-" + token ?? ""
                    };
                    if (DEV) console.log(player);
                    onLogin(player);
                }
            } catch (error) {
                const {code, message, email, credential} = error;
                console.error(message);
            }
        },
        [onLogin]
    );

    return (
        <>
            <GameLayout.Col
                $justifyContent={"flex-start"}
                $alignItems={"center"}
                w={windowSize.width}
                h={windowSize.height}
            >
                <GameLayout.Row
                    m={".5rem"}
                    $alignItems={"center"}
                    $alignSelf={"flex-start"}
                    w={windowSize.isVertical ? windowSize.width * 0.8 : windowSize.width / 3}
                >
                    <GameText m={".5rem"} size={basic.size9} $fontStyle={"Altus"}>
                        @masoibot
                    </GameText>
                </GameLayout.Row>
                <GameLayout.ColExpanded
                    $alignSelf={windowSize.isVertical ? "center" : "flex-start"}
                    $alignItems={"center"}
                    $justifyContent={"center"}
                    w={windowSize.isVertical ? windowSize.width * 0.8 : windowSize.width / 3}
                    pl={windowSize.isVertical ? 0 : "3rem"}
                >
                    <GameText size={basic.size10 * 2} mt={"1rem"} $fontStyle={"Butcherandblock"} $fontWeight={"border"}>
                        MA SÓI
                    </GameText>
                    <GameDiv onKeyUp={quickLogin}>
                        <GameIconButton
                            $backgroundColor={COLOR.blurDark50}
                            round={"3rem"}
                            src={GoogleIcon}
                            size={basic.iconButtonSize}
                            onClick={googleLogin}
                            py={".5rem"}
                            px={"1rem"}
                            $textPosition={POSITION.RIGHT}
                        >
                            LOGIN WITH GOOGLE
                        </GameIconButton>
                        {DEV && (
                            <>
                                <br />
                                <input
                                    name={"playerId"}
                                    placeholder={"playerId"}
                                    onChange={(event) => setPlayerId(event.target.value)}
                                />
                                <button onClick={() => props.onLogin({playerId})}>Login</button>
                            </>
                        )}
                    </GameDiv>
                </GameLayout.ColExpanded>
            </GameLayout.Col>
        </>
    );
}
