import React, {useContext, useEffect, useState} from "react";
import {RoomContext} from "../../../../context/RoomContext";
import {MessageTypes} from "../../../../werewolf-consts/colyseus/MessageTypes";
import {WereWolfEvent} from "../../../../werewolf-consts/colyseus/State";
import {EventNames} from "../../../../werewolf-consts/enums";
import {mapSchema2Object} from "../../../../werewolf-consts/colyseus/Utils";
import {useTranslation} from "react-i18next";
import {ActionResultEnum} from "../../../../language/Vietnamese/ActionResult_vi";
import {CupidEventData, EventData} from "../../../../werewolf-consts/definitions/EventData";
import {getPlayersName} from "../../../../werewolf-consts/colyseus/stateUtils";
import {I18Namspace} from "../../../../language/i18n";
import {DEV} from "../../../../env.const";

export interface IGameEventData {
    eventName: EventNames;
    targets: string[];
    content: string;
}

function useGameEvent() {
    const {t} = useTranslation([I18Namspace.ActionResult]);
    let [notification, setNotification] = useState<IGameEventData>({
        targets: [],
        eventName: EventNames.NO_EVENT,
        content: ""
    });
    const roomContext = useContext(RoomContext);
    const {room, state} = roomContext;
    useEffect(() => {
        const offMessageActionResult = room.onMessage<WereWolfEvent>(MessageTypes.ACTION_RESULT, (event) => {
            if (!event) return;
            let data: IGameEventData = {
                eventName: event.eventName,
                targets: [],
                content: ""
            };
            let eventData = mapSchema2Object<EventData>(event.data);
            if (Array.isArray(eventData.targets)) {
                data.targets = eventData.targets;
                if (DEV) console.log("Targets", eventData.targets);
                let targetsName = getPlayersName(state, eventData.targets, true);
                switch (event.eventName) {
                    case EventNames.VOTE_YES:
                        data.eventName = EventNames.VOTE;
                        data.content = t(EventNames.VOTE_YES, {name: targetsName[0]});
                        break;
                    case EventNames.VOTE_NO:
                        data.eventName = EventNames.VOTE;
                        data.content = t(EventNames.VOTE_NO, {name: targetsName[0]});
                        break;
                    case EventNames.VOTE:
                        if (targetsName.length > 0) {
                            data.content = t(`${EventNames.VOTE}.${ActionResultEnum.hang}`, {name: targetsName[0]});
                        } else {
                            data.content = t(`${EventNames.VOTE}.${ActionResultEnum.noHang}`);
                        }
                        break;
                    case EventNames.BECOME_WOLF:
                        data.content = t(EventNames.BECOME_WOLF);
                        break;
                    case EventNames.BITE:
                        if (eventData.targets.length === 1) {
                            data.content = t(`${EventNames.BITE}.${ActionResultEnum.bite}`, {name: targetsName[0]});
                        } else
                            data.content = t(`${EventNames.BITE}.${ActionResultEnum.noBite}`, {name: targetsName[0]});
                        break;
                    case EventNames.SEE:
                        if (eventData.success) {
                            data.content = t(`${EventNames.SEE}.${ActionResultEnum.werewolf}`, {name: targetsName[0]});
                        } else {
                            data.content = t(`${EventNames.SEE}.${ActionResultEnum.villager}`, {name: targetsName[0]});
                        }
                        break;
                    case EventNames.SAVE:
                        data.content = t(EventNames.SAVE, {name: targetsName[0]});
                        break;
                    case EventNames.FIRE:
                        data.content = t(EventNames.FIRE, {name: targetsName[0]});
                        break;
                    case EventNames.FIRE_DIRECTLY:
                        data.content = t(EventNames.FIRE_DIRECTLY, {name: targetsName[0]});
                        break;
                    case EventNames.KILL:
                        data.content = t(EventNames.KILL, {name: targetsName[0]});
                        break;
                    case EventNames.CURSE:
                        data.content = t(`${EventNames.CURSE}`, {name: targetsName[0]});
                        break;
                    case EventNames.BE_CURSED:
                        data.content = t(`${EventNames.BE_CURSED}`);
                        break;
                    case EventNames.PAIRING:
                        data.content = t(`${EventNames.PAIRING}`, {name1: targetsName[0], name2: targetsName[1]});
                        break;
                    case EventNames.BE_PAIRED_WITH:
                        const cupidData = mapSchema2Object<CupidEventData>(event.data);
                        data.content = t(`${EventNames.BE_PAIRED_WITH}.${cupidData.team}`, {name: targetsName[0]});
                        break;
                    case EventNames.VIEW_DEAD_MAN:
                        if (targetsName.length === 0)
                            data.content = t(`${EventNames.VIEW_DEAD_MAN}.${ActionResultEnum.nobody}`);
                        else {
                            data.content = t(`${EventNames.VIEW_DEAD_MAN}.${ActionResultEnum.deadMan}`, {
                                names: targetsName
                            });
                        }
                        break;
                    case EventNames.CHOOSE_MOTHER:
                        if (!eventData.success)
                            data.content = t(`${EventNames.CHOOSE_MOTHER}.${ActionResultEnum.chose}`, {
                                name: targetsName[0]
                            });
                        else
                            data.content = t(`${EventNames.CHOOSE_MOTHER}.${ActionResultEnum.motherDie}`, {
                                name: targetsName[0]
                            });
                        break;
                    case EventNames.PROTECT:
                        data.content = t(EventNames.PROTECT, {name: targetsName[0]});
                        break;
                    case EventNames.BE_BITE:
                        data.targets = [eventData.targets[0]];
                        data.content = t(EventNames.BE_BITE, {lifeCount: eventData.targets[1]});
                        break;
                    default:
                        data.content = "";
                        break;
                }
                setNotification(data);
            }
        });
        return () => {
            offMessageActionResult();
        };
    }, [room, state, t]);

    return notification;
}

export default useGameEvent;
