import React, {useContext} from "react";
import GameLayout from "../../../shared/game-advance-components/GameLayout";
import {RoomContext} from "../../../../context/RoomContext";
import {SizeContext} from "../../../../context/SizeContext";
import RoleCard from "../../startgame-screen/RoleCard";
import {GameText} from "../../../shared/game-basic-components/GameText";
import {useTranslation} from "react-i18next";
import {I18Namspace} from "../../../../language/i18n";
import {PlayerInfoEnum} from "../../../../language/Vietnamese/PlayerInfo_vi";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";
import RoleEvent from "./RoleEvent";
import {COLOR} from "../../../shared/styled/ColorsPalette";
import RoleCardBack from "../../../../assets/images/card_back.jpg";
import {GameImage} from "../../../shared/game-basic-components/GameImage";
import {UserContext} from "../../../../context/UserContext";

MyRoleInfo.propTypes = {};

function MyRoleInfo(props) {
    const roomContext = useContext(RoomContext);
    const userContext = useContext(UserContext);
    const myRole = roomContext.state.players[userContext.playerId]?.role;
    const {basic} = useContext(SizeContext);
    const {t} = useTranslation(I18Namspace.PlayerInfo);
    return (
        <>
            {myRole != null && (
                <GameDiv
                    display={"flex"}
                    flex={1}
                    $flexDirection={basic.mobileView ? "column" : "row"}
                    $justifyContent={"space-around"}
                    $alignItems={"center"}
                >
                    <RoleCard role={myRole.roleID} />
                    <GameLayout.Col
                        $justifyContent={"flex-start"}
                        $alignItems={"center"}
                        $backgroundColor={COLOR.blurDark80}
                        round={".5rem"}
                        h={basic.mobileView ? basic.gameH * 0.4 : basic.gameH * 0.8}
                        // w={basic.mobileView ? basic.gameW * 0.9 : basic.gameW * 0.5}
                    >
                        <GameText size={basic.size10} $fontStyle={"Altus"}>
                            {t(`${PlayerInfoEnum.roleHistory}`)}
                        </GameText>
                        <GameDiv
                            $scrollY
                            h={(basic.mobileView ? basic.gameH * 0.4 : basic.gameH * 0.8) - basic.size10}
                            w={basic.mobileView ? basic.gameW * 0.9 : basic.gameW * 0.5}
                        >
                            <GameLayout.Col>
                                {myRole.roleEvents.map((event, key) => {
                                    return <RoleEvent key={"r_event" + key} event={event} />;
                                })}
                            </GameLayout.Col>
                        </GameDiv>
                    </GameLayout.Col>
                </GameDiv>
            )}
            {myRole == null && (
                <GameLayout.ColExpanded $justifyContent={"center"}>
                    <GameImage
                        $alignSelf={"center"}
                        round={".8rem"}
                        src={RoleCardBack}
                        w={basic.gameH / 3}
                        ratio={3 / 4}
                    />
                    <GameText size={basic.size5} $fontStyle={"Altus"}>
                        {t(`${PlayerInfoEnum.noRoleData}`)}
                    </GameText>
                </GameLayout.ColExpanded>
            )}
        </>
    );
}

export default MyRoleInfo;
