import React, {useContext} from "react";
import {GameDiv, IBasicProps} from "./GameDiv";
import {GameIcon} from "./GameIcon";
import {GameText} from "./GameText";
import {SizeContext} from "../../../context/SizeContext";
import {POSITION} from "../styled/game-layout/StackLayout";

interface IGameIconBtnProps extends IBasicProps {
    onClick?: () => void;
    src: string;
    children?: string;
    disabled?: boolean;
    $textPosition?: POSITION.RIGHT | POSITION.LEFT | POSITION.BOTTOM;
}

function GameIconButton(props: IGameIconBtnProps) {
    let customProps = {...props};
    const {basic: baseSize} = useContext(SizeContext);
    delete customProps.src;
    delete customProps.size;
    delete customProps.children;
    delete customProps.disabled;
    delete customProps.onClick;
    customProps.display = "flex";
    customProps.cursor = "pointer";
    customProps.$alignItems = "center";
    let size = props.size || baseSize.size3;
    let textSize =
        props.$textPosition === POSITION.RIGHT || props.$textPosition === POSITION.LEFT ? size * 0.8 : size * 0.4;
    delete customProps["size"];
    let flexDirection: "column" | "row" | "column-reverse" | "row-reverse" = "column";
    if (props.$textPosition === POSITION.RIGHT) flexDirection = "row";
    if (props.$textPosition === POSITION.LEFT) flexDirection = "row-reverse";
    customProps.$flexDirection = flexDirection;
    customProps.opacity = props.disabled ? 0.5 : 1;
    customProps.$pointerEvents = "auto";

    return (
        <GameDiv>
            <GameDiv
                {...customProps}
                onClick={() => {
                    if (props.onClick) props.onClick();
                }}
            >
                <GameIcon src={props.src} size={size} />
                {props.children != null && (
                    <GameText size={textSize} color={props.color} m={".5rem"} $fontStyle={"Altus"}>
                        {props.children}
                    </GameText>
                )}
            </GameDiv>
        </GameDiv>
    );
}

export default GameIconButton;
