import {useEffect, useState} from "react";
import {RefreshCallback, useFirestoreDocument} from "@phamngocduy98/flashstore-react";
import {MyDatabase} from "../firestore/MyDatabase";
import {User} from "../firestore/User";
import {arrayNullUndefinedFilter} from "../../werewolf-consts/utils/miscellaneous";

export function useFollowings(db: MyDatabase, uid: string): [User[], RefreshCallback] {
    const [user, doc, refresh] = useFirestoreDocument(db.users, uid);
    const [followings, setFollowings] = useState<User[]>([]);
    useEffect(() => {
        (async () => {
            if (user != null) {
                let followings = await user.following.getAll();
                setFollowings(followings.filter(arrayNullUndefinedFilter));
            }
        })().catch((e) => console.error(e));
    }, [user, doc]);
    return [followings, refresh];
}
