import {styled} from "styletron-react";
import {
    getBorder,
    getColor,
    getCursor,
    getFlex,
    getMargin,
    getPadding,
    getSize,
    IBorderProps,
    IColorProps,
    ICursorProps,
    IFlexProps,
    IMarginProps,
    IPaddingProps,
    ISizeProps
} from "../styled/BasicProps";

export interface IBasicProps
    extends IMarginProps,
        IPaddingProps,
        ISizeProps,
        IBorderProps,
        IColorProps,
        IFlexProps,
        ICursorProps {
    scroll?: boolean;
    $scrollX?: boolean;
    $scrollY?: boolean;
    $zIndex?: number;
    $fontStyle?: "FONT" | "Altus" | "AltusSerif" | "Butcherandblock";
    position?: "relative" | "absolute" | "fixed";
    $textAlign?: "center" | "left" | "right" | "justify";
    display?:
        | "inline"
        | "block"
        | "contents"
        | "flex"
        | "grid"
        | "inline-block"
        | "inline-flex"
        | "inline-grid"
        | "inline-table"
        | "list-item"
        | "run-in"
        | "table"
        | "table-caption"
        | "table-column-group"
        | "table-header-group"
        | "table-footer-group"
        | "table-row-group"
        | "table-cell"
        | "table-column"
        | "table-row"
        | "none";
}

export function getBasicCSS(props) {
    return {
        ...getMargin(props),
        ...getPadding(props),
        ...getSize(props),
        ...getBorder(props),
        ...getColor(props),
        ...getFlex(props),
        ...getCursor(props),
        textAlign: props.$textAlign,
        zIndex: props.$zIndex,
        fontFamily: props.$fontStyle != null ? props.$fontStyle : "",
        position: props.position
    };
}

export const GameDiv = styled<any, IBasicProps>("div", (props) => {
    let {scroll, $scrollX, $scrollY, display} = props;
    let overFlowX: "scroll" | "hidden" | undefined = $scrollX ? "scroll" : undefined;
    let overFlowY: "scroll" | "hidden" | undefined = $scrollY ? "scroll" : undefined;
    if (scroll === true) overFlowX = overFlowY = "scroll";
    if (scroll === false) overFlowY = overFlowX = "hidden";

    return {
        ...getBasicCSS(props),
        overflowX: overFlowX,
        overflowY: overFlowY,
        display: display,
        boxSizing: "border-box",
        pointerEvents: "auto"
    };
});
