import React, {useContext} from "react";
import {UserContext} from "../../context/UserContext";
import "../app/DefaultCSS.css";
import AvatarView from "../shared/game-basic-components/AvatarView";
import GameLayout from "../shared/game-advance-components/GameLayout";
import {GameText} from "../shared/game-basic-components/GameText";
import {GameRouterContext} from "../shared/game-advance-components/GameRouter";
import {useTranslation} from "react-i18next";
import {I18Namspace} from "../../language/i18n";
import {HomeScreenEnum} from "../../language/Vietnamese/HomeScreen_vi";
import {SizeContext} from "../../context/SizeContext";
import {GameDiv} from "../shared/game-basic-components/GameDiv";
import {COLOR} from "../shared/styled/ColorsPalette";
import {DEV} from "../../env.const";

function HomeScreen() {
    let user = useContext(UserContext);
    const {basic, window} = useContext(SizeContext);
    let router = useContext(GameRouterContext);
    const {t} = useTranslation(I18Namspace.HomeScreen);

    function goToScreen(path: string, params: (string | undefined)[]) {
        if (router?.routeTo) router.routeTo(path, params);
    }

    const Button = (text: string, path: string, params: (string | undefined)[] = []) => {
        return (
            <GameDiv
                display={"flex"}
                $alignItems={"center"}
                $justifyContent={"center"}
                round={"5rem"}
                m={".5rem"}
                $backgroundColor={COLOR.homeMainColor}
                w={window.isVertical ? window.width * 0.8 : window.width / 3}
                h={basic.playerAvatarSize * 1.5}
                onClick={() => goToScreen(path, params)}
                cursor={"pointer"}
            >
                <GameText
                    $fontStyle={"Altus"}
                    size={window.isVertical ? basic.size10 : basic.size10}
                    color={COLOR.homeSecondColor}
                    $fWeight={"bolder"}
                >
                    {text.toUpperCase()}
                </GameText>
            </GameDiv>
        );
    };

    return (
        <GameLayout.Col $justifyContent={"flex-start"} $alignItems={"center"} w={window.width} h={window.height}>
            <GameLayout.Row
                m={".5rem"}
                $alignItems={"center"}
                $alignSelf={"flex-start"}
                w={window.isVertical ? window.width * 0.8 : window.width / 3}
            >
                <AvatarView size={basic.playerAvatarSize} src={user.playerPic} />
                <GameText m={".5rem"} size={basic.size9} $fontStyle={"Altus"}>
                    {user.playerName}
                </GameText>
            </GameLayout.Row>
            <GameLayout.ColExpanded
                $alignSelf={window.isVertical ? "center" : "flex-start"}
                $alignItems={"center"}
                $justifyContent={"center"}
                w={window.isVertical ? window.width * 0.8 : window.width / 3}
                pl={window.isVertical ? 0 : "3rem"}
            >
                <GameText size={basic.size10 * 2} mt={"1rem"} $fontStyle={"Butcherandblock"} $fontWeight={"border"}>
                    MA SÓI
                </GameText>
                {Button(t(`${HomeScreenEnum.myVillage}`), "gameRoom", [user.playerId, user.villagePass])}
                {Button(t(`${HomeScreenEnum.availableVillage}`), "lobby")}
                <>{DEV && Button("Test Screen", "test")}</>
            </GameLayout.ColExpanded>
        </GameLayout.Col>
    );
}

export default HomeScreen;
