import {Roles} from "../../../../../werewolf-consts/enums/Roles";
import {ROLE_MAX} from "../../../../../werewolf-consts/definitions/RoleConsts";
import {MapSchema} from "@colyseus/schema";

export interface IGameSettingReducerAction {
    type: "+" | "-" | "clear" | "clone";
    role?: Roles;
    setup?: MapSchema<number>;
}

export const gameSettingReducer = (
    gameSetting: MapSchema<number>,
    action: IGameSettingReducerAction
): MapSchema<number> => {
    const result = new MapSchema(gameSetting);
    switch (action.type) {
        case "+":
            if (action.role != null) {
                const roleKey = action.role + "";
                const count: number = gameSetting[roleKey] || 0;
                if (count < ROLE_MAX[action.role]) result[roleKey] = count + 1;
            }
            break;
        case "-":
            if (action.role != null) {
                const roleKey = action.role + "";
                const count: number = gameSetting[roleKey] || 0;
                if (count > 0) result[roleKey] = count - 1;
                if (count - 1 === 0) delete result[roleKey];
            }
            break;
        case "clear":
            for (const roleKey in result) {
                delete result[roleKey];
            }
            break;
        case "clone":
            if (action.setup) {
                for (const roleKey in result) {
                    delete result[roleKey];
                }
                for (const role in action.setup) {
                    result[role] = action.setup[role];
                }
            }
            break;
        default:
            break;
    }
    return result;
};
