import React, {useContext} from "react";
import PropTypes from "prop-types";
import StackLayout, {LIST_POSITIONS, POSITION, REVERSE_POSITION} from "../../../shared/styled/game-layout/StackLayout";
import TargetItem from "./TargetItem";
import HandPointWithAttackers from "./HandPointWithAttackers";
import sleep_gif from "../../../../assets/role-symbols/sleeping.gif";
import {StageNames} from "../../../../werewolf-consts/enums/StageNames";
import {RoomContext} from "../../../../context/RoomContext";
import RoleAvatar from "./RoleAvatar";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";
import {PICKER_VIEW, TargetContext} from "../../../../context/TargetContext";
import {SizeContext} from "../../../../context/SizeContext";
import {GameText} from "../../../shared/game-basic-components/GameText";
import {getPlayersName} from "../../../../werewolf-consts/colyseus/stateUtils";
import {COLOR} from "../../../shared/styled/ColorsPalette";
import {StagesInDay} from "../../../../werewolf-consts/colyseus/state/WereWolfLog";
import {SkillNames} from "../../../../werewolf-consts/enums";
import {BadRoleStages} from "../../../../werewolf-consts/definitions/StageConsts";
import {arraySchema2Array} from "../../../../werewolf-consts/colyseus/Utils";
import {Action} from "../../../../werewolf-consts/colyseus/state/Action";
import {UserContext} from "../../../../context/UserContext";

TargetObject.propTypes = {
    showPointer: PropTypes.bool.isRequired,
    showAttacker: PropTypes.bool.isRequired,
    attackers: PropTypes.array,
    position: PropTypes.oneOf(LIST_POSITIONS),
    targetId: PropTypes.string,
    onChoose: PropTypes.func
};
const Stages_Not_Show_Sleep: StageNames[] = [
    StageNames.START_GAME,
    StageNames.WAITING_STAGE,
    StageNames.DISCUSS,
    StageNames.VOTE_YES_NO,
    StageNames.VOTE
];

function TargetObject(props) {
    let roomContext = useContext(RoomContext);
    const userContext = useContext(UserContext);
    const state = roomContext.state;
    const target = state.players[props.targetId];
    const currentStage = state.currentStage.stageName;
    const activePlayer = state.currentStage.activePlayers;

    let currentPickerView = useContext(TargetContext).currentView;
    const {basic} = useContext(SizeContext);
    let size = basic.playerAvatarSize;
    const showPrivate = activePlayer?.includes(userContext.playerId);

    let showRole = target?.role && currentPickerView === PICKER_VIEW.CIRCLE && currentStage === StageNames.END_GAME;

    const actions = arraySchema2Array<Action>(state?.currentStage?.actions);
    const ready =
        [StageNames.START_GAME, StageNames.END_GAME].includes(currentStage) &&
        actions.filter((a) => a.from === props.targetId && a.skill === SkillNames.READY).length === 1;

    function onClick() {
        if (props.onChoose) props.onChoose(props.targetId);
    }

    const showActive =
        showPrivate && target?.alive && activePlayer.includes(props.targetId) && !StagesInDay.includes(currentStage);

    const showSleep = target?.alive && !activePlayer.includes(props.targetId) && !StagesInDay.includes(currentStage);
    let colorRing = ready
        ? COLOR.readyColor
        : showActive
        ? BadRoleStages.includes(currentStage)
            ? COLOR.BadActiveColor
            : COLOR.GoodActiveColor
        : "transparent";
    return (
        <StackLayout.Container w={size} h={size * 1.5}>
            {/*avatar người chơi*/}
            <StackLayout.Layer index={2} h={size} w={size}>
                <GameDiv onClick={onClick}>
                    <GameDiv
                        round={"50%"}
                        $style={{
                            boxShadow: `0 0 2rem ${colorRing}`
                        }}
                    >
                        <TargetItem size={size} targetId={props.targetId} />
                    </GameDiv>
                    <GameText size={size / 5} mt={".1rem"} $fontStyle={"Altus"}>
                        {getPlayersName(roomContext.state, props.targetId)[0].substr(0, 15)}
                    </GameText>
                </GameDiv>
            </StackLayout.Layer>
            {/*Pointer kèm attacker*/}
            {(props.showPointer || (props.attackers && props.attackers.length > 0)) && (
                <StackLayout.Layer index={3} h={size} w={size / 2} pos={REVERSE_POSITION[props.position]}>
                    <HandPointWithAttackers
                        targetId={props.targetId}
                        showAttacker={props.showAttacker}
                        pos={props.position}
                        attackers={props.attackers}
                        size={size / 2}
                    />
                </StackLayout.Layer>
            )}
            {/*Biểu tượng người dùng đang ngủ*/}
            {showSleep && (
                <StackLayout.Layer index={3} pos={POSITION.TOP} h={size / 2.5} w={(size * 3) / 10}>
                    <img
                        style={{
                            position: "absolute",
                            bottom: "10%",
                            left: "30%"
                        }}
                        width={size / 2}
                        height={(size * 3) / 8}
                        src={sleep_gif}
                        alt={""}
                        onClick={onClick}
                    />
                </StackLayout.Layer>
            )}
            {showRole && (
                <StackLayout.Layer index={0} pos={REVERSE_POSITION[props.position]} h={size} w={size}>
                    <RoleAvatar pos={props.position} targetId={props.targetId} size={size} />
                </StackLayout.Layer>
            )}
        </StackLayout.Container>
    );
}

export default TargetObject;
