import React, {useContext} from "react";
import {GameIcon} from "../../shared/game-basic-components/GameIcon";
import {SkillImage} from "../game-body/action-form/SkillButton";
import useEventDescription from "./hooks/useEventDescription";
import {COLOR} from "../../shared/styled/ColorsPalette";
import GameLayout from "../../shared/game-advance-components/GameLayout";
import {GameText} from "../../shared/game-basic-components/GameText";
import {WereWolfEvent} from "../../../werewolf-consts/colyseus/state/WereWolfLog";
import {SizeContext} from "../../../context/SizeContext";

interface IGameEvent {
    event: WereWolfEvent;
}

function GameEvent(props: IGameEvent) {
    let des = useEventDescription(props.event);
    const {basic: baseSize} = useContext(SizeContext);
    return (
        <GameLayout.Row $alignItems="center" my={".3rem"}>
            <GameIcon src={SkillImage[props.event.eventName]} size={baseSize.iconButtonSize * 0.5} />
            <GameText ml="3%" color={COLOR.gameHistoryColor} size={baseSize.size4}>
                {des.description}
            </GameText>
        </GameLayout.Row>
    );
}

export default GameEvent;
