import React, {useContext, useEffect, useState} from "react";
import GameLayout from "../../../shared/game-advance-components/GameLayout";
import {GameText} from "../../../shared/game-basic-components/GameText";
import GameInput from "../../../shared/game-basic-components/GameInput";
import GameCheckBox from "../../../shared/game-basic-components/GameCheckBox";
import {SizeContext} from "../../../../context/SizeContext";
import switchOn from "../../../../assets/game-btn/switch_on.svg";
import switchOff from "../../../../assets/game-btn/switch_off.svg";

interface IProps {
    inputType: "text" | "number" | "boolean";
    title?: string;
    placeHolder?: string;
    value?: string | number | boolean | undefined;
    onChange?: (value: string | number | boolean | undefined) => void;
}

function RoomSetupItem(props: IProps) {
    const [value, setValue] = useState<string | number | boolean | undefined>(props.value);
    const {basic} = useContext(SizeContext);

    function change(v: string | number | boolean | undefined) {
        if (props.onChange) {
            props.onChange(v);
        }
    }

    useEffect(() => {
        setValue(props.value);
    }, [props.value]);
    return (
        <>
            <hr
                style={{
                    margin: ".6rem 0",
                    width: "100%",
                    color: "white"
                }}
            />
            <GameLayout.Row $alignItems={"center"} $justifyContent={"space-between"}>
                <GameText $textAlign={"left"} mr={"1rem"} $fontStyle={"Altus"} size={basic.size7}>
                    {props.title}
                </GameText>
                <GameLayout.ColExpanded />
                <>
                    {["text", "number"].includes(props.inputType) && (
                        <GameInput
                            $textAlign={"center"}
                            type={props.inputType}
                            placeholder={props.placeHolder}
                            px={".5rem"}
                            py={".2rem"}
                            $backgroundColor={"white"}
                            value={value}
                            $fontStyle={"Altus"}
                            $fSize={basic.size5}
                            $maxW={basic.size10 * 2}
                            onChange={(event) => {
                                setValue(event.target.value);
                                change(event.target.value);
                            }}
                            color={"back"}
                        />
                    )}
                </>
                <>
                    {props.inputType === "boolean" && (
                        <GameCheckBox
                            icCheck={switchOn}
                            icUnCheck={switchOff}
                            value={value as boolean}
                            size={basic.size10}
                            ml={"1rem"}
                            onChange={(value) => {
                                setValue(value);
                                change(value);
                            }}
                        />
                    )}
                </>
            </GameLayout.Row>
        </>
    );
}

export default RoomSetupItem;
