import React from "react";
import {COLOR, ColorEnum} from "../styled/ColorsPalette";
import {GameText} from "./GameText";
import {IBasicProps} from "./GameDiv";
import {NoticeType} from "../../game-screens/notifications/context/RoomNotifierContext";

interface IXButtonProps extends IBasicProps {
    onClick?: () => void;
}

function XButton(props: IXButtonProps) {
    const customProps = {...props};
    delete customProps["onClick"];
    delete customProps["color"];
    return (
        <GameText
            cursor={"pointer"}
            $pointerEvents={"auto"}
            onClick={() => {
                if (props.onClick) props.onClick();
            }}
            {...customProps}
            color="transparent"
            style={{
                textShadow: `0 0 0 ${props.color || COLOR[`${ColorEnum.noticeText}${NoticeType.error}`]}`
            }}
        >
            ❌
        </GameText>
    );
}

export default XButton;
