import React, {ReactNode} from "react";
import {GameDiv, IBasicProps} from "../game-basic-components/GameDiv";
import {POSITION} from "../styled/game-layout/StackLayout";

interface ILayoutProps extends IBasicProps {
    children?: ReactNode[] | ReactNode;
    show?: boolean;
    reverse?: boolean;
}

function Stack(props: ILayoutProps) {
    let {show} = props;
    if (show == null) show = true;
    let containerProps = {...props};
    delete containerProps["children"];
    containerProps.position = "relative";
    containerProps.display = "flex";
    containerProps.$justifyContent = "center";
    containerProps.$alignItems = "center";
    containerProps.flex = 1;
    let children = Array.isArray(props.children) ? props.children : [props.children];
    return show ? (
        <GameDiv {...containerProps}>
            {children.map((el, i) => {
                let childProps = {...containerProps};
                childProps.position = "absolute";
                return (
                    <GameDiv key={"stackChild" + i} {...childProps}>
                        {el}
                    </GameDiv>
                );
            })}
        </GameDiv>
    ) : (
        <></>
    );
}

interface IPositioned extends IBasicProps {
    children: ReactNode;
    pos: POSITION;
}

function Positioned(props: IPositioned) {
    let cProps = {...props};
    cProps.display = "flex";
    cProps.flex = 1;
    let justifyContent: "center" | "flex-start" | "flex-end" = "center";
    let alignItems: "center" | "flex-start" | "flex-end" = "center";
    let pos = props.pos;
    switch (pos) {
        case POSITION.TOP:
            alignItems = "flex-start";
            break;
        case POSITION.TOP_LEFT:
            alignItems = "flex-start";
            justifyContent = "flex-start";
            break;
        case POSITION.TOP_RIGHT:
            alignItems = "flex-start";
            justifyContent = "flex-end";
            break;
        case POSITION.LEFT:
            justifyContent = "flex-start";
            break;
        case POSITION.CENTER:
            break;
        case POSITION.RIGHT:
            justifyContent = "flex-end";
            break;
        case POSITION.BOTTOM:
            alignItems = "flex-end";
            break;
        case POSITION.BOTTOM_LEFT:
            alignItems = "flex-end";
            justifyContent = "flex-start";
            break;
        case POSITION.BOTTOM_RIGHT:
            alignItems = "flex-end";
            justifyContent = "flex-end";
            break;
    }
    cProps.$justifyContent = justifyContent;
    cProps.$alignItems = alignItems;
    return <GameDiv {...cProps}>{props.children}</GameDiv>;
}

function Row(props: ILayoutProps) {
    let {show} = props;
    if (show == null) show = true;
    let cProps = {...props};
    cProps.display = "flex";
    cProps.$flexDirection = props.reverse === true ? "row-reverse" : "row";
    return show ? <GameDiv {...cProps}>{props.children}</GameDiv> : <></>;
}

function Col(props: ILayoutProps) {
    let {show} = props;
    if (show == null) show = true;
    let cProps = {...props};
    cProps.display = "flex";
    cProps.$flexDirection = props.reverse === true ? "column-reverse" : "column";
    return show ? <GameDiv {...cProps}>{props.children}</GameDiv> : <></>;
}

function RowExpanded(props: ILayoutProps) {
    let {show} = props;
    if (show == null) show = true;
    let cProps = {...props};
    cProps.$fullW = true;
    cProps.display = "flex";
    cProps.$flexDirection = props.reverse === true ? "row-reverse" : "row";
    cProps.flex = 1;
    return show ? <GameDiv {...cProps}>{props.children}</GameDiv> : <></>;
}

function ColExpanded(props: ILayoutProps) {
    let {show} = props;
    if (show == null) show = true;
    let cProps = {...props};
    cProps.$fullH = true;
    cProps.display = "flex";
    cProps.$flexDirection = props.reverse === true ? "column-reverse" : "column";
    cProps.flex = 1;
    return show ? <GameDiv {...cProps}>{props.children}</GameDiv> : <></>;
}

interface ICondition extends ILayoutProps {
    con: boolean;
}

function Condition(props: ICondition) {
    return props.con ? <>{props.children}</> : <></>;
}

const GameLayout = {
    Stack,
    Positioned,
    Row,
    Col,
    RowExpanded,
    ColExpanded,
    Condition
};

export default GameLayout;
