import {styled} from "styletron-react";
import {getBasicCSS, IBasicProps} from "./GameDiv";

interface GameImageProps extends IBasicProps {
    src: string;
}

export const GameImage = styled<any, GameImageProps>("img", (props) => {
    return {
        src: `${props.src}`,
        objectFit: "cover",
        userSelect: "none",
        ...getBasicCSS(props)
    };
});
