import {styled} from "styletron-react";
import {getBasicCSS, IBasicProps} from "./GameDiv";

interface AvatarViewProps extends IBasicProps {
    border_color?: string;
}

const AvatarView = styled<any, AvatarViewProps>("img", (props) => {
    let {border_color} = props;
    let customProps = {...props};
    customProps.round = "50%";
    customProps.$pointerEvents = "auto";
    let color = border_color ? border_color : "white";
    return {
        ...getBasicCSS(customProps),
        objectPosition: "center",
        objectFit: "cover",
        boxSizing: "border-box",
        border: `solid ${color} ${Math.min(Math.max(Math.trunc((props.size || 50) / 20), 1), 4)}px`
    };
});

export default AvatarView;
