import {styled} from "styletron-react";
import {getBasicCSS, IBasicProps} from "./GameDiv";

interface GameIconProps extends IBasicProps {
    src: string;
}

export const GameIcon = styled<any, GameIconProps>("img", (props) => {
    let {src} = props;
    let customProps = {...props};
    customProps.cursor = "pointer";
    return {
        src: `${src}`,
        objectFit: "scale-down",
        ...getBasicCSS(customProps),
        userSelect: "none"
    };
});
