import React, {ReactElement, ReactNode, useEffect, useRef, useState} from "react";
import {GameDiv, IBasicProps} from "../game-basic-components/GameDiv";
import {POSITION} from "../styled/game-layout/StackLayout";
import {IPositionProps} from "../styled/BasicProps";
import {Property} from "csstype";

interface IPOS {
    left: Property.Left;
    right: Property.Right;
    bottom: Property.Bottom;
    marginLeft: Property.MarginLeft;
    marginBottom: Property.MarginBottom;
}

const defaultIPOS: IPOS = {
    left: "unset",
    right: "unset",
    bottom: "unset",
    marginLeft: "unset",
    marginBottom: "unset"
};

function getAbsolutePos(pos: POSITION, W: number, H: number): IPOS {
    const iPos = {...defaultIPOS};
    switch (pos) {
        case POSITION.BOTTOM_RIGHT:
            iPos.left = "100%";
            break;
        case POSITION.BOTTOM_LEFT:
            iPos.right = "100%";
            break;
        case POSITION.TOP_LEFT:
            iPos.right = "100%";
            iPos.bottom = "100%";
            break;
        case POSITION.TOP_RIGHT:
            iPos.left = "100%";
            iPos.bottom = "100%";
            break;
        case POSITION.TOP:
            iPos.left = "50%";
            iPos.bottom = "100%";
            iPos.marginLeft = `-${W / 2}px`;
            break;
        case POSITION.BOTTOM:
            iPos.left = "50%";
            iPos.marginLeft = `-${W / 2}px`;
            break;
        case POSITION.LEFT:
            iPos.bottom = "50%";
            iPos.right = "100%";
            iPos.marginBottom = `-${H / 2}px`;
            break;
        case POSITION.RIGHT:
            iPos.bottom = "50%";
            iPos.left = "100%";
            iPos.marginBottom = `-${H / 2}px`;
            break;
    }
    return iPos;
}

function GameHoverPopup(props: IProps) {
    const [show, setShow] = useState<boolean>(false);
    const customProps = {...props};
    delete customProps["children"];
    delete customProps["popup"];
    delete customProps["pos"];
    const child = useRef<HTMLDivElement>(null);
    const [POS, setPOS] = useState<IPOS>(defaultIPOS);

    useEffect(() => {
        setPOS(
            getAbsolutePos(
                props.pos != null ? props.pos : POSITION.RIGHT,
                child.current?.clientWidth || 0,
                child.current?.clientHeight || 0
            )
        );
    }, [props.pos]);

    return (
        <GameDiv
            onMouseEnter={() => setShow(true)}
            onMouseLeave={() => setShow(false)}
            position={"relative"}
            display={"inline-block"}
        >
            {props.children}
            <div
                ref={child}
                style={{
                    position: "absolute",
                    zIndex: 11,
                    visibility: show ? "visible" : "hidden",
                    padding: ".2rem",
                    ...POS
                }}
            >
                <GameDiv {...customProps}>{props.popup}</GameDiv>
            </div>
        </GameDiv>
    );
}

interface IProps extends IBasicProps, IPositionProps {
    children: ReactNode;
    popup: ReactElement;
}

export default GameHoverPopup;
