import React, {useContext, useState} from "react";
import PropTypes from "prop-types";
import {GameDiv} from "../game-basic-components/GameDiv";
import {GameIcon} from "../game-basic-components/GameIcon";
import leftIcon from "../../../assets/game-btn/left.svg";
import rightIcon from "../../../assets/game-btn/right.svg";
import {GameText} from "../game-basic-components/GameText";
import GameLayout from "./GameLayout";
import {SizeContext} from "../../../context/SizeContext";

GameSlider.propTypes = {
    children: PropTypes.arrayOf(PropTypes.element)
};

function GameSlider(props) {
    const [index, setIndex] = useState<number>(0);
    const {basic: basicSide} = useContext(SizeContext);

    function changeSlide(type: "<" | ">") {
        switch (type) {
            case "<":
                if (index > 0) setIndex(index - 1);
                break;
            case ">":
                if (index < props.children.length - 1) setIndex(index + 1);
                break;
        }
    }

    return (
        <GameLayout.ColExpanded $alignSelf={"center"} $justifyContent="flex-start" $alignItems="center">
            <GameDiv>
                <GameLayout.Row $alignItems="center" $justifyContent="center">
                    <GameIcon
                        $alignSelf="center"
                        src={leftIcon}
                        size={basicSide.size10}
                        onClick={() => changeSlide("<")}
                        opacity={index === 0 ? 0.5 : 1}
                    />
                    <GameText size={basicSide.size10}>{index + 1 + "/" + props.children.length}</GameText>
                    <GameIcon
                        $alignSelf="center"
                        src={rightIcon}
                        size={basicSide.size10}
                        onClick={() => changeSlide(">")}
                        opacity={index === props.children.length - 1 ? 0.5 : 1}
                    />
                </GameLayout.Row>
            </GameDiv>
            <GameLayout.Row $justifyContent={"center"} $alignItems="center" $alignContent={"center"}>
                {props.children.map((el, i) => {
                    return (
                        <GameDiv key={"slide" + i} display={i === index ? "block" : "none"}>
                            {el}
                        </GameDiv>
                    );
                })}
            </GameLayout.Row>
        </GameLayout.ColExpanded>
    );
}

export default GameSlider;
