SkillButton.propTypes = {
    skill: PropTypes.oneOf<EventNames>(EVENTS),
    disabled: PropTypes.bool,
    onClick: PropTypes.func
};

function SkillButton(props) {
    const {basic: basicSize} = useContext(SizeContext);
    let size = basicSize.iconButtonSize;

    function onclick() {
        if (props.onClick) {
            props.onClick();
        }
    }

    return (
        <GameIconButton disabled={props.disabled} src={SkillImage[props.skill]} size={size} onClick={onclick}>
            {SKILL_NAME[props.skill]}
        </GameIconButton>
    );
}

export default SkillButton;
