import React, {useContext, useEffect, useRef, useState} from "react";
import PropTypes from "prop-types";
import {arraySchema2Array} from "../../../../werewolf-consts/colyseus/Utils";
import {RoomContext} from "../../../../context/RoomContext";

import MessageItem from "./MessageItem";
import ChatForm from "./ChatForm";
import {Message, SESSION} from "../../../../werewolf-consts/colyseus/State";
import Grid from "../../../shared/styled/game-layout/Grid";
import ChatHeader from "./styled/ChatHeader";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";
import GameLayout from "../../../shared/game-advance-components/GameLayout";
import {SizeContext} from "../../../../context/SizeContext";
import {GameGuiContext} from "../../GameGuiManager";
import {COLOR} from "../../../shared/styled/ColorsPalette";
import {UserContext} from "../../../../context/UserContext";

function GameChat(props: any) {
    const scrollView = useRef<HTMLDivElement>(null);
    const userContext = useContext(UserContext);
    const roomContext = useContext(RoomContext);
    const {basic, window} = useContext(SizeContext);
    const [messagesState, setMessagesState] = useState<Message[]>([]);
    useEffect(() => {
        const state = roomContext.state;
        if (state.currentStage.messages != null) {
            setMessagesState(arraySchema2Array(state.currentStage.messages));
            state.currentStage.messages.onAdd = (item) => {
                setMessagesState((lastMessagesState) => {
                    return [...lastMessagesState, item];
                });
                if (scrollView.current !== null) {
                    scrollView.current.scrollIntoView({behavior: "smooth"});
                }
            };
        }
    }, [roomContext.state]);

    // state.currentStage.messages.onRemove = (item, key) => {
    //
    //     setMessagesState(lastMessagesState => {
    //         let index = lastMessagesState.findIndex(message => message.mid === item.mid);
    //         if (index !== -1) return [...lastMessagesState.slice(0, index), ...lastMessagesState.slice(index + 1)]
    //         return [...lastMessagesState];
    //     });
    // }
    let list = messagesState.map((message, index, messages) => (
        // <MessageItem key={message.mid} .../>
        <MessageItem
            key={message.from + message.createTime + index}
            message={message}
            $withName={index - 1 < 0 || messages[index - 1].from !== message.from}
            $withAvatar={index + 1 >= messages.length || messages[index + 1].from !== message.from}
            $isMe={message.from === userContext.playerId}
        />
    ));
    const gameGui = useContext(GameGuiContext);
    return (
        <GameLayout.Col
            $backgroundColor={
                window.isVertical || gameGui.session === SESSION.NIGHT ? COLOR.chatBackground : COLOR.blurDark50
            }
            round_right={!basic.mobileView ? "1rem" : undefined}
            round_top={basic.mobileView ? "1rem" : undefined}
            $zIndex={basic.mobileView ? 11 : undefined}
            position={basic.mobileView ? "absolute" : undefined}
            $H={props.height}
            $W={props.width}
            $scrollY
            $justifyContent={"space-around"}
            $alignSelf={"flex-end"}
        >
            <ChatHeader onClose={props.onClose} />
            {/*100 là độ cao của ChatForm*/}
            <GameDiv $scrollY height={props.height - 100} width={props.width}>
                <GameLayout.Col>
                    <>{list}</>
                    <GameDiv ref={scrollView} />
                </GameLayout.Col>
            </GameDiv>
            <Grid.FullHeightRow />
            <ChatForm />
        </GameLayout.Col>
    );
}

GameChat.propTypes = {
    height: PropTypes.number,
    width: PropTypes.number,
    onClose: PropTypes.func
};

export default GameChat;
