import React, {useContext, useState} from "react";
import {RoomContext} from "../../../../context/RoomContext";
import GameInput from "../../../shared/game-basic-components/GameInput";
import sendIcon from "../../../../assets/game-btn/send_icon.svg";
import {MessageTypes} from "../../../../werewolf-consts/colyseus/MessageTypes";
import GameIconButton from "../../../shared/game-basic-components/GameIconButton";
import GameLayout from "../../../shared/game-advance-components/GameLayout";
import {SizeContext} from "../../../../context/SizeContext";
import {GameGuiContext} from "../../GameGuiManager";
import {SESSION} from "../../../../werewolf-consts/colyseus/state/WereWolfLog";

function ChatForm() {
    const roomContext = useContext(RoomContext);
    const [message, setMessage] = useState("");
    const {basic: baseSize} = useContext(SizeContext);
    const gui = useContext(GameGuiContext);
    const onSendMessage = () => {
        if (message.trim() !== "") roomContext.room.send(MessageTypes.CHAT, message);
        setMessage("");
    };
    const handleKeyDown = (event) => {
        if (event.key === "Enter") {
            onSendMessage();
        }
    };
    return (
        <GameLayout.Row $alignItems={"center"} $justifyContent="space-between" ml={".5rem"} mb={".5rem"}>
            <GameInput
                $backgroundColor={gui.session === SESSION.DAY ? "white" : undefined}
                color={gui.session === SESSION.DAY ? "black" : "white"}
                $alignSelf={"center"}
                placeholder={"Nhập tin nhắn..."}
                h={baseSize.size8}
                value={message}
                onChange={(event) => setMessage(event.target.value)}
                onKeyDown={handleKeyDown}
            />
            <GameIconButton src={sendIcon} onClick={onSendMessage} size={baseSize.size10} />
        </GameLayout.Row>
    );
}

export default ChatForm;
