export enum GameSettingEnum {
    numOfPlayer = "numOfPlayer",
    goodForVillager = "goodForVillager",
    goodForWerewolf = "goodForWerewolf",
    goodForBoth = "goodForBoth",
    noSetupFound = "noSetupFound",
    setupNotBeConfigured = "setupNotBeConfigured",
    currentSetup = "currentSetup",
    availableSetup = "availableSetup",
    createNew = "createNew",
    active = "active",
    editSetup = "editSetup"
}

export const getBalanceEnum = (point: number) => {
    if (point < 1) return GameSettingEnum.goodForWerewolf;
    if (point > 1) return GameSettingEnum.goodForVillager;
    return GameSettingEnum.goodForBoth;
};
