import React, {useContext} from "react";
import {SizeContext} from "../../../../context/SizeContext";

interface IElementSize {
    dialogW: number;
    dialogH: number;
    dialogBodyW: number;
    dialogBodyH: number;
    skillSize: number;
    targetSize: number;
    skillMargin: number;
    fontSize: number;
}
interface IDialogElementSizeProps {
    targetCount: number;
}

function useDialogElementSize(props: IDialogElementSizeProps) {
    const {window: S, basic} = useContext(SizeContext);
    let dH = basic.dialogH;
    let dW = basic.dialogW;
    let elementSize: IElementSize = {
        dialogH: dH,
        dialogW: dW,
        dialogBodyH: dH * (389 / 499),
        dialogBodyW: dW * (389 / 499),
        fontSize: basic.size4,
        skillSize: dW / 8,
        skillMargin: (dW * 11) / 200,
        targetSize: Math.max(dW / 4 - (props.targetCount * dW) / 30, 30)
    };
    return elementSize;
}

export default useDialogElementSize;
