import React, {useContext} from "react";
import useClock from "./Clock";
import {COLOR} from "../../shared/styled/ColorsPalette";
import AvatarView from "../../shared/game-basic-components/AvatarView";
import botAvatar from "../../../assets/game-btn/botAvatar.png";
import {StateContext} from "../../../context/StateContext";
import {GameDiv} from "../../shared/game-basic-components/GameDiv";
import {GameText} from "../../shared/game-basic-components/GameText";
import GameLayout from "../../shared/game-advance-components/GameLayout";
import {GameRouterContext} from "../../shared/game-advance-components/GameRouter";
import {SizeContext} from "../../../context/SizeContext";
import {useTranslation} from "react-i18next";
import {I18Namspace} from "../../../language/i18n";
import {UserContext} from "../../../context/UserContext";

function GameHeader(props) {
    let state = useContext(StateContext);
    let closeTime = useClock(state.currentStage.closeTime);
    const {t} = useTranslation(I18Namspace.StageDescription);
    const user = useContext(UserContext);
    let stageDescription = t(`${state.currentStage.stageName}`, {name: user.playerName});
    const {basic: baseSize} = useContext(SizeContext);
    let size = Math.max(50, baseSize.gameW / 15);
    const router = useContext(GameRouterContext);

    function showRoomSetting() {
        if (router?.routeTo) {
            router.routeTo("GameInfoView", []);
        }
    }

    return (
        <GameLayout.Row px={"1rem"} pt={"1rem"} position={"relative"}>
            <AvatarView cursor={"pointer"} src={botAvatar} size={size} onClick={showRoomSetting} />
            <GameLayout.RowExpanded>
                <GameDiv $alignSelf={"flex-start"} mt={"2%"} px={"2%"}>
                    <GameText
                        size={baseSize.size5}
                        $backgroundColor={COLOR.blue}
                        round={"1rem"}
                        p={".5rem"}
                        $fontStyle={"Altus"}
                    >
                        {stageDescription} {closeTime}
                    </GameText>
                </GameDiv>
            </GameLayout.RowExpanded>
        </GameLayout.Row>
    );
}

export default GameHeader;
