import React, {Dispatch, useContext} from "react";
import {IGameSettingReducerAction} from "./functions/gameSettingReducer";
import Grid from "../../../shared/styled/game-layout/Grid";
import RolePickerTable from "./RolePickerTable";
import GameSettingView from "./GameSettingView";
import {MapSchema} from "@colyseus/schema";
import XButton from "../../../shared/game-basic-components/XButton";
import {COLOR} from "../../../shared/styled/ColorsPalette";
import {GameText} from "../../../shared/game-basic-components/GameText";
import {useTranslation} from "react-i18next";
import {I18Namspace} from "../../../../language/i18n";
import {GameSettingEnum} from "../../../../language/Vietnamese/GameSetting_vi";
import GameLayout from "../../../shared/game-advance-components/GameLayout";
import {SizeContext} from "../../../../context/SizeContext";

interface IProps {
    setup: MapSchema<number>;
    updateSetup: Dispatch<IGameSettingReducerAction>;
    onActive: (setup: MapSchema<number>) => void;
    onSave: (setup: MapSchema<number>) => void;
    onClose: () => void;
}

export function GameSetupEditor(props: IProps) {
    const {basic: baseSize} = useContext(SizeContext);
    const {t} = useTranslation(I18Namspace.GameSetting);
    return (
        <GameLayout.ColExpanded
            $alignItems={"space-around"}
            $backgroundColor={COLOR.chatBackground}
            round="1rem"
            m={".5rem"}
        >
            <GameLayout.Row px={".3rem"} pt={".3rem"}>
                <Grid.FullWidthCol>
                    <GameText size={baseSize.size5} $fontStyle={"Altus"}>
                        {t(`${GameSettingEnum.editSetup}`)}
                    </GameText>
                </Grid.FullWidthCol>
                <XButton size={baseSize.size5} onClick={props.onClose} />
            </GameLayout.Row>
            <GameSettingView
                setup={props.setup}
                activated={false}
                onRoleClick={(role) => props.updateSetup({type: "-", role: role})}
                onCheckActive={(setup) => props.onActive(setup)}
                onSave={(setup) => props.onSave(setup)}
            />
            <GameLayout.RowExpanded />
            <RolePickerTable setup={props.setup} onRoleClick={(role) => props.updateSetup({type: "+", role: role})} />
        </GameLayout.ColExpanded>
    );
}
