import React, {useCallback, useContext, useEffect, useMemo, useState} from "react";
import {State} from "../../werewolf-consts/colyseus/State";
import {Room} from "colyseus.js";
import {UserContext} from "../../context/UserContext";
import StageRouter from "./StageRouter";
import {IRoomContext, RoomContext} from "../../context/RoomContext";
import {StateContext} from "../../context/StateContext";
import {IState, stateSchema2IState} from "../../colyseus/IState";
import GameNotifier from "./notifications/GameNotifier";
import GameInfoView from "./game-info/GameInfoView";
import ChatButton from "./game-body/chat/ChatButton";
import RoomNoticeListener from "./RoomNoticeListener";
import GameLayout from "../shared/game-advance-components/GameLayout";
import GameRouter, {GameRouterContext} from "../shared/game-advance-components/GameRouter";
import GameGuiManager from "./GameGuiManager";
import {RoomErrors} from "../../werewolf-consts/colyseus/RoomErrors";
import {SizeContext} from "../../context/SizeContext";
import {useTranslation} from "react-i18next";
import {I18Namspace} from "../../language/i18n";
import GameRoomAuthScreen from "./GameRoomAuthScreen";
import GameLoadingSpinner from "../shared/game-advance-components/GameLoadingSpinner";
import {DEV} from "../../env.const";
import {CLIENT} from "../../context";
import {GameDiv} from "../shared/game-basic-components/GameDiv";

function GameRoomRouter(props) {
    const [roomState, setRoomState] = useState<IState>();
    const [room, setRoom] = useState<Room<State> | null>(null);
    const [state, setState] = useState<State>(useCallback(() => new State(), []));
    let user = useContext(UserContext);
    let router = useContext(GameRouterContext);
    let [showAuth, setShowAuth] = useState<boolean>(false);
    const [disconnected, setDisconnected] = useState(false);
    const roomID = router?.params ? router.params[0] : user.playerId;
    const pass = router?.params ? router.params[1] : undefined;
    const roomName = router?.params ? router.params[2] : user.playerId;
    const {t} = useTranslation(I18Namspace.GameInfo);
    const {basic, window} = useContext(SizeContext);

    const connectRoom = useCallback(function (r: Room<State>) {
        r.onStateChange((state) => {
            setRoomState(stateSchema2IState(state));
            setState(state);
            if (DEV) console.log(stateSchema2IState(state));
        });
        r.onLeave(async (code) => {
            console.log("client lost connection with server: ", code);
            setRoom(null);
            setDisconnected(true);
        });
        setRoom(r);
        setDisconnected(false);
    }, []);

    const joinRoom = useCallback(
        async function (pass?: string) {
            if (DEV) console.log(`login làng ${roomID} pass:${pass}`);
            const r = await CLIENT.joinOrCreate<State>("my_room", {...user, villageId: roomID, pass: pass});
            connectRoom(r);
            setTimeout(() => {
                if (user.playerId === "50") {
                    r.leave(false);
                }
            }, 5000);
            setShowAuth(false);
            return r;
        },
        [connectRoom, roomID, user]
    );

    const reconnectRoom = useCallback(
        function () {
            if (room != null) return;
            console.log("reconnecting...");
            setDisconnected(false);
            joinRoom(pass).catch((e) => {
                console.error(e);
                setDisconnected(true);
            });
        },
        [joinRoom, pass, room]
    );

    useEffect(() => {
        console.log("useEffect: joinRoom");
        const r = joinRoom(pass).catch((e) => {
            if (DEV) console.log(e);
            switch (e.message) {
                case RoomErrors.WRONG_ROOM_PASSWORD:
                    if (DEV) console.log("Mật khẩu sai");
                    setShowAuth(true);
                    break;
                default:
                    if (router?.routeTo) router.routeTo("lobby", []);
                    break;
            }
        });
        return function cleanup() {
            r.then((r) => {
                if (r) r.removeAllListeners();
            });
        };
    }, [joinRoom, pass, router]);

    const roomContext = useMemo(
        () => ({
            room,
            state
        }),
        [room, state]
    );

    return (
        <GameGuiManager>
            <GameNotifier>
                <GameLayout.ColExpanded $justifyContent={"center"}>
                    {showAuth ? (
                        <GameRoomAuthScreen
                            roomName={roomName}
                            onSubmitPass={(pass) => {
                                joinRoom(pass);
                            }}
                        />
                    ) : roomContext.room != null ? (
                        <RoomContext.Provider value={roomContext as IRoomContext}>
                            {roomState && (
                                <StateContext.Provider value={roomState}>
                                    <RoomNoticeListener />
                                    <GameLayout.RowExpanded reverse={true}>
                                        <GameRouter paths={["StageRouter", "GameInfoView"]}>
                                            <StageRouter />
                                            <GameInfoView />
                                        </GameRouter>
                                        <ChatButton initShow={true} />
                                    </GameLayout.RowExpanded>
                                </StateContext.Provider>
                            )}
                        </RoomContext.Provider>
                    ) : disconnected ? (
                        <GameDiv $alignItems="center" $flexDirection={"column"} display={"flex"}>
                            <h1>Bạn đã bị mất kết nối!</h1>
                            <button onClick={() => reconnectRoom()}>Kết nối lại</button>
                        </GameDiv>
                    ) : (
                        <GameLoadingSpinner show={true} position={"absolute"} size={basic.playerAvatarSize * 2} />
                    )}
                </GameLayout.ColExpanded>
            </GameNotifier>
        </GameGuiManager>
    );
}

export default GameRoomRouter;
