import React from "react";
import {getWindowDimensions, ISize} from "../utils/custom-hooks/useWindowSize";
import {defaultSize, IBasicSize} from "../utils/custom-hooks/useBasicSize";

interface ISizeContext {
    basic: IBasicSize;
    window: ISize;
}

export const SizeContext = React.createContext<ISizeContext>({
    basic: defaultSize,
    window: getWindowDimensions()
});
