import React, {useContext} from "react";
import PropTypes from "prop-types";
import AvatarView from "../../../shared/game-basic-components/AvatarView";
import {COLOR} from "../../../shared/styled/ColorsPalette";
import StackLayout, {POSITION} from "../../../shared/styled/game-layout/StackLayout";
import {StateContext} from "../../../../context/StateContext";
import {GameIcon} from "../../../shared/game-basic-components/GameIcon";
import heartIcon from "../../../../assets/role-symbols/heart.png";
import ripIcon from "../../../../assets/role-symbols/rip.png";
import {EventNames, StageNames} from "../../../../werewolf-consts/enums";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";
import {countRoleEvent, isPlayerExist} from "../../../../werewolf-consts/colyseus/stateUtils";
import {RoomContext} from "../../../../context/RoomContext";
import {GameText} from "../../../shared/game-basic-components/GameText";
import GameLayout from "../../../shared/game-advance-components/GameLayout";

TargetItem.propTypes = {
    size: PropTypes.number,
    targetId: PropTypes.string,
    scaleSize: PropTypes.number,
    backImage: PropTypes.string,
    showBack: PropTypes.bool,
    showEffect: PropTypes.bool
};

function TargetItem(props) {
    let state = useContext(StateContext);
    let roomState = useContext(RoomContext).room.state;
    let player = roomState.players[props.targetId];
    let showHeart =
        state.currentStage.stageName === StageNames.END_GAME &&
        countRoleEvent(roomState, props.targetId, EventNames.BE_PAIRED_WITH) === 1;
    let showRip = !showHeart && isPlayerExist(roomState, props.targetId, false);
    return (
        <GameDiv
            style={{
                display: "block",
                position: "relative",
                userSelect: "none",
                pointerEvents: "none"
            }}
        >
            {player && (
                <StackLayout.Container w={props.size} h={props.size}>
                    <StackLayout.Layer pos={POSITION.CENTER} index={0} w={props.size} h={props.size}>
                        <AvatarView
                            src={player.avatar}
                            alt={player.name}
                            size={props.size}
                            border_color={player?.alive ? COLOR.aliveColor : COLOR.deadColor}
                        />
                    </StackLayout.Layer>
                    {showHeart && (
                        <StackLayout.Layer index={1} pos={POSITION.CENTER} w={props.size / 1.5} h={props.size / 1.5}>
                            <GameIcon
                                src={heartIcon}
                                size={props.size / 1.5}
                                style={{
                                    position: "absolute",
                                    top: `${props.size / 2.9}px`,
                                    left: `${props.size / 3.4}px`
                                }}
                            />
                        </StackLayout.Layer>
                    )}
                    {showRip && (
                        <StackLayout.Layer index={1} pos={POSITION.CENTER} w={props.size / 1.5} h={props.size / 1.5}>
                            <GameIcon
                                src={ripIcon}
                                size={props.size / 1.5}
                                style={{
                                    position: "absolute",
                                    top: `${props.size / 5.7}px`,
                                    left: `${props.size / 3}px`
                                }}
                            />
                        </StackLayout.Layer>
                    )}
                    {player.seat != null && (
                        <StackLayout.Layer
                            pos={POSITION.BOTTOM_RIGHT}
                            index={2}
                            w={props.size / 2.5}
                            h={props.size / 2.5}
                        >
                            <GameLayout.Row
                                color={showRip ? "black" : "white"}
                                $backgroundColor={showHeart || showRip ? "transparent" : COLOR.aliveColor}
                                round={"50%"}
                                size={props.size / 2.5}
                                $justifyContent={"center"}
                                $alignItems={"center"}
                            >
                                <GameText size={props.size / 3.5} color={showRip ? "black" : ""}>
                                    {player.seat}
                                </GameText>
                            </GameLayout.Row>
                        </StackLayout.Layer>
                    )}
                </StackLayout.Container>
            )}
        </GameDiv>
    );
}

export default TargetItem;
