import React, {useContext} from "react";
import PropTypes from "prop-types";
import {AVAILABLE_ROLES, Roles} from "../../../../werewolf-consts/enums/Roles";
import Grid from "../../../shared/styled/game-layout/Grid";
import {RoleCardImage} from "../../../shared/styled/RoleCard";
import {ROLE_AVATAR} from "../../startgame-screen/GameCardShower";
import {GameText} from "../../../shared/game-basic-components/GameText";
import {RoleGuideEnum} from "../../../../language/Vietnamese/RoleGuide_vi";
import {useTranslation} from "react-i18next";
import {COLOR} from "../../../shared/styled/ColorsPalette";
import StackLayout from "../../../shared/styled/game-layout/StackLayout";
import {PARTY, ROLE_PARTY} from "../../../../werewolf-consts/enums/RoleParties";
import {ROLE_MAX, ROLE_POINT} from "../../../../werewolf-consts/definitions/RoleConsts";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";
import GameLayout from "../../../shared/game-advance-components/GameLayout";
import {SizeContext} from "../../../../context/SizeContext";

RoleItem.propTypes = {
    role: PropTypes.oneOf(AVAILABLE_ROLES).isRequired,
    onClick: PropTypes.func
};
export const PartyColor = (role: Roles) => {
    if (ROLE_PARTY[PARTY.BETRAYER].includes(role)) return COLOR[PARTY.BETRAYER];
    if (ROLE_PARTY[PARTY.VILLAGER].includes(role)) return COLOR[PARTY.VILLAGER];
    if (ROLE_PARTY[PARTY.WEREWOLF].includes(role)) return COLOR[PARTY.WEREWOLF];
};

function RoleItem(props) {
    const {t} = useTranslation("RoleGuide");
    const {basic: basicSize} = useContext(SizeContext);
    let CardH = basicSize.iconButtonSize * 1.6;
    let CardW = CardH * (3 / 3.5);

    function onClick() {
        if (props.onClick) props.onClick();
    }

    function showRoleInfo() {}

    const Item = (
        <GameDiv onClick={onClick} cursor={"pointer"}>
            <GameLayout.Row
                height={CardH}
                width={CardW * 2}
                $justifyContent="flex-start"
                $alignItems="center"
                round=".8rem"
                $backgroundColor={PartyColor(props.role)}
            >
                <RoleCardImage round_left={".8rem"} src={ROLE_AVATAR[props.role]} size={CardH} />
                <GameLayout.ColExpanded $alignItems="center" $justifyContent="space-around">
                    <GameText size={basicSize.size3}>{t(`${RoleGuideEnum.name}.${props.role}`).toUpperCase()}</GameText>
                    <GameText size={basicSize.size5}>{("+" + ROLE_POINT[props.role]).slice(-2)}</GameText>
                </GameLayout.ColExpanded>
            </GameLayout.Row>
        </GameDiv>
    );
    return (
        <GameLayout.Col mx={CardW / 10} mb={CardW / 5}>
            <StackLayout.Container w={CardW * 2} h={CardH}>
                <StackLayout.Layer index={0} w={CardW * 2} h={CardH}>
                    {Item}
                </StackLayout.Layer>
                <StackLayout.Layer index={1} w={CardW * 2} h={CardH}>
                    {ROLE_MAX[props.role] === 0 && (
                        <Grid.Col
                            width={CardW * 2}
                            height={CardH}
                            style={{borderRadius: ".8rem", justifyContent: "center"}}
                            $style={{backgroundColor: COLOR.blurDark80}}
                        >
                            <GameText size={basicSize.size4} color={COLOR.bloodColor}>
                                {t(RoleGuideEnum.unReleased)}
                            </GameText>
                        </Grid.Col>
                    )}
                </StackLayout.Layer>
            </StackLayout.Container>
        </GameLayout.Col>
    );
}

export default RoleItem;
