import React, {useContext} from "react";
import gridIcon from "../../../../assets/game-btn/gridView.png";
import circleIcon from "../../../../assets/game-btn/circleView.png";
import historyIcon from "../../../../assets/game-btn/history.svg";
import GameIconButton from "../../../shared/game-basic-components/GameIconButton";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";
import {StateContext} from "../../../../context/StateContext";
import {StageNames} from "../../../../werewolf-consts/enums";
import {SizeContext} from "../../../../context/SizeContext";
import {PICKER_VIEW} from "../../../../context/TargetContext";

interface IProps {
    view: PICKER_VIEW;
    onChangeView: (view: PICKER_VIEW) => void;
}

const ViewIcon = {
    [PICKER_VIEW.GRID]: gridIcon,
    [PICKER_VIEW.CIRCLE]: circleIcon,
    [PICKER_VIEW.HISTORY]: historyIcon
};
const NextView: {[view in PICKER_VIEW]: PICKER_VIEW} = {
    [PICKER_VIEW.GRID]: PICKER_VIEW.CIRCLE,
    [PICKER_VIEW.CIRCLE]: PICKER_VIEW.HISTORY,
    [PICKER_VIEW.HISTORY]: PICKER_VIEW.GRID
};

function ChangeViewBtn(props: IProps) {
    const {basic: baseSize} = useContext(SizeContext);
    let size = baseSize.iconButtonSize;
    let state = useContext(StateContext);

    function changeLayout() {
        let newView: PICKER_VIEW = NextView[props.view];
        if (state.currentStage.stageName !== StageNames.END_GAME || !baseSize.mobileView) {
            while (newView === PICKER_VIEW.HISTORY) newView = NextView[newView];
        }
        if (state.currentStage.stageName === StageNames.END_GAME && baseSize.mobileView) {
            while (newView === PICKER_VIEW.GRID) newView = NextView[newView];
        }
        if (props.onChangeView) props.onChangeView(newView);
    }

    return (
        <GameDiv p={"1rem"}>
            <GameIconButton src={ViewIcon[props.view]} size={size} onClick={changeLayout} />
        </GameDiv>
    );
}

export default ChangeViewBtn;
