import React, {useContext, useEffect, useRef, useState} from "react";
import GameHeader from "./game-header/GameHeader";
import {StageNames} from "../../werewolf-consts/enums";
import StartGameScreen from "./startgame-screen/StartGameScreen";
import DefaultNightScreen from "./default-night-screen/DefaultNightScreen";
import {RoomContext, SizeContext, StateContext, UserContext} from "../../context";
import TargetPicker from "./game-body/target-picker/TargetPicker";
import ActionForm from "./game-body/action-form/actionForm";
import {DEFAULT_NIGHT, Stages_For_All_Players, Stages_Not_Show_Action_Form} from "./StageSettings";
import {POSITION} from "../shared/styled/game-layout/StackLayout";
import GameDialogManager from "./notifications/GameDialogManager";
import EndGame from "./game-history/EndGame";
import GameLayout from "../shared/game-advance-components/GameLayout";
import {SESSION, StagesInDay} from "../../werewolf-consts/colyseus/state/WereWolfLog";
import {AbsolutePosition} from "../shared/styled/game-layout/AbsolutePosition";
import {Random} from "../../werewolf-consts/utils/Random";
import sun1 from "../../assets/game-btn/SessionObject_sun1.png";
import sun2 from "../../assets/game-btn/SessionObject_sun2.png";
import moon1 from "../../assets/game-btn/SessionObject_moon1.png";
import moon2 from "../../assets/game-btn/SessionObject_moon2.png";
import cloud7 from "../../assets/game-btn/SessionObject_cloud7.png";
import {GameGuiContext} from "./GameGuiManager";
import {GameImage} from "../shared/game-basic-components/GameImage";
import GameSettingView from "./game-info/game-setting/GameSettingView";

const SessionObj = {
    [SESSION.NIGHT]: [moon1, moon2, cloud7],
    [SESSION.DAY]: [sun1, sun2, cloud7]
};

function StageRouter() {
    const userContext = useContext(UserContext);
    let roomContext = useContext(RoomContext);
    let state = useContext(StateContext);
    const {basic: baseSize} = useContext(SizeContext);
    const header = useRef<HTMLDivElement>(null);
    const [gameBodyH, setGameBodyH] = useState<number>(0);
    const gameGui = useContext(GameGuiContext);
    const [dayIcon, setDayIcon] = useState<string>(sun1);
    const [nightIcon, setNightIcon] = useState<string>(moon1);
    const clientHeight = header.current?.clientHeight || 100;
    useEffect(() => {
        setGameBodyH(baseSize.gameH - clientHeight);
    }, [baseSize.gameH, clientHeight]);
    useEffect(() => {
        if (gameGui.setTime) {
            let dayNo = state.dayNo;
            let session = StagesInDay.includes(state.currentStage.stageName) ? SESSION.DAY : SESSION.NIGHT;
            gameGui.setTime(session, dayNo);
        }
    }, [gameGui, state.currentStage.stageName, state.dayNo]);
    useEffect(() => {
        setDayIcon(SessionObj[SESSION.DAY][Random.random(0, 3)]);
        setNightIcon(SessionObj[SESSION.NIGHT][Random.random(0, 3)]);
    }, [roomContext.state.dayNo]);
    let historyW = baseSize.mobileView ? baseSize.gameW : baseSize.gameW * 0.6;
    let historyH = baseSize.mobileView
        ? gameBodyH - baseSize.playerAvatarSize
        : baseSize.gameH - 7.5 * baseSize.playerAvatarSize;
    let stageName: string =
        state.currentStage.activePlayers.includes(userContext.playerId) ||
        Stages_For_All_Players.includes(state.currentStage.stageName)
            ? state.currentStage.stageName
            : DEFAULT_NIGHT;

    function getSessionObj() {
        if (StagesInDay.includes(state.currentStage.stageName)) return dayIcon;
        else return nightIcon;
    }

    return (
        <GameLayout.ColExpanded position={"relative"}>
            <AbsolutePosition pos={POSITION.TOP_RIGHT} w={baseSize.gameW} h={baseSize.gameH}>
                <GameImage src={getSessionObj()} height={baseSize.playerAvatarSize * 2.5} />
            </AbsolutePosition>
            <div ref={header}>
                <GameHeader />
            </div>
            <>
                {stageName && (
                    <TargetPicker>
                        <GameLayout.ColExpanded $justifyContent={"center"} $alignItems={"center"} position={"relative"}>
                            <GameLayout.RowExpanded />
                            {/*<GameLayout.Positioned pos={POSITION.CENTER}>*/}
                            {/*    <>{!Stages_For_All_Players.includes(stageName) && <FireObject />}</>*/}
                            {/*</GameLayout.Positioned>*/}
                            <>{stageName === StageNames.START_GAME && <StartGameScreen />}</>
                            <>{stageName === DEFAULT_NIGHT && <DefaultNightScreen />}</>
                            <>
                                {stageName === StageNames.WAITING_STAGE && (
                                    <GameSettingView activated={true} setup={state.setup} />
                                )}
                            </>
                            {/*<>{stageName === StageNames.END_GAME && }</>*/}
                            <AbsolutePosition pos={POSITION.CENTER}>
                                <GameDialogManager />
                            </AbsolutePosition>
                            <>{stageName === StageNames.END_GAME && <EndGame h={historyH} w={historyW} />}</>
                            <>{!Stages_Not_Show_Action_Form.includes(stageName) && <ActionForm />}</>
                        </GameLayout.ColExpanded>
                    </TargetPicker>
                )}
            </>
        </GameLayout.ColExpanded>
    );
}

export default StageRouter;
