import {POSITION} from "./game-layout/StackLayout";

function getDistance(x, y, left, right, top, bottom) {
    let [dx, dy, dl, dr, dt, db] = [x, y, left, right, top, bottom];
    if (dx) dl = dr = dx;
    if (dy) dt = db = dy;
    const Top = dt ? (typeof dt == "number" ? `${dt}px` : dt) : "0";
    const Bottom = db ? (typeof db == "number" ? `${db}px` : db) : "0";
    const Left = dl ? (typeof dl == "number" ? `${dl}px` : dl) : "0";
    const Right = dr ? (typeof dr == "number" ? `${dr}px` : dr) : "0";
    return {
        Top: Top,
        Bottom: Bottom,
        Left: Left,
        Right: Right
    };
}

export interface IMarginProps {
    mt?: number | string;
    ml?: number | string;
    mb?: number | string;
    mr?: number | string;
    mx?: number | string;
    my?: number | string;
    m?: number | string;
}

export function getMargin(props) {
    let {mx, my, ml, mr, mt, mb, m} = props;
    if (m) return {margin: typeof m === "number" ? `${m}px` : m};
    let {Top, Bottom, Left, Right} = getDistance(mx, my, ml, mr, mt, mb);
    return {
        margin: `${Top} ${Right} ${Bottom} ${Left}`
    };
}

export interface IPaddingProps {
    pt?: number | string;
    pl?: number | string;
    pb?: number | string;
    pr?: number | string;
    px?: number | string;
    py?: number | string;
    p?: number | string;
}

export function getPadding(props) {
    let {px, py, pl, pr, pt, pb, p} = props;
    if (p) return {padding: typeof p === "number" ? `${p}px` : p};
    let {Top, Bottom, Left, Right} = getDistance(px, py, pl, pr, pt, pb);
    return {
        padding: `${Top} ${Right} ${Bottom} ${Left}`
    };
}

export interface ISizeProps {
    width?: number;
    height?: number;
    size?: number;
    ratio?: number;
    w?: number;
    h?: number;
    $H?: number;
    $W?: number;
    $fullW?: boolean;
    $fullH?: boolean;
    $fSize?: number;
    $maxW?: number;
    $maxH?: number;
    $minW?: number;
    $minH?: number;
}

export function getSize(props) {
    let {width, w, $W, height, h, $H, size, ratio, $maxW, $maxH, $minW, $minH} = props;
    let widthC = width || w || $W;
    let heightC = height || h || $H;
    if (size) widthC = heightC = size;
    if (ratio)
        if (widthC) heightC = widthC / ratio;
        else widthC = heightC * ratio;
    return {
        width: props.$fullW ? "100%" : `${widthC}px`,
        height: props.$fullH ? "100%" : `${heightC}px`,
        maxWidth: `${$maxW}px`,
        maxHeight: `${$maxH}px`,
        minWidth: `${$minW}px`,
        minHeight: `${$minH}px`,
        fontSize: `${props.$fSize}px`
    };
}

export interface IPositionProps extends ISizeProps {
    pos?: POSITION;
}

export function getPosition(props) {
    let {width, w, W, height, h, H, size, ratio} = props;
    let widthC = width || w || W;
    let heightC = height || h || H;
    if (size) widthC = heightC = size;
    if (ratio)
        if (widthC) heightC = widthC / ratio;
        else widthC = heightC * ratio;
    let pos = props.pos ? props.pos : POSITION.CENTER;
    let layout = {top: "", left: "", right: "", bottom: "", marginLeft: "", marginTop: ""};
    switch (pos) {
        case POSITION.TOP:
            layout.top = "0";
            layout.left = "50%";
            layout.marginLeft = `-${widthC / 2}px`;
            break;
        case POSITION.TOP_LEFT:
            layout.top = "0";
            layout.left = "0";
            break;
        case POSITION.TOP_RIGHT:
            layout.top = "0";
            layout.right = "0";
            break;
        case POSITION.LEFT:
            layout.left = "0";
            layout.top = "50%";
            layout.marginTop = `-${heightC / 2}px`;
            break;
        case POSITION.CENTER:
            layout.left = "50%";
            layout.marginLeft = `-${widthC / 2}px`;
            layout.top = "50%";
            layout.marginTop = `-${heightC / 2}px`;
            break;
        case POSITION.RIGHT:
            layout.right = "0";
            layout.top = "50%";
            layout.marginTop = `-${heightC / 2}px`;
            break;
        case POSITION.BOTTOM_LEFT:
            layout.bottom = "0";
            layout.left = "0";
            break;
        case POSITION.BOTTOM:
            layout.bottom = "0";
            layout.left = "50%";
            layout.marginLeft = `-${widthC / 2}px`;
            break;
        case POSITION.BOTTOM_RIGHT:
            layout.bottom = "0";
            layout.right = "0";
            break;
    }
    return layout;
}

export interface IBorderProps {
    round?: string;
    round_left?: string;
    round_right?: string;
    round_bottom?: string;
    round_top?: string;
    $roundTopLeft?: string;
    $roundTopRight?: string;
    $roundBottomLeft?: string;
    $roundBottomRight?: string;
    border?: string;
}

export function getBorder(props) {
    let {
        round_left,
        round_right,
        round_bottom,
        round_top,
        $roundTopLeft,
        $roundTopRight,
        $roundBottomLeft,
        $roundBottomRight
    } = props;
    if (props.round) return {borderRadius: props.round, border: props.border};
    if (props.round_left) {
        $roundBottomLeft = $roundTopLeft = round_left;
    }
    if (props.round_right) {
        $roundBottomRight = $roundTopRight = round_right;
    }
    if (props.round_top) {
        $roundTopRight = $roundTopLeft = round_top;
    }
    if (props.round_bottom) {
        $roundBottomRight = $roundBottomLeft = round_bottom;
    }
    return {
        borderTopLeftRadius: $roundTopLeft,
        borderTopRightRadius: $roundTopRight,
        borderBottomLeftRadius: $roundBottomLeft,
        borderBottomRightRadius: $roundBottomRight,
        border: props.border
    };
}

export interface IFlexProps {
    $justifyContent?: "center" | "flex-start" | "flex-end" | "space-between" | "space-around";
    $alignItems?: "center" | "flex-start" | "flex-end" | "space-between" | "space-around" | "base-line";
    $alignContent?: "center" | "flex-start" | "flex-end" | "space-between" | "space-around";
    $alignSelf?: "center" | "flex-start" | "flex-end";
    $flexWrap?: "wrap" | "-moz-initial" | "inherit" | "initial" | "revert" | "unset" | "nowrap" | "wrap-reverse";
    $flexDirection?: "column" | "row" | "column-reverse" | "row-reverse";
    flex?: "content" | 1 | "auto";
}

export function getFlex(props) {
    return {
        justifyContent: props.$justifyContent,
        alignItems: props.$alignItems,
        alignContent: props.$alignContent,
        alignSelf: props.$alignSelf,
        flexWrap: props.$flexWrap,
        flexDirection: props.$flexDirection,
        flex: props.flex
    };
}

export interface IColorProps {
    $backgroundColor?: string;
    color?: string;
    opacity?: number;
}

export function getColor(props) {
    return {
        backgroundColor: props.$backgroundColor,
        color: props.color ? props.color : "white",
        opacity: props.opacity
    };
}

export interface ICursorProps {
    cursor?: string;
    $userSelect?: "none";
    $pointerEvents?: "none" | "auto";
}

export function getCursor(props) {
    let cursor: any = false;
    return {
        cursor: props.cursor,
        pointerEvents: props.$pointerEvents,
        userSelect: props.$userSelect
    };
}
