import {SkillNames} from "../werewolf-consts/enums/SkillNames";
import {StageNames} from "../werewolf-consts/enums/StageNames";
import {Roles} from "../werewolf-consts/enums/Roles";
import {
    Action,
    Message,
    Player,
    RoomSetting,
    Stage,
    State,
    WereWolfEvent,
    WereWolfLog,
    WerewolfRole
} from "../werewolf-consts/colyseus/State";
import {arraySchema2Array, mapSchema2Object} from "../werewolf-consts/colyseus/Utils";
import {MapSchema} from "@colyseus/schema";
import {EventNames} from "../werewolf-consts/enums/EventNames";
import {EventData} from "../werewolf-consts/definitions/EventData";

export interface IRoleData {
    skillWorking: boolean;
    lover: string;
    canCursed: boolean; // wolf+ only
    lastTarget: string; // for both seer/saver or hunter
    lastResult: boolean; // seer only
    canSave: boolean; // witch only
    canKill: boolean; // witch only
    mother: string; // wild child only
    lifeCount: number; // old man only
}

export interface IWerewolfRole {
    roleID: Roles;
    roleEvents: IWereWolfEvent[];
}

export interface IPlayer {
    seat: number;
    playerId: string; // facebook token or something else :))
    name: string;
    avatar: string;
    role?: IWerewolfRole;
    alive: boolean;
}

export interface IMessage {
    id: string;
    from: string;
    content: string;
    createTime: number;
}

export interface IAction {
    from: string;
    skill: SkillNames;
    targets: string[];
}

export interface IStage {
    stageName: StageNames;
    openTime: number;
    closeTime: number;
    activePlayers: string[];
    deadMans: string[];
    actions: IAction[];
    messages: IMessage[];
}

export interface IWereWolfEvent {
    eventName: EventNames;
    from: string;
    data: EventData;
}

export interface IWereWolfLog {
    session: number;
    dayNo: number;
    events: IWereWolfEvent[];
}

export interface IState {
    // State is not Stage <- remember this;
    roomSetting: RoomSetting;
    setup: MapSchema<number>;
    players: Map<string, IPlayer>;
    currentStage: IStage;
    dayNo: number;
    gameHistory: IWereWolfLog[];
    winTeam: number;
}

export function stateSchema2IState(state: State): IState {
    return {
        currentStage: stageSchema2IStage(state.currentStage),
        dayNo: 0,
        gameHistory: arraySchema2Array<WereWolfLog>(state.gameHistory).map((log) => {
            return {
                dayNo: log.dayNo,
                session: log.session,
                events: arraySchema2Array<WereWolfEvent>(log.events).map((event) => {
                    return werewolfEvent2IwerewolfEvent(event);
                })
            } as IWereWolfLog;
        }),
        players: mapPlayer2MapIPlayer(state.players),
        roomSetting: state.roomSetting,
        setup: state.setup,
        winTeam: state.winTeam
    };
}

function stageSchema2IStage(stage: Stage): IStage {
    return {
        actions: arraySchema2Array<Action>(stage.actions).map((a) => {
            return {
                from: a.from,
                skill: a.skill,
                targets: arraySchema2Array<string>(a.targets)
            } as IAction;
        }),
        activePlayers: arraySchema2Array<string>(stage.activePlayers),
        closeTime: stage.closeTime,
        deadMans: arraySchema2Array<string>(stage.deadMans),
        messages: arraySchema2Array<Message>(stage.messages).map((m) => {
            return {
                id: m.id,
                from: m.from,
                content: m.content,
                createTime: m.createTime
            } as IMessage;
        }),
        openTime: stage.openTime,
        stageName: stage.stageName
    };
}
function werewolfEvent2IwerewolfEvent(event: WereWolfEvent): IWereWolfEvent {
    return {
        from: event.from,
        eventName: event.eventName,
        data: mapSchema2Object<EventData>(event.data)
    } as IWereWolfEvent;
}

function werewolfRoleSchema2IWereWolfRole(role: WerewolfRole | undefined): IWerewolfRole | undefined {
    if (!role) return undefined;
    return {
        roleEvents: arraySchema2Array<WereWolfEvent>(role.roleEvents).map((event) => {
            return werewolfEvent2IwerewolfEvent(event);
        }),
        roleID: role.roleID
    };
}

function playerSchema2IPlayer(player: Player): IPlayer {
    return {
        alive: player.alive,
        avatar: player.avatar,
        name: player.name,
        playerId: player.playerId,
        role: werewolfRoleSchema2IWereWolfRole(player.role),
        seat: player.seat
    };
}

function mapPlayer2MapIPlayer(players: MapSchema<Player>): Map<string, IPlayer> {
    let map = new Map<string, IPlayer>();
    for (const playersKey in players) {
        map.set(playersKey, playerSchema2IPlayer(players[playersKey]));
    }
    return map;
}
