import React, {useContext} from "react";
import PropTypes from "prop-types";
import {StateContext} from "../../../context/StateContext";
import {GameHistoryEnum} from "../../../language/Vietnamese/GameHistory_vi";
import GameEvent from "./GameEvent";
import {useTranslation} from "react-i18next";
import {COLOR} from "../../shared/styled/ColorsPalette";
import GameResult from "./GameResult";
import {GameDiv} from "../../shared/game-basic-components/GameDiv";
import GameLayout from "../../shared/game-advance-components/GameLayout";
import {GameText} from "../../shared/game-basic-components/GameText";
import {SizeContext} from "../../../context/SizeContext";
import {RoomContext} from "../../../context/RoomContext";

GameHistory.propTypes = {
    w: PropTypes.number,
    h: PropTypes.number
};

function GameHistory(props) {
    let state = useContext(StateContext);
    let roomState = useContext(RoomContext).room.state;
    const {t} = useTranslation("GameHistory");
    const {basic: baseSize} = useContext(SizeContext);
    return (
        <GameLayout.ColExpanded $H={props.h} $W={props.w}>
            <GameResult />
            <GameText $fWeight={"bolder"} size={baseSize.size6} color={COLOR.gameHistoryColor}>
                {t(`${GameHistoryEnum.gameHistory}`)}
            </GameText>
            <GameDiv
                style={{borderBottom: `solid ${COLOR.gameHistoryColor} .1em`}}
                w={props.w / 3}
                $alignSelf={"center"}
            />
            <GameLayout.Col $scrollY $H={props.h * 0.75} $W={props.w}>
                {roomState.gameHistory.map((log, lIndex) => {
                    return (
                        <GameDiv
                            key={"log" + lIndex}
                            style={{borderBottom: `solid ${COLOR.gameHistoryColor} .1em`, margin: "0 1rem"}}
                        >
                            <GameText $fWeight={"bolder"} size={baseSize.size5} color={COLOR.gameHistoryColor}>
                                {t(`${GameHistoryEnum.gameHistoryHeader}.${log.session}`, {dayNo: log.dayNo})}:
                            </GameText>
                            <GameLayout.Col m={"0 5%"}>
                                {log.events.map((e, eIndex) => (
                                    <GameEvent key={"event" + lIndex + eIndex} event={e} />
                                ))}
                            </GameLayout.Col>
                        </GameDiv>
                    );
                })}
            </GameLayout.Col>
        </GameLayout.ColExpanded>
    );
}

export default GameHistory;
