import {styled} from "styletron-react";
import {getPosition, getSize, IPositionProps, ISizeProps} from "../BasicProps";
import {IBasicProps} from "../../game-basic-components/GameDiv";

const Container = styled<any, IBasicProps>("div", (props) => {
    return {
        position: "relative",
        ...getSize(props)
    };
});

interface LayerProps extends IPositionProps, ISizeProps {
    index?: number;
}

export enum POSITION {
    TOP,
    TOP_LEFT,
    TOP_RIGHT,
    LEFT,
    CENTER,
    RIGHT,
    BOTTOM,
    BOTTOM_LEFT,
    BOTTOM_RIGHT
}

export const LIST_POSITIONS = [
    POSITION.TOP,
    POSITION.TOP_LEFT,
    POSITION.TOP_RIGHT,
    POSITION.LEFT,
    POSITION.CENTER,
    POSITION.RIGHT,
    POSITION.BOTTOM,
    POSITION.BOTTOM_LEFT,
    POSITION.BOTTOM_RIGHT
];
export const REVERSE_POSITION: {[pos in POSITION]: POSITION} = {
    [POSITION.TOP]: POSITION.BOTTOM,
    [POSITION.BOTTOM]: POSITION.TOP,
    [POSITION.LEFT]: POSITION.RIGHT,
    [POSITION.RIGHT]: POSITION.LEFT,
    [POSITION.CENTER]: POSITION.CENTER,
    [POSITION.TOP_LEFT]: POSITION.BOTTOM_RIGHT,
    [POSITION.TOP_RIGHT]: POSITION.BOTTOM_LEFT,
    [POSITION.BOTTOM_LEFT]: POSITION.TOP_RIGHT,
    [POSITION.BOTTOM_RIGHT]: POSITION.TOP_LEFT
};

const Layer = styled<any, LayerProps>("div", (props) => {
    return {
        zIndex: props.index ? props.index : 1,
        position: "absolute",
        display: "flex",
        justifyContent: "center",
        userSelect: "none",
        ...getPosition(props)
    };
});

const StackLayout = {
    Container,
    Layer
};
export default StackLayout;
