export enum GAME_INFO_TAB {
    MY_INFO,
    GAME_SETTING,
    VILLAGE_INFO
}

export const GAME_INFO_TABS = Object.keys(GAME_INFO_TAB)
    .slice(0, Object.keys(GAME_INFO_TAB).length / 2)
    .map((t) => parseInt(t) as GAME_INFO_TAB);

