import React, {useContext} from "react";
import {StateContext} from "../../../context/StateContext";
import {useTranslation} from "react-i18next";
import {GameResultEnum} from "../../../language/Vietnamese/GameResult_vi";
import {COLOR} from "../../shared/styled/ColorsPalette";
import {GameDiv} from "../../shared/game-basic-components/GameDiv";
import {GameText} from "../../shared/game-basic-components/GameText";
import {SizeContext} from "../../../context/SizeContext";

function GameResult(props) {
    let state = useContext(StateContext);
    const {basic: basicSize} = useContext(SizeContext);
    let winTeam = state.winTeam;
    const {t} = useTranslation("GameResult");
    return (
        <GameDiv
            style={{
                borderBottom: `dashed ${COLOR.gameHistoryColor} .1rem`,
                margin: "0 1em"
            }}
        >
            <GameText color={COLOR.gameHistoryColor} size={basicSize.size8}>
                {t(`${winTeam}`)}
            </GameText>
            <GameText color={COLOR.gameHistoryColor} size={basicSize.size5}>
                {t(`${GameResultEnum.hadWin}`)}
            </GameText>
        </GameDiv>
    );
}

export default GameResult;
