import React, {useContext} from "react";

import {RoomContext} from "../../../../context/RoomContext";
import AvatarView from "../../../shared/game-basic-components/AvatarView";
import MessageBubble, {MessageTitle} from "./styled/MessageBubble";
import {Message, Player, SESSION} from "../../../../werewolf-consts/colyseus/State";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";
import {SizeContext} from "../../../../context/SizeContext";
import {GameGuiContext} from "../../GameGuiManager";

interface MessageItemProps {
    message: Message;
    $withAvatar: boolean; // isEndOfMessageGroup
    $withName: boolean; // isStartOfMessageGroup
    $isMe: boolean;
}

function MessageItem(props: MessageItemProps) {
    const roomContext = useContext(RoomContext);
    const player = roomContext.state.players[props.message.from] as Player;
    const gui = useContext(GameGuiContext);
    const {basic: baseSize} = useContext(SizeContext);
    return (
        <GameDiv
            style={{
                zIndex: "inherit",
                alignSelf: props.$isMe ? "flex-end" : "flex-start",
                marginBottom: props.$withAvatar ? "15px" : "2px",
                fontSize: "inherit"
            }}
        >
            <GameDiv style={{position: "relative", maxWidth: "70vh"}}>
                {!props.$isMe && props.$withAvatar && (
                    <AvatarView
                        $style={{position: "absolute", bottom: 0, left: 0}}
                        src={player?.avatar ?? "https://sites.google.com/site/masoibot/user/user.png?attredirects=0"}
                        alt={player?.name ?? "BOT"}
                        size={baseSize.size5}
                    />
                )}
                {!props.$isMe && props.$withName && <MessageTitle>{player?.name ?? ""}</MessageTitle>}
                <MessageBubble $isMe={props.$isMe} $lightMode={gui.session === SESSION.DAY ? 1 : 0}>
                    {props.message.content}
                </MessageBubble>
            </GameDiv>
        </GameDiv>
    );
}

export default MessageItem;
