import React, {useContext, useState} from "react";
import PropTypes from "prop-types";
import {SESSION} from "../../werewolf-consts/colyseus/state/WereWolfLog";
import lightBG_m from "../../assets/images/background_game-Light-m.jpg";
import lightBG_l from "../../assets/images/background_game-Light-l.jpg";
import darkBG_m from "../../assets/images/background_game-Dark-m.jpg";
import darkBG_l from "../../assets/images/background_game-Dark-l.jpg";
import {GameDiv} from "../shared/game-basic-components/GameDiv";
import {GameImage} from "../shared/game-basic-components/GameImage";
import {SizeContext} from "../../context/SizeContext";
import {COLOR} from "../shared/styled/ColorsPalette";

interface IGameGUIContext {
    session: SESSION;
    setTime: (s: SESSION, d: number) => void;
}

export const GameGuiContext = React.createContext<IGameGUIContext>({
    setTime: () => {},
    session: SESSION.DAY
});
GameGuiManager.propTypes = {
    children: PropTypes.element
};

function GameGuiManager(props) {
    const [dayNo, setDayNo] = useState<number>(1);
    const [session, setSession] = useState<SESSION>(SESSION.DAY);
    const [background, setBackground] = useState<string>(lightBG_m);
    const {window} = useContext(SizeContext);

    function setTime(s: SESSION, d: number) {
        setDayNo(d);
        setSession(s);
        let newBG;
        if (s === SESSION.DAY) newBG = window.isVertical ? lightBG_m : lightBG_l;
        else newBG = window.isVertical ? darkBG_m : darkBG_l;
        setBackground(newBG);
    }

    return (
        <GameGuiContext.Provider value={{setTime: setTime, session: session}}>
            <>
                <GameImage position={"absolute"} src={background} w={window.width} h={window.height} />
                <GameDiv
                    position={"absolute"}
                    w={window.width}
                    h={window.height}
                    $backgroundColor={session === SESSION.DAY ? COLOR.blurDark50 : COLOR.blurDark90}
                >
                    {props.children}
                </GameDiv>
            </>
        </GameGuiContext.Provider>
    );
}

export default GameGuiManager;
