import React, {useContext} from "react";
import {RoomContext} from "../../../../context/RoomContext";
import {IAction, MessageTypes} from "../../../../werewolf-consts/colyseus/MessageTypes";
import {TargetContext} from "../../../../context/TargetContext";
import Grid from "../../../shared/styled/game-layout/Grid";
import SkillButton from "./SkillButton";
import StageGuide from "./StageGuide";
import useSkills, {ISkillDetail} from "./hooks/useSkills";
import {useTranslation} from "react-i18next";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";
import GameLayout from "../../../shared/game-advance-components/GameLayout";
import {GameText} from "../../../shared/game-basic-components/GameText";
import {COLOR} from "../../../shared/styled/ColorsPalette";
import {SizeContext} from "../../../../context/SizeContext";
import {SkillNames} from "../../../../werewolf-consts/enums";
import {arraySchema2Array} from "../../../../werewolf-consts/colyseus/Utils";
import {Action} from "../../../../werewolf-consts/colyseus/state/Action";
import GameHoverPopup from "../../../shared/game-advance-components/GameHoverPopup";
import AttackersView from "../target-picker/AttackersView";
import {POSITION} from "../../../shared/styled/game-layout/StackLayout";
import {UserContext} from "../../../../context/UserContext";
import {RoomNotifierContext} from "../../notifications/context/RoomNotifierContext";

const SkillsShowTotal = [
    SkillNames.SKIP,
    SkillNames.READY,
    SkillNames.BITE,
    SkillNames.VOTE,
    SkillNames.VOTE_NO,
    SkillNames.VOTE_YES
];

function ActionForm() {
    let roomContext = useContext(RoomContext);
    const userContext = useContext(UserContext);
    let targetContext = useContext(TargetContext);
    let alert = useContext(RoomNotifierContext);
    const {t} = useTranslation("SkillDisabledReason");
    let skills: ISkillDetail[] = useSkills();

    function getPlayersUseSkill(skill: SkillNames) {
        let players: string[] = [];
        if (!Array.isArray(roomContext.state.currentStage?.actions))
            return {
                count: 0,
                ids: [],
                max: roomContext.state.currentStage.activePlayers?.length
            };
        arraySchema2Array<Action>(roomContext.state.currentStage.actions)
            .filter((a) => {
                return a.skill === skill;
            })
            .forEach((a) => {
                players.push(a.from);
            });
        return {
            count: players.length,
            ids: players,
            max: roomContext.state.currentStage.activePlayers.length
        };
    }

    const {basic} = useContext(SizeContext);

    function sendAction(skill: ISkillDetail) {
        //TODO kiểm tra target ok không
        if (skill.disabled) {
            if (alert.showNotice)
                alert.showNotice({
                    time: 5,
                    content: skill.reason
                });
            return;
        }
        roomContext.room.send(MessageTypes.ACTION, {
            skill: skill.skill,
            from: userContext.playerId,
            targets: targetContext.targets
        } as IAction);
    }

    let skillsButton = skills?.map((skillDetail, index) => {
        const {count, ids, max} = getPlayersUseSkill(skillDetail.skill);
        return (
            <GameLayout.Row key={`SkillBtn_${index}`} $alignItems={"flex-start"}>
                <SkillButton
                    skill={skillDetail.skill}
                    disabled={skillDetail.disabled}
                    onClick={() => {
                        sendAction(skillDetail);
                    }}
                />
                <>
                    {count > 0 && max > 1 && (
                        <GameHoverPopup popup={<AttackersView attackers={ids} />} pos={POSITION.TOP}>
                            <GameLayout.Row
                                round={"50%"}
                                p={"1rem"}
                                $backgroundColor={COLOR.blue}
                                size={basic.size5}
                                $alignItems={"center"}
                                $justifyContent={"center"}
                            >
                                <GameText size={basic.size3}>
                                    {count}
                                    {`${SkillsShowTotal.includes(skillDetail.skill) ? `/${max}` : ""}`}
                                </GameText>
                            </GameLayout.Row>
                        </GameHoverPopup>
                    )}
                </>
            </GameLayout.Row>
        );
    });
    return (
        <GameDiv $zIndex={1}>
            <StageGuide />
            <Grid.Row mt={".5rem"} $style={{justifyContent: "space-around", alignContent: "center"}}>
                {skillsButton}
            </Grid.Row>
        </GameDiv>
    );
}

export default ActionForm;
