import React, {Fragment, useContext, useState} from "react";
import PropTypes, {InferProps} from "prop-types";
import LayoutIcon from "../../../../assets/game-btn/chatBtn.png";
import GameChat from "./GameChat";
import GameIconButton from "../../../shared/game-basic-components/GameIconButton";
import {POSITION} from "../../../shared/styled/game-layout/StackLayout";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";
import {SizeContext} from "../../../../context/SizeContext";
import {AbsolutePosition} from "../../../shared/styled/game-layout/AbsolutePosition";

ChatButton.propTypes = {
    onShow: PropTypes.func,
    onHide: PropTypes.func,
    initShow: PropTypes.bool.isRequired,
    showActivePlayer: PropTypes.bool
};

function ChatButton(props: InferProps<typeof ChatButton.propTypes>) {
    const {basic: basicSize} = useContext(SizeContext);
    const [showChat, setShowChat] = useState<boolean>(props.initShow || false);

    function onclick() {
        if (showChat) {
            setShowChat(false);
            if (props.onHide) props.onHide();
        } else {
            setShowChat(true);
            if (props.onShow) props.onShow();
        }
    }

    return (
        <Fragment>
            {!showChat && (
                <AbsolutePosition pos={POSITION.BOTTOM_LEFT}>
                    <GameDiv p={"1rem"}>
                        <GameIconButton src={LayoutIcon} size={basicSize.iconButtonSize} onClick={onclick} />
                    </GameDiv>
                </AbsolutePosition>
            )}
            {showChat && <GameChat width={basicSize.chatW} height={basicSize.chatH} onClose={onclick} />}
        </Fragment>
    );
}

export default ChatButton;
