import {Village} from "./Village";
import {Collections} from "./CollectionNames";
import {DocumentData, FDUnionArray, FirestoreDocument, RefFDUnionArray} from "@phamngocduy98/flashstore-react";

export class User extends DocumentData {
    @RefFDUnionArray(Collections.VILLAGES)
    villages: FDUnionArray<FirestoreDocument<Village>>;

    @RefFDUnionArray(Collections.USERS)
    following: FDUnionArray<FirestoreDocument<User>>;

    @RefFDUnionArray(Collections.USERS)
    followers: FDUnionArray<FirestoreDocument<User>>;

    constructor(
        public name: string,
        public avatarUrl: string,
        public villageName: string = "",
        public villagePass: string = ""
    ) {
        super();
        this.villages = new FDUnionArray();
        this.following = new FDUnionArray();
        this.followers = new FDUnionArray();
    }
}
