import {styled} from "styletron-react";

interface MessageProps {
    $isMe: any;
    $lightMode: 0 | 1;
}

const paddingLeft = 40;

// [dark mode, light mode]
export const MessageViewStyle = {
    backgroundIsMe: ["#0099ff", "#0099ff"],
    background: ["#373737", "#f1f0f0"],
    textColorIsMe: ["#fff", "#fff"],
    textColor: ["#fff", "#000"]
};
const MessageBubble = styled<any, MessageProps>("div", (props) => ({
    // direction: props.$isActive ? "ltr" : "rtl",
    padding: "6px 12px 7px",
    color: props.$isMe
        ? MessageViewStyle.textColorIsMe[props.$lightMode]
        : MessageViewStyle.textColor[props.$lightMode],
    background: props.$isMe
        ? MessageViewStyle.backgroundIsMe[props.$lightMode]
        : MessageViewStyle.background[props.$lightMode],
    fontSize: "inherit",
    borderRadius: "1.3em",
    marginLeft: `${paddingLeft}px`,
    textAlign: "left",
    wordBreak: "break-word",
    zIndex: "inherit"
}));

export const MessageTitle = styled("h5", (props) => ({
    // fontSize: "12px",
    fontWeight: "normal",
    lineHeight: 1.28,
    marginBottom: "1px",
    overflow: "hidden",
    zIndex: "inherit",
    paddingLeft: `${paddingLeft + 12}px`,
    textOverflow: "ellipsis",
    whiteSpace: "nowrap",
    textAlign: "left"
}));

export default MessageBubble;
