import React, {useContext, useState} from "react";
import {GameDiv, IBasicProps} from "../../shared/game-basic-components/GameDiv";
import {Roles} from "../../../werewolf-consts/enums";
import {GameImage} from "../../shared/game-basic-components/GameImage";
import {ROLE_AVATAR} from "./GameCardShower";
import {GameText} from "../../shared/game-basic-components/GameText";
import {useTranslation} from "react-i18next";
import {I18Namspace} from "../../../language/i18n";
import {RoleGuideEnum} from "../../../language/Vietnamese/RoleGuide_vi";
import {PartyColor} from "../game-info/game-setting/RoleItem";
import {COLOR} from "../../shared/styled/ColorsPalette";
import {ROLE_POINT} from "../../../werewolf-consts/definitions/RoleConsts";
import {CardBack, CardFront, FlipCard} from "../../shared/styled/Card";
import GameLayout from "../../shared/game-advance-components/GameLayout";
import {SizeContext} from "../../../context/SizeContext";

RoleCard.propTypes = {};

interface RoleCardProps extends IBasicProps {
    role: Roles;
}

function RoleCard(props: RoleCardProps) {
    const {basic: S} = useContext(SizeContext);
    const {t} = useTranslation(I18Namspace.RoleGuide);
    const cardW = (props.size || S.gameH / 3) / 1.2;
    const [flip, setFlip] = useState<boolean>(false);
    let customProps = {...props};
    delete customProps["borderRadius"];
    delete customProps["backgroundColor"];
    delete customProps["ratio"];
    delete customProps["size"];
    delete customProps["p"];
    return (
        <GameDiv
            onClick={() => setFlip(!flip)}
            position={"relative"}
            $W={cardW * 1.2}
            ratio={3 / 4}
            round={".8rem"}
            {...customProps}
        >
            {/*<Card onClick={() => setFlip(!flip)}>*/}
            <FlipCard style={{transform: flip ? "rotateY(180deg)" : ""}}>
                <CardFront>
                    <GameLayout.ColExpanded
                        round={".8rem"}
                        $backgroundColor={PartyColor(props.role)}
                        $W={cardW * 1.2}
                        ratio={3 / 4}
                    >
                        <GameLayout.RowExpanded $alignItems={"flex-start"}>
                            <GameImage
                                $roundTopLeft={".8rem"}
                                src={ROLE_AVATAR[props.role]}
                                $W={cardW}
                                ratio={3 / 3.5}
                            />
                            <GameLayout.ColExpanded
                                $justifyContent={"space-between"}
                                $alignItems={"center"}
                                $backgroundColor={COLOR.blurDark50}
                                $roundTopRight={".8rem"}
                                py={".4rem"}
                            >
                                <GameText
                                    $alignSelf={"center"}
                                    $fWeight={"bolder"}
                                    size={cardW / 15}
                                    $style={{
                                        writingMode: "vertical-rl",
                                        textOrientation: "upright"
                                    }}
                                >
                                    {t(`${RoleGuideEnum.name}.${props.role}`).toUpperCase()}
                                </GameText>
                                <GameText $fWeight={"bolder"} size={cardW / 15}>
                                    {("+" + ROLE_POINT[props.role] + "").slice(-2)}
                                </GameText>
                            </GameLayout.ColExpanded>
                        </GameLayout.RowExpanded>
                        <GameLayout.RowExpanded $justifyContent={"center"} $alignItems={"center"} px={cardW / 22}>
                            <GameText size={cardW / 13} $alignSelf="center">
                                {t(`${RoleGuideEnum.guide}.${props.role}`)}
                            </GameText>
                        </GameLayout.RowExpanded>
                    </GameLayout.ColExpanded>
                </CardFront>
                <CardBack>
                    <GameLayout.RowExpanded
                        round={".8rem"}
                        $backgroundColor={PartyColor(props.role)}
                        $W={cardW * 1.2}
                        p={cardW / 10}
                        ratio={3 / 4}
                    >
                        <GameText size={cardW / 10}>{t(`${RoleGuideEnum.description}.${props.role}`)}</GameText>
                    </GameLayout.RowExpanded>
                </CardBack>
            </FlipCard>
            {/*</Card>*/}
        </GameDiv>
    );
}

export default RoleCard;
