import React, {useContext} from "react";
import {GameDiv, IBasicProps} from "./GameDiv";
import Grid from "../styled/game-layout/Grid";
import {GameText} from "./GameText";
import {GameIcon} from "./GameIcon";
import checkIc from "../../../assets/game-btn/check.svg";
import uncheckIc from "../../../assets/game-btn/uncheck.svg";
import {SizeContext} from "../../../context/SizeContext";

interface ICheckBoxProps extends IBasicProps {
    value: boolean;
    onChange?: (value: boolean) => void;
    children?: string;
    icCheck?: string;
    icUnCheck?: string;
}

function GameCheckBox(props: ICheckBoxProps) {
    const {basic: baseSize} = useContext(SizeContext);
    let size = props.size || baseSize.size3;
    let customProps = {...props};
    const icCheck = props.icCheck || checkIc;
    const icUnCheck = props.icUnCheck || uncheckIc;
    customProps.$pointerEvents = "auto";
    delete customProps["size"];
    delete customProps["value"];
    delete customProps["children"];
    delete customProps["onChange"];
    customProps.cursor = "pointer";
    return (
        <GameDiv
            {...customProps}
            onClick={() => {
                if (props.onChange) props.onChange(!props.value);
            }}
        >
            <Grid.Row $alignItems="center" $justifyContent="center">
                <GameIcon mr={size / 5} src={props.value ? icCheck : icUnCheck} size={size} />
                <GameText opacity={props.value ? 1 : 0.5} size={size * 0.8}>
                    {props.children}
                </GameText>
            </Grid.Row>
        </GameDiv>
    );
}

export default GameCheckBox;
