import React, {useContext, useEffect, useState} from "react";
import PropTypes from "prop-types";
import {LIST_POSITIONS, POSITION} from "../../../shared/styled/game-layout/StackLayout";
import {ROLE_AVATAR} from "../../startgame-screen/GameCardShower";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";
import AvatarView from "../../../shared/game-basic-components/AvatarView";
import {PartyColor} from "../../game-info/game-setting/RoleItem";
import {RoomContext} from "../../../../context/RoomContext";

RoleAvatar.propTypes = {
    pos: PropTypes.oneOf(LIST_POSITIONS).isRequired,
    size: PropTypes.number.isRequired,
    targetId: PropTypes.string.isRequired
};

interface IAvatarPosition {
    top?: number;
    bottom?: number;
    left?: number;
    right?: number;
}

function RoleAvatar(props) {
    let [avatarPosition, setAvatarPosition] = useState<IAvatarPosition>(AvatarPosition[POSITION.TOP]);
    const room = useContext(RoomContext).room;

    let role = room.state.players[props.targetId].role.roleID;
    useEffect(() => {
        setAvatarPosition(AvatarPosition[props.pos]);
    }, [props.pos]);
    return (
        <GameDiv
            style={{
                position: "absolute",
                userSelect: "none",
                pointerEvents: "none",
                top: avatarPosition.top ? `${props.size * avatarPosition.top}px` : "",
                left: avatarPosition.left ? `${props.size * avatarPosition.left}px` : "",
                right: avatarPosition.right ? `${props.size * avatarPosition.right}px` : "",
                bottom: avatarPosition.bottom ? `${props.size * avatarPosition.bottom}px` : ""
            }}
        >
            {role != null && <AvatarView src={ROLE_AVATAR[role]} border_color={PartyColor(role)} size={props.size} />}
        </GameDiv>
    );
}

const basicDistance = 1;

const AvatarPosition: {[pos in POSITION]: IAvatarPosition} = {
    [POSITION.TOP]: {left: basicDistance * 0.04, top: basicDistance * 0.1},
    [POSITION.BOTTOM]: {bottom: basicDistance * 0, left: basicDistance * 1.2},
    [POSITION.LEFT]: {top: basicDistance * 0.6, left: basicDistance * 0.04},
    [POSITION.RIGHT]: {top: basicDistance * 0.6, right: basicDistance * 0.04},
    [POSITION.CENTER]: {top: basicDistance},
    [POSITION.TOP_LEFT]: {top: basicDistance * 0.05},
    [POSITION.TOP_RIGHT]: {top: basicDistance * 0.05},
    [POSITION.BOTTOM_LEFT]: {top: basicDistance * 0.6, left: basicDistance * 0.04},
    [POSITION.BOTTOM_RIGHT]: {top: basicDistance * 0.6, right: basicDistance * 0.04}
};

export default RoleAvatar;
