import React, {useCallback, useContext, useEffect, useState} from "react";
import {RoomContext, SizeContext, UserContext} from "../../../../../context";

interface IRoundedTargetLayout {
    topTargets: string[];
    leftTargets: string[];
    rightTargets: string[];
    bottomTargets: string[];
}

const defaultValue = {
    topTargets: [],
    leftTargets: [],
    rightTargets: [],
    bottomTargets: []
};

function useTargetLayout() {
    let [layout, setLayout] = useState<IRoundedTargetLayout>(defaultValue);
    const userContext = useContext(UserContext);
    let roomContext = useContext(RoomContext);
    const {basic: BaseSize} = useContext(SizeContext);
    let players = roomContext.state.players;

    const calculateTopCount = useCallback(
        function (totalCount: number): number {
            if (totalCount === 4) return 1; // trường hợp đặc biệt :))
            if (totalCount < 3) return totalCount - 1;
            let topCount = Math.floor(BaseSize.gameW / BaseSize.playerAvatarSize);
            if (BaseSize.gameW < BaseSize.gameH) {
                if (topCount > totalCount / 4) topCount = Math.floor(totalCount / 4);
            } else if (topCount > totalCount / 2) topCount = Math.floor(totalCount / 2);
            if ((totalCount - topCount - 1) % 2 === 1) {
                topCount += 1;
            }
            return topCount;
        },
        [BaseSize.gameH, BaseSize.gameW, BaseSize.playerAvatarSize]
    );

    useEffect(() => {
        let playerKeys = [...Object.keys(players)];
        if (!playerKeys.length) return;
        playerKeys.sort((a, b) => {
            const seatA = players[a]?.seat | 0;
            const seatB = players[b]?.seat | 0;
            return seatA - seatB;
        });
        //số lượng target trên đỉnh
        const topCount = calculateTopCount(playerKeys.length);
        // players.length-1: người chơi hiện tại luôn ở BOTTOM nên không tính
        //số lượng target 2 bên
        const sideCount = Math.floor((playerKeys.length - 1 - topCount) / 2);
        const mySeat = players[userContext.playerId]?.seat;
        const index = playerKeys.findIndex((id) => players[id].seat === mySeat);
        const myIndex = index >= 0 ? index + 1 : 1;
        const targets = playerKeys.concat(playerKeys); //x2 để đỡ phải xử lý vòng tròn khi tràn mảng
        setLayout({
            leftTargets: targets.splice(myIndex, sideCount).reverse(),
            topTargets: targets.splice(myIndex, topCount),
            rightTargets: targets.splice(myIndex, sideCount),
            bottomTargets: [targets[myIndex - 1]]
        });
    }, [players, BaseSize.gameW, calculateTopCount, userContext.playerId]);

    return layout;
}

export default useTargetLayout;
