import {useEffect, useRef, useState} from "react";
import {Room, RoomAvailable} from "colyseus.js";
import {FacebookPlayer} from "../../context/UserContext";
import {State} from "../../werewolf-consts/colyseus/State";
import {Metadata} from "../../werewolf-consts/colyseus/GameRoomTypes";
import {CLIENT} from "../../context";

export function useLobby(user: FacebookPlayer): RoomAvailable<Metadata>[] {
    const [roomAvailable, setRoomAvailable] = useState<RoomAvailable<Metadata>[]>([]);
    let lobby = useRef<Room<any> | undefined>(undefined);
    useEffect(() => {
        CLIENT.joinOrCreate<State>("lobby", user)
            .then((room) => {
                lobby.current = room;
                lobby.current.onMessage("rooms", (listRooms) => {
                    setRoomAvailable(listRooms);
                });
                lobby.current.onMessage("+", ([roomId, room]) => {
                    setRoomAvailable((oldRoomAvailable) => {
                        const roomIndex = oldRoomAvailable.findIndex((room) => room.roomId === roomId);
                        if (roomIndex === -1) {
                            return [...oldRoomAvailable, room];
                        } else {
                            const newRoomAvailable = [...oldRoomAvailable];
                            newRoomAvailable[roomIndex] = room;
                            return newRoomAvailable;
                        }
                    });
                    // if(DEV) console.log(roomAvailable);
                });
                lobby.current.onMessage("-", (roomId) => {
                    setRoomAvailable((oldRoomAvailable) => oldRoomAvailable.filter((room) => room.roomId !== roomId));
                });
            })
            .catch((e) => {});
        return () => {
            if (lobby.current !== undefined) {
                lobby.current.removeAllListeners();
                lobby.current.leave();
            }
        };
    }, [user]);
    return roomAvailable;
}
