import {styled} from "styletron-react";
import {getFlex} from "../BasicProps";
import {getBasicCSS, IBasicProps} from "../../game-basic-components/GameDiv";

interface IProps extends IBasicProps {
    noExpand?: boolean;
}

const ColReverse = styled("div", (props) => {
    return {
        display: "flex",
        alignContent: "center",
        flexDirection: "column-reverse",
        boxSizing: "border-box"
    };
});

const FullHeightRow = styled<any, IProps>("div", (props) => {
    return {
        display: "flex",
        flexDirection: "column",
        flexWrap: "wrap",
        ...getBasicCSS(props),
        boxSizing: "border-box",
        flex: 1
    };
});
const FullWidthCol = styled<any, IProps>("div", (props) => {
    return {
        display: "flex",
        flexDirection: "column",
        ...getBasicCSS(props),
        boxSizing: "border-box",
        flex: 1
    };
});
export const ColAuto = styled<any, IProps>("div", (props) => {
    return {
        flex: "0 0 auto",
        display: "flex",
        flexDirection: "column",
        height: "auto",
        maxHeight: "100%",
        ...getBasicCSS(props),
        ...getFlex(props),
        boxSizing: "border-box"
    };
});
export const RowAuto = styled<any, IProps>("div", (props) => {
    return {
        flex: "0 0 auto",
        display: "flex",
        flexDirection: "row",
        width: "auto",
        maxWidth: "100%",
        ...getBasicCSS(props),
        ...getFlex(props),
        boxSizing: "border-box"
    };
});

const Col = styled<any, IProps>("div", (props) => {
    return {
        display: "flex",
        flexDirection: "column",
        ...getBasicCSS(props),
        ...getFlex(props),
        boxSizing: "border-box",
        flex: props.noExpand ? "content" : 1
    };
});
const Row = styled<any, IProps>("div", (props) => {
    return {
        display: "flex",
        flexDirection: "row",
        ...getBasicCSS(props),
        ...getFlex(props),
        boxSizing: "border-box",
        flex: props.noExpand ? "content" : 1
    };
});

const Grid = {
    Row,
    FullWidthCol,
    Col,
    FullHeightRow,
    ColReverse,
    ColAuto,
    RowAuto
};

export default Grid;
