import React, {ReactElement, useCallback, useContext, useEffect, useMemo, useRef, useState} from "react";
import GameLayout from "./GameLayout";
import {is2ArrayEqual} from "../../../werewolf-consts/utils/comparator/ArrayCompare";

interface RouterProps {
    paths: string[];
    children: ReactElement[];
    initShow?: string;
}

function GameRouter(props: RouterProps) {
    const fatherRoute = useContext(GameRouterContext);
    const fatherPath = fatherRoute?.fullPath || "";
    const propsDotPath = useRef<string[]>([]);
    const [path, setPath] = useState<string>();
    const [childPath, setChildPath] = useState<string>();
    const [params, setParams] = useState<(string | undefined)[]>();
    const [childParams, setChildParams] = useState<(string | undefined)[]>();

    if (!is2ArrayEqual(propsDotPath.current, props.paths)) {
        propsDotPath.current = props.paths;
    }

    const changeRoute = useCallback(
        function (path: string, params: (string | undefined)[]) {
            let subPaths = path.split("/");
            if (propsDotPath.current.includes(subPaths[0])) {
                setPath(subPaths[0]);
                if (subPaths.length > 1) {
                    setChildPath(subPaths.slice(1).toString().replace(",", "/"));
                    setChildParams(params);
                } else {
                    setParams(params);
                }
            } else {
                if (fatherRoute?.routeTo) fatherRoute.routeTo(path, params);
                return;
            }
        },
        [fatherRoute]
    );

    useEffect(() => {
        console.log("GameRouter: useEffect");
        if (fatherRoute?.currentPath != null) {
            let subPaths = fatherRoute.currentPath.split("/");
            if (propsDotPath.current.includes(subPaths[0])) {
                setPath(subPaths[0]);
                if (subPaths.length > 1) {
                    setChildPath(subPaths.slice(1).toString().replace(",", "/"));
                    setChildParams(fatherRoute.childParams);
                } else {
                    setParams(fatherRoute.childParams || []);
                }
            }
        } else {
            setPath(props.initShow || propsDotPath.current[0]);
        }
    }, [fatherRoute, props.initShow]);

    const gameRouterContextValue = useMemo(
        () => ({
            fullPath: `${fatherPath}/${path}`,
            currentPath: childPath,
            params: params,
            childParams: childParams,
            routeTo: changeRoute
        }),
        [changeRoute, childParams, childPath, fatherPath, params, path]
    );

    return (
        <>
            {props.children.map((el, index) => {
                return (
                    <GameLayout.Condition
                        con={propsDotPath.current[index] === path}
                        key={fatherPath + propsDotPath.current[index]}
                    >
                        <GameRouterContext.Provider value={gameRouterContextValue}>{el}</GameRouterContext.Provider>
                    </GameLayout.Condition>
                );
            })}
        </>
    );
}

interface IRouterContext {
    routeTo?: (path: string, params: (string | undefined)[]) => void;
    fullPath?: string;
    currentPath?: string;
    params?: (string | undefined)[];
    childParams?: (string | undefined)[];
}

export const GameRouterContext = React.createContext<IRouterContext | null>(null);

export default GameRouter;
