import React, {useContext, useEffect} from "react";
import "./LobbyRoomScreen.css";
import "../app/DefaultCSS.css";
import {RoomAvailable} from "colyseus.js";
import RoomItem from "./RoomItem";
import {UserContext} from "../../context/UserContext";
import {useLobby} from "./useLobby";
import {FirestoreContext} from "../../context/FirestoreContext";
import {useFollowings} from "../../database/hooks/useFollowings";
import GameLayout from "../shared/game-advance-components/GameLayout";
import {GameText} from "../shared/game-basic-components/GameText";
import {SizeContext} from "../../context/SizeContext";
import GameIconButton from "../shared/game-basic-components/GameIconButton";
import backIcon from "../../assets/game-btn/back.svg";
import {GameRouterContext} from "../shared/game-advance-components/GameRouter";
import {useTranslation} from "react-i18next";
import {I18Namspace} from "../../language/i18n";
import {GameInfoEnum} from "../../language/Vietnamese/GameInfo_vi";
import {POSITION} from "../shared/styled/game-layout/StackLayout";
import {COLOR} from "../shared/styled/ColorsPalette";
import {GameDiv} from "../shared/game-basic-components/GameDiv";
import {Metadata} from "../../werewolf-consts/colyseus/GameRoomTypes";
import {DEV} from "../../env.const";

export function LobbyRoomScreen() {
    let user = useContext(UserContext);
    const db = useContext(FirestoreContext);
    const [users] = useFollowings(db, user.playerId);
    const {basic, window} = useContext(SizeContext);
    const router = useContext(GameRouterContext);
    useEffect(() => {
        // READ: following users of "elet-1023912734"
        if (DEV) console.log(users);
    }, [users]);

    // useEffect(() => {
    //     async function follows(fromUid: string, targetUid: string) {
    //         const myUser = db.users.document(fromUid);
    //         const userToFollow = db.users.document(targetUid);
    //         await myUser.linkedArray("following")!.add(userToFollow);
    //         await userToFollow.linkedArray("followers")!.add(myUser);
    //     }
    //     follows("elet-1023912734", "elet-1149916101")
    //         .then(() => {
    //             if (DEV) console.log("follow success");
    //         })
    //         .catch((e) => {
    //             console.error(e);
    //         });
    // }, [db.users]);

    const {t} = useTranslation(I18Namspace.GameInfo);
    const roomAvailable = useLobby(user);
    const favoriteRoom = users.map((u) => {
        const onlineRoom = roomAvailable.find((r) => r.roomId === u._id);
        if (onlineRoom) {
            return onlineRoom;
        } else {
            return ({
                metadata: {
                    villageId: u._id,
                    lock: u.villagePass
                }
            } as unknown) as RoomAvailable<Metadata>;
        }
    });
    return (
        <GameLayout.ColExpanded $alignItems={"flex-start"}>
            <GameIconButton
                m={".5rem"}
                src={backIcon}
                size={basic.size5}
                $textPosition={POSITION.RIGHT}
                color={COLOR.bloodColor}
                onClick={() => {
                    if (router?.routeTo) router.routeTo("home", []);
                }}
            >
                {t(`${GameInfoEnum.back}`)}
            </GameIconButton>
            <GameLayout.ColExpanded m={"1rem"} $alignItems={"flex-start"}>
                <>
                    <GameText size={basic.size8} $fontStyle={"Altus"}>
                        {t(`${GameInfoEnum.favoriteRoom}`)}
                    </GameText>
                    <GameLayout.Row $justifyContent={"space-around"} $flexWrap={"wrap"}>
                        {favoriteRoom.map((r, index) => (
                            <RoomItem key={"favorite" + index} metadata={r.metadata} roomId={r.roomId} />
                        ))}
                    </GameLayout.Row>
                </>
                <>
                    <GameText size={basic.size8} $fontStyle={"Altus"}>
                        {t(`${GameInfoEnum.onlineRoom}`)}
                    </GameText>
                    <GameDiv $scrollY h={window.height * 0.8}>
                        <GameLayout.Row $justifyContent={"space-around"} $flexWrap={"wrap"}>
                            {roomAvailable.map((r) => (
                                <RoomItem key={r.roomId} metadata={r.metadata} roomId={r.roomId} />
                            ))}
                        </GameLayout.Row>
                    </GameDiv>
                </>
            </GameLayout.ColExpanded>
        </GameLayout.ColExpanded>
    );
}
