import React, {useContext, useEffect, useState} from "react";
import PropTypes from "prop-types";
import {LIST_POSITIONS, POSITION, REVERSE_POSITION} from "../../../shared/styled/game-layout/StackLayout";
import {UpToDown} from "../../../shared/styled/Animations";
import handPoint from "../../../../assets/role-objects/handpoint.png";
import TargetItem from "./TargetItem";
import {RoomContext} from "../../../../context/RoomContext";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";
import GameHoverPopup from "../../../shared/game-advance-components/GameHoverPopup";
import AttackersView from "./AttackersView";
import {GameIcon} from "../../../shared/game-basic-components/GameIcon";
import GameLayout from "../../../shared/game-advance-components/GameLayout";
import {GameText} from "../../../shared/game-basic-components/GameText";

HandPointWithAttackers.propTypes = {
    pos: PropTypes.oneOf(LIST_POSITIONS).isRequired,
    size: PropTypes.number.isRequired,
    showAttacker: PropTypes.bool.isRequired,
    targetId: PropTypes.string.isRequired,
    attackers: PropTypes.array
};

interface IHandPosition {
    top?: number;
    bottom?: number;
    left?: number;
    right?: number;
    rotateDeg: number;
}

interface IAttackerPosition {
    top?: number;
    bottom?: number;
    left?: number;
    right?: number;
}

function HandPointWithAttackers(props) {
    let [handPointPosition, setHandPointPosition] = useState<IHandPosition>(HandPosition[POSITION.TOP]);
    let [attackerPosition, setAttackerPosition] = useState<IAttackerPosition>(HandPosition[POSITION.TOP]);
    let [attackers, setAttackers] = useState<string[]>([]);
    let [zoom, setZoom] = useState<boolean>(false);
    let roomPlayer = useContext(RoomContext).room.state.players;
    useEffect(() => {
        setHandPointPosition(HandPosition[props.pos]);
        setAttackerPosition(AttackerPosition[props.pos]);
    }, [props.pos]);
    useEffect(() => {
        setAttackers((attackers) => {
            if (attackers.length > 0) attackers.splice(0, attackers.length - 1);
            if (props.attackers)
                for (const attacker of props.attackers) {
                    attackers.push(attacker);
                }
            return attackers;
        });
        if (props.attackers) setAttackers([...props.attackers]);
    }, [props.attackers]);
    let listAttacker = attackers
        ? attackers.map((playerId) => (
              <TargetItem
                  size={Math.max(props.size / attackers.length, props.size / 2.3)}
                  key={playerId + Math.random() * 10000000}
                  targetId={playerId}
              />
          ))
        : null;
    return (
        <GameDiv
            position={"absolute"}
            $style={{
                top: handPointPosition.top ? `${props.size * handPointPosition.top}px` : "",
                left: handPointPosition.left ? `${props.size * handPointPosition.left}px` : "",
                right: handPointPosition.right ? `${props.size * handPointPosition.right}px` : "",
                bottom: handPointPosition.bottom ? `${props.size * handPointPosition.bottom}px` : ""
            }}
        >
            <GameLayout.Stack>
                <GameDiv
                    $style={{
                        transform: `rotate(${handPointPosition.rotateDeg}deg)`
                    }}
                >
                    <UpToDown>
                        <GameIcon src={handPoint} size={props.size} />
                    </UpToDown>
                </GameDiv>
                <GameDiv
                    position={"absolute"}
                    display={"flex"}
                    $alignItems={"flex-end"}
                    $style={{
                        top: attackerPosition.top ? `${props.size * attackerPosition.top}px` : "",
                        left: attackerPosition.left ? `${props.size * attackerPosition.left}px` : "",
                        right: attackerPosition.right ? `${props.size * attackerPosition.right}px` : "",
                        bottom: attackerPosition.bottom ? `${props.size * attackerPosition.bottom}px` : ""
                    }}
                >
                    <>
                        {props.showAttacker && (
                            <GameHoverPopup
                                pos={REVERSE_POSITION[props.pos]}
                                popup={<AttackersView attackers={attackers || []} />}
                            >
                                <GameLayout.Row
                                    size={props.size * 1.3}
                                    $flexWrap={"wrap"}
                                    border={attackers.length >= 3 ? "solid white 1px" : ""}
                                    round={".5rem"}
                                    $justifyContent={"space-around"}
                                    $alignContent={"space-around"}
                                    scroll={false}
                                >
                                    <>{listAttacker}</>
                                </GameLayout.Row>
                            </GameHoverPopup>
                        )}
                        {props.showAttacker && attackers.length >= 3 && (
                            <GameText m={".3rem"} size={props.size / 2}>
                                +{attackers.length}
                            </GameText>
                        )}
                    </>
                </GameDiv>
            </GameLayout.Stack>
        </GameDiv>
    );
}

const HandPosition: {[pos in POSITION]: IHandPosition} = {
    [POSITION.TOP]: {top: 1, left: 0.5, rotateDeg: 0},
    [POSITION.BOTTOM]: {bottom: 1, left: 0.1, rotateDeg: 180},
    [POSITION.LEFT]: {top: 1.3, left: 1, rotateDeg: -70},
    [POSITION.RIGHT]: {top: 1.5, right: 1, rotateDeg: 70},
    [POSITION.CENTER]: {top: 1, rotateDeg: 0},
    [POSITION.TOP_LEFT]: {top: 0.2, left: 0.7, rotateDeg: 315},
    [POSITION.TOP_RIGHT]: {top: 0.3, right: 0.6, rotateDeg: 45},
    [POSITION.BOTTOM_LEFT]: {top: 2, left: 0.7, rotateDeg: -70},
    [POSITION.BOTTOM_RIGHT]: {top: 2.5, right: 0.6, rotateDeg: 70}
};
const AttackerPosition: {[p in POSITION]: IAttackerPosition} = {
    [POSITION.TOP]: {top: 0.5, right: -1.5},
    [POSITION.BOTTOM]: {bottom: 0.3, left: 0.2},
    [POSITION.LEFT]: {top: 0.3, left: 0.2},
    [POSITION.RIGHT]: {top: 0.2, left: -1.6},
    [POSITION.CENTER]: {top: 1},
    [POSITION.TOP_LEFT]: {top: 0.2, left: 0.2},
    [POSITION.TOP_RIGHT]: {top: 0.3, left: -1.6},
    [POSITION.BOTTOM_LEFT]: {top: 2, left: 0.7},
    [POSITION.BOTTOM_RIGHT]: {top: 2.5, right: 0.6}
};

export default HandPointWithAttackers;
