import React, {useEffect, useState} from "react";
import useGameEvent, {IGameEventData} from "./hooks/useGameEvent";
import GameEventDialog from "./GameEventDialog";
import {v4 as uuidv4} from "uuid";
import GameLayout from "../../shared/game-advance-components/GameLayout";
import {GameDiv} from "../../shared/game-basic-components/GameDiv";

GameDialogManager.propTypes = {};

export interface IGameDialogData {
    event: IGameEventData;
    time: number;
    uuid: string;
}

function GameDialogManager() {
    const [dialogs, setDialogs] = useState<IGameDialogData[]>([
        // {
        //     time: 10,
        //     event: {content: "sdfsdfsd", eventName: mEventNames.BE_CURSED, targets: ["wildchild"]},
        //     uuid: "sfsdfdf"
        // },
        // {
        //     time: 20,
        //     event: {content: "sdfsdfsd", eventName: mEventNames.BECOME_WOLF, targets: ["seer"]},
        //     uuid: "sfsdfdfádf"
        // }
    ]);
    let gameEvent = useGameEvent();
    useEffect(() => {
        if (gameEvent.content !== "") {
            setDialogs((prevState) => {
                let list = [...prevState].slice(0, 2);
                list.push({event: gameEvent, time: 15, uuid: uuidv4()});
                return list;
            });
        }
    }, [gameEvent]);

    useEffect(() => {
        if (dialogs.length > 0) {
            let i = setTimeout(() => {
                setDialogs((dialogs) => {
                    for (const alert of dialogs) {
                        alert.time -= 1;
                    }
                    dialogs = dialogs.filter((a) => {
                        return a.time > 0;
                    });
                    return dialogs;
                });
            }, 1000);
            return function clean() {
                clearTimeout(i);
            };
        }
    }, [dialogs]);

    function onClose(id: string) {
        let list = dialogs.filter((a) => {
            return a.uuid !== id;
        });
        setDialogs(list);
    }

    let listDialog = dialogs.map((dialog, index) => (
        <GameDiv key={"dialog" + index}>
            <GameEventDialog
                key={"dialog" + dialog.uuid}
                index={index}
                dialogData={dialog}
                onClose={() => onClose(dialog.uuid)}
            />
        </GameDiv>
    ));
    return <GameLayout.Stack $zIndex={20}>{listDialog}</GameLayout.Stack>;
}

export default GameDialogManager;
