import React, {useContext, useState} from "react";
import GameLayout from "../../../shared/game-advance-components/GameLayout";
import {SizeContext} from "../../../../context/SizeContext";
import {GameText} from "../../../shared/game-basic-components/GameText";
import {RoomContext} from "../../../../context/RoomContext";
import {useTranslation} from "react-i18next";
import {I18Namspace} from "../../../../language/i18n";
import {GameInfoEnum} from "../../../../language/Vietnamese/GameInfo_vi";
import {getPlayersName} from "../../../../werewolf-consts/colyseus/stateUtils";
import TargetItem from "../../game-body/target-picker/TargetItem";
import RoomSetupItem from "./RoomSetupItem";
import {RoomSetting} from "../../../../werewolf-consts/colyseus/state/RoomSetting";
import {AdminActionTypes, IAdminAction, MessageTypes} from "../../../../werewolf-consts/colyseus/MessageTypes";
import GameIconButton from "../../../shared/game-basic-components/GameIconButton";
import saveIcon from "../../../../assets/game-btn/save.svg";
import {arraySchema2Array} from "../../../../werewolf-consts/colyseus/Utils";
import {DEV} from "../../../../env.const";

RoomInfo.propTypes = {};

function RoomInfo(props) {
    const {basic} = useContext(SizeContext);
    const room = useContext(RoomContext).room;
    const roomSetting = room.state.roomSetting;
    const [tempSetting, setTempSetting] = useState<RoomSetting>(roomSetting);
    const {t} = useTranslation(I18Namspace.GameInfo);

    function updateRoomSetting() {
        const setting = new RoomSetting();
        setting.autoChangeSetup = tempSetting.autoChangeSetup;
        setting.password = tempSetting.password;
        setting.maxPlayer = tempSetting.maxPlayer;
        setting.bannedUsers = tempSetting.bannedUsers;
        if (DEV) console.log(tempSetting);
        room.send(MessageTypes.ADMIN_ACTION, {
            type: AdminActionTypes.UPDATE_ROOM_SETTING,
            data: tempSetting
        } as IAdminAction);
    }

    function isSame(tempSetting: RoomSetting, roomSetting: RoomSetting) {
        const {roomAdmin: r1, autoChangeSetup: a1, bannedUsers: b1, password: p1, maxPlayer: m1} = tempSetting;
        const {roomAdmin: r2, autoChangeSetup: a2, bannedUsers: b2, password: p2, maxPlayer: m2} = roomSetting;
        return (
            r1 === r2 &&
            a1 === a2 &&
            p1 === p2 &&
            m1 === m2 &&
            JSON.stringify(arraySchema2Array<string>(b1)) === JSON.stringify(arraySchema2Array<string>(b2))
        );
    }

    return (
        <GameLayout.ColExpanded m={"1rem"}>
            <GameText $alignSelf={"flex-start"} mb={"1rem"} $fontStyle={"Altus"} size={basic.size6}>
                {t(`${GameInfoEnum.roomAdmin}`)}:
            </GameText>
            <GameLayout.ColExpanded $alignItems={"center"} $alignSelf={"center"}>
                <TargetItem targetId={roomSetting.roomAdmin} size={basic.playerAvatarSize * 2} />
                <GameText $fontStyle={"Altus"} size={basic.size10} mx={"1rem"}>
                    {getPlayersName(room.state, roomSetting.roomAdmin)}
                </GameText>
            </GameLayout.ColExpanded>
            <RoomSetupItem
                inputType={"text"}
                placeHolder={t(`${GameInfoEnum.off}`)}
                value={roomSetting.password}
                title={t(`${GameInfoEnum.passwordToJoin}`)}
                onChange={(value) => {
                    setTempSetting((s) => {
                        let temp = {...s};
                        if (typeof value === "string") temp.password = value;
                        else temp.password = "";
                        return temp as RoomSetting;
                    });
                }}
            />
            <RoomSetupItem
                inputType={"number"}
                value={roomSetting.maxPlayer}
                title={t(`${GameInfoEnum.maxPlayer}`)}
                onChange={(value) => {
                    setTempSetting((s) => {
                        let temp = {...s};
                        if (typeof value === "number") temp.maxPlayer = value;
                        return temp as RoomSetting;
                    });
                }}
            />
            <RoomSetupItem
                inputType={"boolean"}
                value={roomSetting.autoChangeSetup}
                title={t(`${GameInfoEnum.autoChangeSetup}`)}
                onChange={(value) => {
                    setTempSetting((s) => {
                        let temp = {...s};
                        if (typeof value === "boolean") temp.autoChangeSetup = value;
                        return temp as RoomSetting;
                    });
                }}
            />
            <GameLayout.RowExpanded />
            <GameIconButton
                size={basic.size10}
                src={saveIcon}
                onClick={() => updateRoomSetting()}
                disabled={isSame(tempSetting, roomSetting)}
            >
                {t(`${GameInfoEnum.saveRoomSetting}`)}
            </GameIconButton>
        </GameLayout.ColExpanded>
    );
}

export default RoomInfo;
