import React, {useContext} from "react";
import useStageGuide from "./hooks/useStageGuide";
import Grid from "../../../shared/styled/game-layout/Grid";
import TargetItem from "../target-picker/TargetItem";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";
import {GameText} from "../../../shared/game-basic-components/GameText";
import {Roles} from "../../../../werewolf-consts/enums";
import {RoomContext} from "../../../../context/RoomContext";
import {SizeContext} from "../../../../context/SizeContext";
import {COLOR} from "../../../shared/styled/ColorsPalette";
import {UserContext} from "../../../../context/UserContext";

function StageGuide() {
    let guide = useStageGuide();
    const {window: S, basic: BaseSize} = useContext(SizeContext);
    let targetSize = Math.max(40, S.min / 10);
    let roomContext = useContext(RoomContext);
    const userContext = useContext(UserContext);

    const myRole = roomContext.state.players[userContext.playerId]?.role?.roleID | Roles.VILLAGER;
    return (
        <GameDiv
            round={"1rem"}
            p={".5rem"}
            w={BaseSize.mobileView ? BaseSize.gameW - BaseSize.playerAvatarSize * 3 : BaseSize.gameW / 2}
            $backgroundColor={guide?.targets?.length !== 0 || guide?.body || guide?.header ? COLOR.blue : "transparent"}
        >
            {guide?.header && <GameText size={BaseSize.size3}>{guide.header}</GameText>}
            {guide?.targets && (
                <Grid.Row mt={".5rem"} style={{justifyContent: "center"}}>
                    {guide.targets.map((id) => {
                        return (
                            <GameDiv key={`StageGuideTarget_${id}`}>
                                <TargetItem targetId={id} size={targetSize} />
                            </GameDiv>
                        );
                    })}
                </Grid.Row>
            )}
            {guide?.body && (
                <GameDiv mt={".5rem"}>
                    <GameText size={BaseSize.size5} $fontStyle={"Altus"}>
                        {guide.body}
                    </GameText>
                </GameDiv>
            )}
        </GameDiv>
    );
}

export default StageGuide;
