import {Collections} from "./CollectionNames";
import {User} from "./User";
import {Village} from "./Village";
import {Collection, Database, firebase, FirestoreCollection} from "@phamngocduy98/flashstore-react";

export class MyDatabase extends Database {
    @Collection(User, Collections.USERS)
    public users: FirestoreCollection<User>;

    @Collection(Village, Collections.VILLAGES)
    public villages: FirestoreCollection<Village>;
    constructor(db: firebase.firestore.Firestore) {
        super(db);
        this.users = this.collection(Collections.USERS);
        this.villages = this.collection(Collections.VILLAGES);
    }
}
