import i18n from "i18next";
import {initReactI18next} from "react-i18next";
import {StageGuide_vi} from "./Vietnamese/StageGuide_vi";
import {RoleGuide_vi} from "./Vietnamese/RoleGuide_vi";
import {StageGuide_en} from "./English/StageGuide_en";
import {RoleGuide_en} from "./English/RoleGuide_en";
import {ActionResult_vi} from "./Vietnamese/ActionResult_vi";
import {SkillDisabledReason_vi} from "./Vietnamese/SkillDisabledReason_vi";
import {GameHistory_vi} from "./Vietnamese/GameHistory_vi";
import {GameResult_vi} from "./Vietnamese/GameResult_vi";
import {GameInfo_vi} from "./Vietnamese/GameInfo_vi";
import {GameSetting_vi} from "./Vietnamese/GameSetting_vi";
import {GameNotice_vi} from "./Vietnamese/GameNotice_vi";
import {HomeScreen_vi} from "./Vietnamese/HomeScreen_vi";
import {PlayerInfo_vi} from "./Vietnamese/PlayerInfo_vi";
import {StageDescription_vi} from "./Vietnamese/StageDescription_vi";
// the translations
// (tip move them in a JSON file and import them)
export enum I18Namspace {
    StageGuide = "StageGuide",
    StageDescription = "StageDescription",
    RoleGuide = "RoleGuide",
    ActionResult = "ActionResult",
    SkillDisabledReason = "SkillDisabledReason",
    GameHistory = "GameHistory",
    GameResult = "GameResult",
    GameInfo = "GameInfo",
    GameSetting = "GameSetting",
    GameNotice = "GameNotice",
    HomeScreen = "HomeScreen",
    PlayerInfo = "PlayerInfo"
}

const resources = {
    en: {
        [I18Namspace.StageGuide]: StageGuide_en,
        [I18Namspace.RoleGuide]: RoleGuide_en
    },
    vi: {
        [I18Namspace.StageGuide]: StageGuide_vi,
        [I18Namspace.RoleGuide]: RoleGuide_vi,
        [I18Namspace.ActionResult]: ActionResult_vi,
        [I18Namspace.SkillDisabledReason]: SkillDisabledReason_vi,
        [I18Namspace.GameHistory]: GameHistory_vi,
        [I18Namspace.GameResult]: GameResult_vi,
        [I18Namspace.GameInfo]: GameInfo_vi,
        [I18Namspace.GameSetting]: GameSetting_vi,
        [I18Namspace.GameNotice]: GameNotice_vi,
        [I18Namspace.HomeScreen]: HomeScreen_vi,
        [I18Namspace.PlayerInfo]: PlayerInfo_vi,
        [I18Namspace.StageDescription]: StageDescription_vi
    }
};

i18n.use(initReactI18next) // passes i18n down to react-i18next
    .init({
        resources,
        lng: "vi",
        fallbackLng: "vi",
        saveMissing: true,
        keySeparator: false, // we do not use keys in form messages.welcome
        interpolation: {
            escapeValue: false // react already safes from xss
        }
    });

export default i18n;
